<template>
    <div class="container">
        <details-head
                :title="stitle"
                :createperson="create_person"
                :copyperson="copyperson"
                :createtime="create_time"
                :applyid="apply_id"
                :status="status"
                :steps="steps"
                :next_des="next_des"
        ></details-head>
        <div class="line mt-lg mb-sm"></div>
        <h3>需审批内容 <el-button @click="approve_attachment_download" type="text" size="small">下载审批单</el-button></h3>
        <reimburse-sheet v-if="type=='1'"
                             :tableData="tableData"
                             :curdate="date"
                             :applyID="apply_id"
                             :org_full_name="org"
                             :name="create_person"
                             :bankname="bank_name"
                             :bank_card_id="bank_card_id"
                             :constperson="approval_person"
                             :copyperson="copyperson"
                             :counter="counter"
                             :caiwuConfirm="caiwuConfirm"
            ></reimburse-sheet>
            <applyloan-sheet v-if="type=='2'"
                             :tableData="tableData"
                             :curdate="date"
                             :applyID="apply_id"
                             :org_full_name="org"
                             :name="create_person"
                             :bankname="bank_name"
                             :bank_card_id="bank_card_id"
                             :constperson="approval_person"
                             :copyperson="copyperson"
                             :counter="counter"
                             :tips="tips"
                             :caiwuConfirm="caiwuConfirm"
            ></applyloan-sheet>
            <repayment-sheet v-if="type=='3'"
                             :tableData="tableData"
                             :curdate="date"
                             :applyID="apply_id"
                             :org_full_name="org"
                             :name="create_person"
                             :bankname="bank_name"
                             :bank_card_id="bank_card_id"
                             :constperson="approval_person"
                             :copyperson="copyperson"
                             :counter="counter"
                             :tips="tips"
                             :caiwuConfirm="caiwuConfirm"
            ></repayment-sheet>
            <payment-sheet v-if="type=='4'"
                           :tableData="tableData"
                           :curdate="date"
                           :applyID="apply_id"
                           :org_full_name="org"
                           :name="create_person"
                           :bankname="bank_name"
                           :bank_card_id="bank_card_id"
                           :to_name="to_name"
                           :constperson="approval_person"
                           :copyperson="copyperson"
                           :counter = "counter"
                           :tips="tips"
                           :des="des"
                           :consttypename="consttypename"
                           :caiwuConfirm="caiwuConfirm"
            ></payment-sheet>
            <purchase-sheet v-if="type=='5'"
                            :tableData="tableData"
                            :curdate="date"
                            :applyID="apply_id"
                            :org_full_name="org"
                            :name="create_person"
                            :bankname="bank_name"
                            :bank_card_id="bank_card_id"
                            :to_name="to_name"
                            :constperson="approval_person"
                            :copyperson="copyperson"
                            :counter = "counter"
                            :tips="tips"
                            :des="des"
                            :caiwuConfirm="caiwuConfirm"
            ></purchase-sheet>
        <div class="attachment-table mt-lg" v-if="type!=3">
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">附件：</el-col>
                <el-col :xs="24" :sm="16" class="upload-wrap">
                    <el-table :data="attachment" border style="max-width:661px; margin-left: 50px;" max-height="250">
                        <el-table-column type="index" label="序号" width="80"></el-table-column>
                        <el-table-column prop="name" label="附件名" width="400"></el-table-column>
                        <el-table-column prop="ext" label="格式" width="100"></el-table-column>
                        <el-table-column label="操作" width="80">
                            <template scope="scope">
                                <el-button @click="attachment_download(scope.$index,scope.row)" type="text" size="small">下载
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
        </div>
        <div class="mt-lg" v-if="status=='4'">
            <label class="f-l" style="width: 50px;">&nbsp;</label>
            <el-button @click="Onconfirm" type="primary">确认</el-button>
        </div>
        <confirm-modal
                :title="confirm_title"
                :apply_id="apply_id"
                :info="confirm_info"
                :departments="confirm_department"
                :bank="confirm_bank"
                :bankType="confirm_bankType"
                :loading="confirm_loading"
                :type="confirm_type"
                :status="status"
                :isCollectPayment="isCollectPayment"
                @ToggleDialog="ToggleDialog"
                @OnSubmitConfirmModal="OnSubmitConfirmModal"
        ></confirm-modal>
    </div>
</template>
<script>
    import {SpendTime} from '../../components/common.js'
    import DetailsHead from '../../components/Detailopration/steps.vue'
    import ConfirmModal from '../../components/Detailopration/confirmModal.vue'
    import ReimburseSheet from '../../components/ApprovalSheet/ReimburseSheet.vue'
    import ApplyloanSheet from '../../components/ApprovalSheet/ApplyloanSheet.vue'
    import RepaymentSheet from '../../components/ApprovalSheet/RepaymentSheet.vue'
    import PaymentSheet from '../../components/ApprovalSheet/PaymentSheet.vue'
    import PurchaseSheet from '../../components/ApprovalSheet/PurchaseSheet.vue'

    import { mapGetters } from 'vuex'
    export default{
        components: {
            DetailsHead,
            ReimburseSheet,
            ApplyloanSheet,
            RepaymentSheet,
            PaymentSheet,
            PurchaseSheet,
            ConfirmModal,

        },
        name:'detail',
        data(){
            return{
                type:0,
                title:'',//页面标题
                steps:[],//步骤
                apply_id:'',//审批单编号
                stitle:'',//详情头部标题
                create_person:'',//创建人
                create_time:'',//创建时间
                status:'',//审批状态
                attachment:[],//附件数据
                app_attachment_url:null,//审批单链接
                copyperson:'',//抄送人
                approval_person:'',//审批人
                org:'',//组织架构
                date:'',//审批单时间
                bank_name:'',//银行名称
                bank_card_id:'',//银行卡账号
                tableData:[],//单据动态表数据
                next_des:'',
                caiwuConfirm:'',


                //报销单
                counter:'',//总金额
                //借款单
                tips:'',

                //付款单
                to_name: '',
                des: '',
                consttypename: '',


                //收付款确认
                confirm_info:[],
                confirm_department:[],
                confirm_bank:[],
                confirm_bankType:[],
                confirm_loading:false,
                confirm_type:'',
                isCollectPayment:false,
                confirm_title:'',
                data_type:'',
            }
        },
        computed:{
            ...mapGetters([
                'org_full_name',
                'serverUrl'
            ])
        },
        created(){
            this.type = this.$route.query.type;
            this.apply_id = this.$route.query.apply_id;
            this.$store.dispatch('Detais_list',{apply_id:this.apply_id,type:this.type}).then(response => {
                let data = response.data.data;
                this.stitle = data.title;
                this.create_person = data.person;
                this.create_time = data.create_time;
                this.status = data.status;


                //步骤条
                this.steps = data.flow;
                for (let i = 0; i < this.steps.length; i += 1) {
                    let step = this.steps[i];
                    if (i != this.steps.length-1) {
                        this.steps[i].content = step.name+' &nbsp; '+step.date+'<br>'+step.org;
                        if (step.diff_time) this.steps[i].content += '<br>耗费: '+SpendTime(step.diff_time);
                        if (step.des) this.steps[i].content += '<br>说明: '+step.des;
                    } else {
                        this.steps[i].content = step.status==2 ? step.date+'完成<br>总共耗费: '+SpendTime(data.create_time) : '';
                    }

                    switch(this.steps[i].status) {
                        case 0:
                            this.steps[i].status_name = 'wait'
                            break;
                        case 2:
                            this.steps[i].status_name = i!=this.steps.length-1 ? 'finish' : 'success';
                            break;
                        case 3:
                            this.steps[i].status_name = 'error'
                            break;
                        default:
                            this.steps[i].status_name = 'process'
                            break;
                    }
                }



                this.attachment = data.info.files == "" ? []:data.info.files;
                this.app_attachment_url = data.pdf;
                this.copyperson =  data.copy_person==''?'--':data.copy_person;
                this.approval_person = data.approval_persons;
                this.org = this.$store.getters.org_full_name;
                this.date = data.date;
                this.next_des = data.next_des;
                this.bank_name = data.info.bank_name;
                this.bank_card_id = data.info.bank_card_id;
                for(let i=0;i<data.flow.length;i++){
                    if(i==data.flow.length-2){
                        switch (data.flow[data.flow.length-2].status){
                            case 0:
                                this.caiwuConfirm = '等待确认';
                                break;
                            case 1:
                                this.caiwuConfirm = '确认中';
                                break;
                            case 2:
                                this.caiwuConfirm = '确认完成';
                                break;
                        }
                    }

                }

                //console.log(typeof(this.$route.query.type));
                switch((this.$route.query.type).toString()){
                    case '1':
                        this.counter = data.info.money;
                        this.tableData = data.info.list;
                        break;
                    case '2':
                        this.tips = data.info.tips == null ? '--':data.info.tips;
                        this.tableData = [{money:data.info.money,des:data.info.des}];
                        break;
                    case '3':
                        this.tips = data.info.des == null ? '--':data.info.des;
                        this.counter = data.info.money;
                        this.tableData = data.info.list;
                        console.log(this.tableData);
                        break;
                    case '4':
                        this.to_name = data.info.to_name;
                        this.des = data.info.des == null ? '--':data.info.des;
                        this.counter = data.info.money;
                        this.consttypename = data.info.pay_type_name;
                        break;
                    case '5':
                        this.to_name = data.info.to_name;
                        this.des = data.info.des == null ? '--':data.info.des;
                        this.counter = data.info.money;
                        this.tableData = data.info.buy_list;
                        this.counter = 0;
                        for (let i in this.tableData) {
                            let price = this.tableData[i].price * this.tableData[i].amount;
                            this.tableData[i].total = price;
                            this.counter += price;
                        }
                        break;
                }
            });
            this.Title();
        },
        watch:{
            'stepsdata':(val,oldval)=>{
                console.log(val,oldval);
            }
        },
        methods:{
            Title(){
                if(this.type == '1'){
                    this.title = '报销';
                }
            },
            //审批单下载
            approve_attachment_download(){
                window.open('http://'+window.location.host+'/oa_v1/default/get-pdf?apply_id='+this.apply_id);
            },
            //附件下载
            attachment_download(index,row){
                window.open('http://'+window.location.host+'/oa_v1/default/down?path=' + row.url + '&name=' + row.name + '.' + row.ext);
            },
            ToggleDialog(){
                this.isCollectPayment = false;
            },
            Onconfirm(){
                this.confirm_loading = false;
                this.isCollectPayment = true;
                let data_type ='';
                if(this.type == 1 || this.type == 2 || this.type == 4 || this.type == 5){
                    this.confirm_type = 'payment';
                    this.confirm_title = '付款确认';
                    data_type='Payment_confirm_init';
                }else if(this.type == 3){
                    this.confirm_type = 'collection';
                    this.confirm_title = '收款确认';
                    data_type='Collection_confirm_init';
                }
                this.$store.dispatch(data_type,{apply_id:this.apply_id}).then(response=>{
                    let data = response.data.data;
                    let {entries} = Object;
                    let org = [];
                    for (let [key,value] of entries(data.pay_org)) {
                        org.push([key,value]);
                    }
                    for (let item in org){
                        this.confirm_department.push({label:org[item][1],value:org[item][0]});
                    }
                    this.confirm_bank = data.pay_bank;
                    this.confirm_bankType = data.tags;
                    this.confirm_info = [{namebank:this.create_person+" "+data.bank_name,bank_card_id:data.bank_card_id,bank_name_des:data.bank_name_des}];
                })
            },
            OnSubmitConfirmModal(data){
                this.confirm_loading = true;
                let data_type ='';
                if(this.type == 1 || this.type == 2 || this.type == 4 || this.type == 5){
                    data_type='Payment_confirm_form';
                }else if(this.type == 3){
                    data_type='Collection_confirm_form';
                }
                 return new Promise((resolve, reject) => {
                     this.$store.dispatch(data_type,data).then(response => {
                         if (response.data.code == 200) {
                             this.confirm_loading = false;
                             this.isCollectPayment = false;
                             this.$message({
                                 message: '确认成功!',
                                 type: 'success',
                                 duration:1000,
                                 onClose:()=>{
                                     if(data_type=="Collection_confirm_form"){
                                        this.$router.push({path: '/finance/make_collections'});
                                     }else{
                                         this.$router.push({path: '/finance/payment'});
                                     }
                                 }
                             });
                         }
                     }).catch(error => {
                         reject(error);
                     });
                 });
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-table{
        p{ float: left}
    }
</style>


