<template>
    <div class="container">
        <h3 class="mb-md">门店列表</h3>
        <el-row class="store_main mb-md">
            <el-row class="store_header" >
                <el-col :span="2"><div class="grid-content bg-purple">{{Statistics.all}}</div></el-col>
                <el-col :span="2"><div class="grid-content bg-purple">{{Statistics.ing}}</div></el-col>
                <el-col :span="2"><div class="grid-content bg-purple">{{Statistics.pass}}</div></el-col>
                <el-col :span="2"><div class="grid-content bg-purple">{{Statistics.fail}}</div></el-col>
            </el-row>
            <el-row class="store_footer" >
                <el-col :span="2"><div class="grid-content bg-purple">门店选址总数</div></el-col>
                <el-col :span="2"><div class="grid-content bg-purple">选址待确认</div></el-col>
                <el-col :span="2"><div class="grid-content bg-purple">选址通过</div></el-col>
                <el-col :span="2"><div class="grid-content bg-purple">选址不通过</div></el-col>
            </el-row>
        </el-row>

        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">

                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="keywords">
                                    <el-input v-model="ruleForm.keywords" placeholder="门店城市／选址／选址人"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="applyDate">
                                    <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围"></el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="24" class="t-r">
                        <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isClear('ruleForm')">清除</el-button>
                    </el-col>
            </el-row>
        </el-form>
        <el-row >
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                <el-table-column type="index" prop="id" label="序号" width="100"></el-table-column>
                <el-table-column prop="city" label="门店城市" ></el-table-column>
                <el-table-column prop="address" label="门店选址" ></el-table-column>
                <el-table-column prop="rental" label="门店租金" ></el-table-column>
                <el-table-column prop="person" label="选址人"></el-table-column>
                <el-table-column prop="date" label="申请日期" sortable="custom" width="250"></el-table-column>
                <el-table-column prop="status" label="状态" ></el-table-column>
                <el-table-column prop="operation" label="操作" >
                    <template scope="scope">
                        <el-button
                                @click="Tolink(scope.$index,scope.row)"
                                type="text"
                                size="small">
                            详情
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>


    </div>
</template>

<script>
    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";
    export default {
        components: {
            ElCol,
            ElRow
        },
        name:'storeList',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
                Popup:false,
                item:'',
                Statistics:''
            };
        },
        created(){
            this.$store.dispatch('storeList_table',{type:1,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.page.currentPage;
                this.total = data.page.totalCount;
            });
            this.$store.dispatch('storeStatistics_table').then(response=>{
                this.Statistics=response.data.data;
            });
        },
        computed:{
            tableData(){
                return this.$store.state.StoreClass.StoreClass_table;
            },
        },
        methods:{
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('storeList_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            Tolink(index,row) {
                console.log(index, row);
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:13}});
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family: Helvetica, sans-serif;
        .store_main{
            border-left:1px solid #E9E9E9 ;
            padding-left:10px;
            .store_header{
                font-size: 25px;
                font-weight:bold;
                color:rgba(41, 36, 36, 0.67);
            }
            .store_footer{
                font-size: 14px;
                font-weight: bold;
                color: rgba(36, 39, 45, 0.51);
            }
        }

    }
</style>






