import { Details_data } from '../../../api/public';

const detail = {
    state: {

    },
    mutations: {

    },
    actions: {
        //详情数据请求
        Detais_list:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Details_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
}
export default detail


