import { Staff_add,Staff_update,Staff_list,Staff_info } from '../../../api/basic';


const StaffInformation = {
    state: {
        Staffclass_table:[],//表数据

    },
    mutations: {
        SET_staff_table: (state, data) => {
            state.Staffclass_table = data;
        },
    },
    actions: {
        //获取员工列表
        StaffList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Staff_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_staff_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //添加员工类型
        StaffAdd_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Staff_add(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //编辑员工类型
        StaffUpdate_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Staff_update(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
};


export default StaffInformation;



