<template>
    <div class="container">
        <h3 class="mb-md">在借款员工明细表</h3>
        <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="关键字" prop="keywords">
                            <el-input v-model="ruleForm.keywords" placeholder="借款人/事由"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="部门" prop="department">
                            <el-cascader
                                    v-model="ruleForm.department"
                                    placeholder="请选择"
                                    :options="options"
                                    filterable
                            ></el-cascader>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="申请时间" prop="applyDate">
                            <el-date-picker
                                    v-model="ruleForm.applyDate"
                                    type="daterange"
                                    placeholder="选择日期范围">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24" class="t-r">
                        <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isClear('ruleForm')">清除</el-button>
                    </el-col>
                </el-row>
            </el-row>
        </el-form>
        <el-row>
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                <el-table-column prop="id" label="序号" width="65"></el-table-column>
                <el-table-column prop="get_money_time" label="借款时间" sortable="custom" width="150"></el-table-column>
                <el-table-column prop="person" label="借款人" width="190"></el-table-column>
                <el-table-column prop="org" label="部门"></el-table-column>
                <el-table-column prop="money" label="借款金额" width="150"></el-table-column>
                <el-table-column prop="des" label="事由"></el-table-column>
                <el-table-column prop="operation" label="操作" width="65">
                    <template scope="scope">
                        <el-button
                                @click="Tolink(scope.$index,scope.row)"
                                type="text"
                                size="small">
                            详情
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>
    </div>
</template>

<script>
    export default {
        components: {},
        name:'repaymentStatistics',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                currentpage:1,//当前页
                pageSize:10,//一页N天
                total:null,//总记录数
                sort:'',//排序
                orgId:[],//组织架构
                keywords:null,//关键词
                time:'',//时间
                type:2//类型
            }
        },
        created(){
            this.$store.dispatch('repaymentStatistics_table',{pageSize:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.pages.currentPage;
                this.total = data.pages.totalCount;
            });
            this.$store.dispatch('OrganizationalStructure');
        },
        computed:{
            tableData(){
                //console.log(this.$Store.state.RepaymentStatistics.repaymentStatistics_table);
                return this.$store.state.RepaymentStatistics.repaymentStatistics_table;
            },
            options(){
                return this.$store.state.global.organizationalStructure;
            }
        },
        methods:{
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    pageSize:this.pageSize,
                    sort:this.sort,
                    orgId:this.orgId,
                    key:this.keywords,
                    time:this.time,
                };
            },
            ondispatch(){
                this.$store.dispatch('repaymentStatistics_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.pages.currentPage;
                        this.total = data.pages.totalCount;
                        this.pageSize = data.pages.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            Tolink(index,row) {
                //console.log(index, row);
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:2}});
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.key ='';
                this.time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        this.orgId = this.ruleForm.department;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd")+'--'+this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .search-wrap{
        padding: 15px;
        background: #fbfbfb;
        border: 1px solid #d9d9d9;
        border-radius: 6px;
    }
</style>



