<template>
    <el-dialog title="增加审批人" :visible="isconstshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm">
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>审批人：</el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item class="mb-0" prop="constperson" :rules="[{ required: true, message: '请选择'}]" >
                        <el-select v-model="ruleForm.constperson" placeholder="请选择" :loading="selectloading" loading-text="数据加载中" filterable style="width: 100%" >
                            <el-option :label="item.name" :key="key" :value="item.id+'/'+item.name" :disabled="item.disabled" v-for="(item,key) in datavalue"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="onsubmitform('ruleForm')" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import _ from 'underscore';
    export default{
        name:'ConstModal',
        props:['cpperson','constdata','isconstshow','selectloading'],
        data(){
            return{
                ruleForm:{
                    constperson:'',
                },
                hide:false,
                loading:false
            }
        },
        computed:{
            datavalue(){
                let constdata = this.constdata;
               // debugger
                //console.log(constdata);
                let constperson = this.cpperson;
                if(constdata.length > 0){
                    let resultdata = [],constid = [];
                    constdata.map(data=> constid.push(parseInt(data.split("/")[0])));
                    for(let i=0;i<constperson.length;i++){
                        let personid=[];
                        personid.push(constperson[i].id);
                        if(_.intersection(constid,personid).length > 0){
                            resultdata.push({'id':constperson[i].id,'name':constperson[i].name,disabled:true});
                        }else{
                            resultdata.push(constperson[i]);
                        }
                    }
                    return resultdata;
                }else{
                    return constperson;
                }
            },
        },
        methods: {
            isVisible(formName){
                this.$emit('ToggleConstDialog',{isconstshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        let consttext = this.ruleForm.constperson;
                        this.$emit('Onconstsubmit',{consttext:consttext});
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleConstDialog',{isconstshow:false});
                            this.$refs[formName].resetFields();
                        },300);
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


