<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :district="citys"
                :address="address"
                :rental="rental"
                :summary="summary"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></SubmitModal>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">门店申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>门店城市：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="district" :rules="[{ required: true, message: '请选择门店城市'}]">
                        <el-cascader v-model="ruleForm.district" placeholder="请选择" :options="options" filterable style="width: 100%;"></el-cascader>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>门店选址：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="address" :rules="[{ required: true, message: '请输入门店选址'}]">
                        <el-input v-model="ruleForm.address" placeholder="请输入" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>门店租金：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item label="" prop="rental" :rules="[{ required: true, message: '请输入门店租金'}]">
                        <el-input v-model="ruleForm.rental" placeholder="请输入" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>门店概述：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item label="" prop="summary" :rules="[{ required: true, message: '请输入门店概述'}]">
                        <el-input v-model="ruleForm.summary" type="textarea" :rows="3" placeholder="请输入" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="10">
                        <el-upload class="upload-demo" multiple name="files" :action="BaseUrl" :on-success="handlefileChange" :on-remove="handlefileRemove">
                            <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                        </el-upload>
                </el-col>
            </el-row>
            <h3>审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>抄送人：
                </el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"></el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <el-button @click="onsubmitCheck('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/newStore/submitModal.vue';
    export default {
        name:'newstore',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    district: [],
                    address: '',
                    summary:'',
                    rental:''
                },
                citys:'',
                district:'',
                address:'',
                rental:'',
                summary:'',
                fileList: [],
                loading:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.newStore.newStore_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.newStore.newStore_constdata;
            },
            copydata(){
                return this.$store.state.newStore.newStore_copydata;
            },
            options(){
                return this.$store.state.global.citys_data;
            }
        },
        created(){
            this.$store.dispatch("Citys_init");
        },
        methods: {
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '合计';
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                    if(index === 1){
                        if (!values.every(value => isNaN(value))) {
                            sums[index] = values.reduce((prev, curr) => {
                                const value = Number(curr);
                                if (!isNaN(value)) {
                                    let pcvalue = prev*1 + curr*1;
                                    pcvalue = (pcvalue).toFixed(2);
                                    return pcvalue;
                                } else {
                                    return (prev).toFixed(2);
                                }
                            }, 0);
                        } else {
                            sums[index] = '';
                        }
                    }
                });
                return sums;
            },
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            deleteRow(index, rows) {
                rows.splice(index,1);
                this.$store.dispatch('Deleterows',rows);
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_newStore_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_newStore_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_newStore_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_newStore_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },


            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.newStore.newStore_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.newStore.newStore_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }

                let fujian = (this.fileList).map(data => data.response.data[0]);

                let data = {
                    apply_id:this.ApplyID,
                    district:this.district,
                    address:this.address,
                    rental:this.rental,
                    summary:this.summary,
                    files:fujian,
                    approval_persons:resultconstdata,
                    copy_person:resulcopydata,
                };

                this.$store.dispatch('NewStore_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:13}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitCheck(formName){
                let constdata = this.$store.state.newStore.newStore_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0) {
                            this.district = this.ruleForm.district[this.ruleForm.district.length - 1];
                            let resultcitys='';
                            let citys =  this.options;
                            for(var j = 0; j < this.ruleForm.district.length; j++){
                                if(j == this.ruleForm.district.length-1){
                                    resultcitys += this.eachArray(citys,this.ruleForm.district[j]);
                                }else{
                                    resultcitys += this.eachArray(citys,this.ruleForm.district[j]) +'-';
                                }
                            }

                            this.citys = resultcitys;
                            this.address = this.ruleForm.address;
                            this.summary = this.ruleForm.summary;
                            this.rental = parseFloat(this.ruleForm.rental);
                            this.issubmitshow = true;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            },
            //递归遍历数组
            eachArray (obj,b){
                for(var i = 0; i < obj.length;i++){
                    if(obj[i].value == b){
                        name = obj[i].label;
                        break;
                    }else{
                        if(obj[i].children){
                            this.eachArray(obj[i].children,b);
                        }
                    }
                }
                return name;
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
        text-decoration: none;
        color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


