import { AddApplyID,Demand_Create } from '../../../api/public';

const demand = {
    state: {
        demand_constdata: [],//已选中审批人
        demand_copydata: [],//已选中抄送人
        demand_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_demand_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.demand_constdata.push(data);
            }else{
                state.demand_constdata = data;
            }
        },
        SET_demand_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.demand_copydata).push(data);
            }else{
                state.demand_copydata = data;
            }
        },
        SET_demand_addApplyID: (state, data) => {
            state.demand_addApplyID = data;
        },
    },
    actions: {
        //添加审批人
        Add_demand_constsubmit:({ commit },data) =>{
            commit('SET_demand_constdata',data.consttext);
        },
        Delete_demand_ConstPerson:({ commit },data) =>{
            commit('SET_demand_constdata',data);
        },

        //添加抄送人
        Add_demand_copysubmit:({ commit },data) =>{
            commit('SET_demand_copydata',data.consttext);
        },
        Delete_demand_CopyPerson:({ commit },data) =>{
            commit('SET_demand_copydata',data);
        },

        //获取addApplyID
        Demand_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_demand_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        Demand_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                Demand_Create(data).then(response => {
                    commit('SET_demand_constdata',[]);
                    commit('SET_demand_copydata',[]);
                    commit('SET_demand_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  demand;


