import { AddApplyID,NewStore_Create } from '../../../api/public';

const newStore = {
    state: {
        newStore_constdata: [],//已选中审批人
        newStore_copydata: [],//已选中抄送人
        newStore_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_newStore_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.newStore_constdata.push(data);
            }else{
                state.newStore_constdata = data;
            }
        },
        SET_newStore_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.newStore_copydata).push(data);
            }else{
                state.newStore_copydata = data;
            }
        },
        SET_newStore_addApplyID: (state, data) => {
            state.newStore_addApplyID = data;
        },
    },
    actions: {
        //添加审批人
        Add_newStore_constsubmit:({ commit },data) =>{
            commit('SET_newStore_constdata',data.consttext);
        },
        Delete_newStore_ConstPerson:({ commit },data) =>{
            commit('SET_newStore_constdata',data);
        },

        //添加抄送人
        Add_newStore_copysubmit:({ commit },data) =>{
            commit('SET_newStore_copydata',data.consttext);
        },
        Delete_newStore_CopyPerson:({ commit },data) =>{
            commit('SET_newStore_copydata',data);
        },

        //获取addApplyID
        NewStore_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_newStore_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        NewStore_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                NewStore_Create(data).then(response => {
                    commit('SET_newStore_constdata',[]);
                    commit('SET_newStore_copydata',[]);
                    commit('SET_newStore_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  newStore;


