import Vue from 'vue';
import Router from 'vue-router';

/* layout */
import Layout from '../views/main/Layout.vue';
import AdminHome from '../views/adminhome.vue';
import Reimburse  from '../views/Home/reimburse.vue';//报销申请
import Applyloan  from '../views/Home/applyloan.vue';//借款申请
import Repayment  from '../views/Home/repayment.vue';//还款申请
import Forpayment  from '../views/Home/forpayment.vue';//付款申请
import Purchase  from '../views/Home/purchase.vue';//
import Demand  from '../views/Home/demand.vue';//
import Chapter  from '../views/Home/chapter.vue';//
import AssetsRecipients  from '../views/Home/assetsRecipients.vue';
import AssetsReturn  from '../views/Home/assetsReturn.vue';
import Applyleave  from '../views/Home/applyleave.vue';//申请离职
import Positive  from '../views/Home/positive.vue';//申请转正
import ApproveTransfer  from '../views/Home/approveTransfer.vue';
import NewStore  from '../views/Home/newStore.vue';


//申请提交成功
import Success  from '../views/Home/Success.vue';

//审批管理
import WaitMe from '../views/Approvals/waitmeApproal.vue';
import AlreadyApproval from '../views/approvals/alreadyapproval.vue';
import MySend from '../views/approvals/mysend.vue';
import CcSend from '../views/approvals/ccsend.vue';
import Details from '../views/Home/Details.vue';
import Confirm from '../views/Home/confirm.vue';

// 财务管理
import Payment from '../views/Finance/payment.vue';
import Make_collections from '../views/Finance/make_collections.vue';

//人事管理
import Talents from '../views/Hire/talents.vue';
import Staffs from '../views/Hire/staffs.vue';
import Salary from '../views/Hire/salary.vue';
import Resume from '../views/Hire/resume.vue';
import StaffDetail from '../views/Hire/staffdetail.vue';

//基础数据
import AssetBrand from '../views/BasicData/AssetBrand.vue';
import AssetClass from '../views/BasicData/AssetClass.vue';
import personnelClass from '../views/BasicData/personnelClass.vue';
import staffInformation from '../views/BasicData/staffInformation.vue';

//门店
import StoreList from '../views/Store/storeList.vue';

//行政
import DemandList from '../views/Administration/demandList.vue';
import ApplyList from '../views/Administration/applyList.vue';
import InventoryManagement from '../views/Administration/inventoryManagement.vue';
import InventoryDetails from '../views/Administration/inventoryDetails.vue';


//统计分析
import RepaymentStatistics from '../views/StatisticsAnalysis/RepaymentStatistics.vue';

Vue.use(Router);

export const constantRouterMap = [
    {
        path: '',
        component: Layout,
        redirect: '/adminhome',
        noDropdown: true,
        children: [
            { path: 'adminhome', component: AdminHome,name:'首页' },
            { path: 'reimburse', component: Reimburse,name:'申请报销' },
            { path: 'applyloan', component: Applyloan,name:'申请借款' },
            { path: 'repayment', component: Repayment,name:'申请还款' },
            { path: 'forpayment',component: Forpayment,name:'申请付款' },
            { path: 'purchase', component: Purchase,name:'申请请购' },
            { path: 'demand', component: Demand,name:'发起需求' },
            { path: 'chapter', component: Chapter,name:'申请用章' },
            { path: 'assetsrecipients', component: AssetsRecipients,name:'固定资产领用' },
            { path: 'assetsReturn', component: AssetsReturn,name:'固定资产归还' },
            { path: 'positive', component: Positive,name:'申请转正' },
            { path: 'applyleave', component: Applyleave,name:'申请离职' },
            { path: 'approvetransfer', component: ApproveTransfer,name:'申请调职' },
            { path: 'newstore', component: NewStore,name:'申请开店'},//meta: { role: ['tuozhan'] }
            { path: 'success', component: Success, hidden: true,name:'申请提交成功' },
        ]
    },
]

export default new Router({
    //mode: 'history', //后端支持可开
    scrollBehavior: () => ({ y: 0 }),
    routes: constantRouterMap
});


export const asyncRouterMap = [
    {
        path: '/approvals',
        component: Layout,
        redirect: '/approvals/waitme',
        name: '审批管理',
        icon: 'iconfont icon-shenpi3',
        meta: { role: ['yuangong','caiwu','caiwujingli','xingzheng','tuozhan','zhaopin','xinchou','renshi'] },
        children: [
            { path: 'waitme', component: WaitMe,name:'待我审批',meta: { role: ['yuangong','caiwu','caiwujingli','xingzheng','tuozhan','zhaopin','xinchou','renshi'] }},
            { path: 'alreadyapproval', component: AlreadyApproval,name:'我已审批',meta: { role: ['yuangong','caiwu','caiwujingli','xingzheng','tuozhan','zhaopin','xinchou','renshi']}},
            { path: 'mysend', component: MySend,name:'我发起的',meta: { role: ['yuangong','caiwu','caiwujingli','xingzheng','tuozhan','zhaopin','xinchou','renshi'] }},
            { path: 'ccsend', component: CcSend,name:'抄送给我',meta: { role: ['yuangong','caiwu','caiwujingli','xingzheng','tuozhan','zhaopin','xinchou','renshi'] }},
            { path: 'details', component: Details,name:'详情',hidden: true,},
            { path: 'confirm', component: Confirm,name:'确认',hidden: true,meta: { role: ['yuangong','caiwu','caiwujingli','xingzheng','tuozhan','zhaopin','xinchou','renshi']}},
        ]
    },
    {
        path: '/finance',
        component: Layout,
        redirect: '/finance/payment',
        name: '财务管理',
        icon: 'zujian',
        meta: { role: ['caiwu','caiwujingli'] },
        children: [
            {path: 'payment', component: Payment,name: '付款确认',meta: { role: ['caiwu','caiwujingli'] }},
            {path: 'make_collections', component: Make_collections,name: '收款确认',meta: { role: ['caiwu','caiwujingli'] }}
        ]
    },
    {
        path: '/Administration',
        component: Layout,
        redirect: '/Administration/demandList',
        name: '行政',
        hidden: true,
        children: [
            {path: 'demandList', component: DemandList,name: '需求单列表'},
            {path: 'applyList', component: ApplyList,name: '请购列表'},
            {path: 'inventoryManagement', component: InventoryManagement,name: '库存管理'},
            {path: 'inventoryDetails', component: InventoryDetails,name: '库存详情',hidden: true,}
        ]
    },
    {
        path: '/hire',
        component: Layout,
        redirect: '/hire/talents',
        name: '人事管理',
        hidden: true,
        children: [
            {path: 'talents', component: Talents, name: '招聘管理' },
            {path: 'talents?joined=1', component: Talents, name: '人才库管理' },
            {path: 'resume', component: Resume, name: '人才简历', hidden: true },
            {path: 'staffs', component: Staffs, name: '员工管理' },
            {path: 'detail', component: StaffDetail, name:'员工详情',hidden: true},
            {path: 'salary', component: Salary, name: '薪酬管理' },
        ]
    },
    {
        path: '/basicData',
        component: Layout,
        redirect: '/basicData/AssetClass',
        name: '基础数据',
        hidden: true,
        meta: { role: ['yuangong','caiwu',]},
        children: [
            {path: 'AssetBrand', component: AssetBrand ,name: '资产品牌管理'},
            {path: 'AssetClass', component: AssetClass,name: '资产类别管理'},
            {path: 'personnelClass', component: personnelClass,name: '人才类型管理'},
            {path: 'staffInformation', component: staffInformation,name: '员工信息类型管理'}
        ]
    },
    {
        path: '/Store',
        component: Layout,
        redirect: '/Store/storeList',
        name: '门店列表',
        hidden: true,
        children: [
            {path: 'storeList', component: StoreList,name: '门店'},//,meta: { role: ['caiwu','caiwujingli'] }

        ]
    },
    {
        path: '/statisticsfx',
        component: Layout,
        redirect: '/statistics/index',
        name: '统计&分析',
        icon: 'statistics',
        meta: { role: ['caiwujingli'] },
        children: [
            { path: 'index', component: RepaymentStatistics,name:'在借款员工明细表',meta: { role: ['caiwujingli'] } },
        ]
    },
];


