<template>
    <el-dialog title="增加抄送人" :visible="iscopyshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm">
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">抄送人：</el-col>
                <el-col :xs="24" :sm="20" >
                    <el-form-item class="mb-0" prop="copyperson" :rules="[{ required: true, message: '请选择'}]" >
                        <el-select v-model="ruleForm.copyperson" placeholder="请选择" filterable style="width: 100%">
                            <el-option :key="key" :label="item.name" :value="item.id+'/'+item.name" :disabled="item.disabled" v-for="(item,key) in datavalue"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="onsubmitform('ruleForm')" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import _ from 'underscore';
    export default{
        name:'CopyModal',
        props:['cpperson','copydata','iscopyshow'],
        data(){
            return{
                ruleForm:{
                    copyperson:'',
                },
                hide:false,
                loading:false
            }
        },
        computed:{
            datavalue(){
                let copydata = this.copydata;
                let copyperson = this.cpperson;
                //console.log(copydata.length);
                if(copydata.length > 0){
                    let resultdata = [],constid = [];
                    copydata.map(data=> constid.push(parseInt(data.split("/")[0])));
                    for(let i=0;i<copyperson.length;i++){
                        let personid=[];
                        personid.push(copyperson[i].id);
                        if(_.intersection(constid,personid).length > 0){
                            resultdata.push({id:copyperson[i].id,name:copyperson[i].name,disabled:true});
                        }else{
                            resultdata.push(copyperson[i]);
                        }
                    }
                    return resultdata;
                }else{
                    return this.cpperson;
                }
            },
        },
        created(){
           // console.log(this.iscopyshow);
        },
        methods: {
            isVisible(formName){
                this.$emit('ToggleCopyDialog',{iscopyshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        let consttext = this.ruleForm.copyperson;
                        this.$emit('Oncopysubmit',{consttext:consttext});
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleCopyDialog',{iscopyshow:false});
                            this.$refs[formName].resetFields();
                        },300);
                    } else {
                        return false;
                    }
                });
                this.$refs[formName].resetFields();
            }
        }
    }
</script>


