import fetch from '../utils/fetch';
/*import axios from 'axios';*/
import qs from 'qs';

//需求单列表
export function Demand_list(params) {
    return fetch({
        url: '/oa_v1/apply-demand/list',
        method: 'get',
        params:params
    });
}
export function DemandConfirmData(params) {
    return fetch({
        url: '/oa_v1/apply-demand/confirm-buy',
        method: 'post',
        data: qs.stringify(params)
    });
}

//请购列表
export function Apply_list(params) {
    return fetch({
        url: '/oa_v1/apply-buy/list',
        method: 'get',
        params:params
    });
}

//请购入库弹层列表
export function Enter_Storage_List(params) {
    return fetch({
        url: '/oa_v1/apply-buy/buy-list',
        method: 'get',
        params:params
    });
}
//请购弹窗提交
export function Enter_Storage_Update_data(params) {
    return fetch({
        url: '/oa_v1/apply-buy/add-stock',
        method: 'post',
        data: qs.stringify(params)
    });
}


