<template>
    <el-dialog  class="constom-md-dialog" title="增加需求明细" :visible.sync="istableshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm">
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>需求名称：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="demand_name" :rules="[{ required: true, message: '请选择' }]">
                        <el-input v-model="ruleForm.demand_name" placeholder="请输入" style="width:100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>数量：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="amount" :rules="[{ required: true, message: '请选择' }]">
                        <el-input-number v-model="ruleForm.amount" :step="1" :min="1" style="width:100%;"></el-input-number>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button @click="onsubmitform('ruleForm')" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import { mapState } from 'vuex'
    export default {
        name:'TableModal',
        props:['istableshow'],
        data(){
            var checkmoney = (rule, value, callback) => {
                var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
                if (!value) {
                    return callback(new Error('报销金额不能为空'));
                }else{
                    if (!reg.test(value)) {
                        callback(new Error('金额格式错误'));
                    } else {
                        callback();
                    }
                };
            };
            return {
                ruleForm: {
                    demand_name:'',
                    amount:'1'
                },
                hide:false,
                loading:false
            }
        },
        computed: {

        },
        created(){

        },
        methods: {
            isVisible(formName){
                this.$emit('ToggleTableDialog',{istableshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let data = {
                            name:this.ruleForm.demand_name,
                            amount:this.ruleForm.amount
                        };

                        this.loading = true;
                        this.$emit('ontableadd',data);
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleTableDialog',{istableshow:false});
                        },300);
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


