import { AddApplyID,repayment_Create } from '../../../api/public';
import { Repayment_Record } from '../../../api/apply'
import Cookies from 'js-cookie';

const repayment = {
    state: {
        repayment_constdata:[],//已选中审批人
        repayment_copydata:[],//已选中抄送人
        repayment_addApplyID:null,//报销单ID
        repayment_record:[],//需还款记录
    },
    mutations: {
        SET_repayment_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.repayment_constdata.push(data);

                //console.log(state.constdata);
            }else{
                //console.log(constdata);
                state.repayment_constdata = data;
            }
        },
        SET_repayment_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.repayment_copydata).push(data);
                //console.log(state.copydata);
            }else{
                //console.log(copydata);
                state.repayment_copydata = data;
            }
        },
        SET_repayment_addApplyID: (state, data) => {
            state.repayment_addApplyID = data;
        },
        SET_repayment_record: (state, data) => {
            state.repayment_record = data;
        }
    },
    actions: {
        //添加审批人
        Add_repayment_constsubmit:({ commit },data) =>{
            commit('SET_repayment_constdata',data.consttext);
        },
        Delete_repayment_ConstPerson:({ commit },data) =>{
            commit('SET_repayment_constdata',data);
        },

        //添加抄送人
        Add_repayment_copysubmit:({ commit },data) =>{
            commit('SET_repayment_copydata',data.consttext);
        },
        Delete_repayment_CopyPerson:({ commit },data) =>{
            commit('SET_repayment_copydata',data);
        },
        //需还款记录
        Repayment_Record:({ commit }) => {
            return new Promise((resolve, reject) => {
                Repayment_Record().then(response => {
                    let data = response.data.data;
                    commit('SET_repayment_record',data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //获取addApplyID
        Repayment_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID({type:data.type}).then(response => {
                    let apply_id = response.data.data.apply_id;
                    //debugger
                    commit('SET_repayment_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //提交报销单
        Repayment_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                repayment_Create(data).then(response => {
                    commit('SET_repayment_constdata',[]);
                    commit('SET_repayment_copydata',[]);
                    commit('SET_repayment_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}

export default repayment;


