<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        />
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        />

        <CardModal :iscardshow="iscardshow"
                   @ToggleCardDialog="ToggleCardDialog"
        />
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :money="money"
                :bank="bank"
                :des="des"
                :tips="tips"
                :files="fileList"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        />
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">借款申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w97 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>借款金额：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="money"  :rules="[{ required: true, message: '请输入金额'}]">
                        <el-input v-model="ruleForm.money" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w97 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>借款到：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10" class="card-wrap">
                    <el-form-item prop="bank" :rules="[{ required: true, message: '请选择银行卡'}]">
                        <el-select v-model="ruleForm.bank" placeholder="请选择" style="width: 100%;">
                            <el-option :key="key" :label="item.label" :value="item.value" v-for="(item,key) in carddatas"></el-option>
                        </el-select>
                        <a class="f12 addcard" href="javascript:;" @click="ToggleCardDialog({iscardshow:true})">增加银行卡</a>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w97 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>事由：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10" >
                    <el-form-item prop="des" :rules="[{ required: true, message: '请输入事由'}]">
                        <el-input
                                type="textarea"
                                :rows="3"
                                placeholder="请输入"
                                v-model="ruleForm.des">
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w97 f14 mb-md t-r t-xs-l">备注：</el-col>
                <el-col :xs="24" :sm="12" :md="10" >
                    <el-form-item>
                        <el-input
                                type="textarea"
                                :rows="3"
                                placeholder="请输入"
                                v-model="ruleForm.tips">
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w97 f14 mb-md t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="12" :md="10" class="upload-wrap">
                    <el-upload class="upload-demo" multiple name="files" :action="BaseUrl"
                               :file-list="fileList"
                               :on-error="errorchange"
                               :on-success="handlefileChange"
                               :on-remove="handlefileRemove">
                        <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                    </el-upload>
                </el-col>
            </el-row>
            <h3 class="mt-lg mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10">
                    抄送人：
                </el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10"></el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <el-button @click="onsubmitApplyloan('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import CardModal from '../../components/CardModal/CardModal.vue';
    import SubmitModal from '../../components/applyloan/submitModal.vue';
    export default {
        name:'applyloan',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            CardModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    bank: '',
                },
                money:'',
                bank:null,
                des:'',
                tips:'',
                fileList: [],
                loading:false,
                dialogImageUrl:'',
                dialogVisible:false,
                istableshow:false,
                isconstshow:false,
                iscopyshow:false,
                iscardshow:false,
                issubmitshow:false,
                counter:0
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.applyloan.applyloan_addApplyID;
            },
            constType(){
                return this.$store.state.global.constType;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            carddatas(){
                return this.$store.state.global.bankCard;
            },
            constdata(){
                return this.$store.state.applyloan.applyloan_constdata;
            },
            copydata(){
                return this.$store.state.applyloan.applyloan_copydata;
            },
        },
        created(){
            this.$store.dispatch('InitCard');
        },
        methods: {
            isVisible(){
                this.$router.push({path:'/'});
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            errorchange(err,file,fileList){
                this.$message(err);
            },
            beforeUpload(file){
                //console.log(file.type);
                const isjpg = file.type === 'image/jpeg';
                const isgif = file.type === 'image/gif';
                const ispng = file.type === 'image/png';
                const isxls = file.type === 'application/vnd.ms-excel';
                const isxlsx = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                const isdoc = file.type === 'application/msword';
                const isdocx = file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
                if(!isjpg && !isgif && !ispng && !isxls && !isdoc && !isxlsx && !isdocx){
                    this.$message.error('上传文件格式错误，必须是jpg,gif,png,xls,xlsx,doc,docx中的一种!');
                }
                return isjpg || isgif || ispng || isxls || isxlsx || isdoc || isdocx;
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_applyloan_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_applyloan_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_applyloan_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_applyloan_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },
            //添加银行卡弹窗状态修改
            ToggleCardDialog(data){
                this.iscardshow = data.iscardshow;
            },
            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            //核对信息提交按钮
            onsubmitApplyloan(formName){
                let constdata = this.$store.state.applyloan.applyloan_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0) {
                            this.money = parseFloat(this.ruleForm.money);
                            this.bank = this.ruleForm.bank;
                            this.des = this.ruleForm.des;
                            this.tips = this.ruleForm.tips;
                            this.issubmitshow = true;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            },
            Onsubmit(data){
                let constdata = this.$store.state.applyloan.applyloan_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.applyloan.applyloan_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }
                let resultdata = {
                        apply_id: this.ApplyID,
                        bank_card_id: this.bank.split('/')[1],
                        bank_name: this.bank.split('/')[0],
                        bank_des: this.bank.split('/')[2],
                        approval_persons: resultconstdata,
                        copy_person: resulcopydata,
                        money: this.money,
                        des: this.des,
                        tips: this.tips,
                        files: (this.fileList).map(data => data.response.data[0]),
                    };

                this.$store.dispatch('Applyloan_Add',resultdata).then( response => {
                    this.loading = false;
                    this.issubmitshow = false;
                    if(response.data.code==200){
                        this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:2}});
                    }else{
                        this.$message('系统出错，错误信息：'+response.data.message);
                    }
                }).catch(err => {
                    this.loading = false;
                });
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


