<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :selectdata="selectdata"
                :des="des"
                :total="totalmoney"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></SubmitModal>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">固定资产归还申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>资产明细：
                </el-col>
                <el-col :xs="24" :sm="16" class="card-wrap mb-md">
                    <el-table
                            :data="tabledata"
                            :summary-method="getSummaries"
                            ref="multipleTable" border tooltip-effect="dark"
                            @selection-change="handleSelectionChange"
                            style="width: 100%; margin-bottom: 7px;">
                        <el-table-column type="selection" width="55"></el-table-column>
                        <el-table-column type="index" label="序号" width="100"></el-table-column>
                        <el-table-column prop="asset_type" label="类型"></el-table-column>
                        <el-table-column prop="asset_brand" label="品牌"></el-table-column>
                        <el-table-column prop="name" label="名称"></el-table-column>
                        <el-table-column prop="price" label="单价"></el-table-column>
                        <el-table-column prop="socket_number" label="库存编号"></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"><span class="c-red mr4">*</span>接收人：</el-col>
                <el-col :xs="24" :sm="10" class="card-wrap">
                    <el-form-item prop="get_person" :rules="[{ required: true, message: '请选择接收人'}]">
                        <el-select v-model="ruleForm.get_person" placeholder="请选择" style="width: 100%;">
                            <el-option :key="key" :label="item.label" :value="item.value" v-for="(item,key) in getpersons"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"><span class="c-red mr4">*</span>说明：</el-col>
                <el-col :xs="24" :sm="10" class="card-wrap">
                    <el-form-item prop="des" :rules="[{ required: true, message: '请输入说明文字'}]">
                        <el-input type="textarea" :rows="3" placeholder="请输入" v-model="ruleForm.des" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="10" class="card-wrap">
                    <el-form-item>
                        <el-upload class="upload-demo" multiple :on-success="handlefileChange" :action="BaseUrl">
                            <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>
            <h3 class="mt-0">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"><span class="c-red mr4">*</span>审批人：</el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">抄送人：</el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"></el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <el-button @click="onsubmitCheck('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/assetsReturn/SubmitModal.vue';
    export default {
        name:'assetsReturn',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    des: '',
                    files: '',
                    get_person:''
                },
                getpersons:[
                    {label:'孙敏',value:'1/孙敏'},
                    {label:'前台',value:'2/前台'}
                ],
                tabledata:[],
                selectdata:'',
                get_person:'',
                des:'',
                totalmoney:'',
                fileList: [],
                loading:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.assetsReturn.assetsReturn_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.assetsReturn.assetsReturn_constdata;
            },
            copydata(){
                return this.$store.state.assetsReturn.assetsReturn_copydata;
            },
        },
        created(){
            this.$store.dispatch('AssetsReturn_kh_list').then(response=>{
                this.tabledata = response.data.data;
            });
        },
        methods: {
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '合计';
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                     if(index === 5){
                        if (!values.every(value => isNaN(value))) {
                            sums[index] = values.reduce((prev, curr) => {
                                const value = Number(curr);
                                if (!isNaN(value)) {
                                    let pcvalue = prev*1 + curr*1;
                                    pcvalue = (pcvalue).toFixed(2);
                                    return pcvalue;
                                } else {
                                    return (prev).toFixed(2);
                                }
                            }, 0);
                            sums[index] = '￥ ' + sums[index];
                        } else {
                            sums[index] = '';
                        }
                    }
                });
                return sums;
            },
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            deleteRow(index, rows) {
                rows.splice(index,1);
                this.$store.dispatch('Deleterows',rows);
            },
            handlefileRemove(file, fileList) {//图片上传

            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_assetsReturn_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_assetsReturn_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },

            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_assetsReturn_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_assetsReturn_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            //选择表格中数据
            handleSelectionChange(val) {
                this.selectdata = val;
            },

            Onsubmit(){
                this.loading=true;
                let tabledata = this.$store.state.assetsReturn.assetsReturn_tabledata;
                let constdata = this.$store.state.assetsReturn.assetsReturn_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.assetsReturn.assetsReturn_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }


                let fujian = (this.fileList).map(data => data.response.data);
                let data = {
                    apply_id:this.ApplyID,
                    approval_persons:resultconstdata,
                    copy_person:resulcopydata,
                    files:fujian,
                    asset_back_ids:this.selectdata.map(data => data.id),
                    get_person:this.get_person.split("/")[0],
                    des:this.des,
                };

                this.$store.dispatch('AssetsReturn_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:9}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitCheck(formName){
                let constdata = this.$store.state.assetsReturn.assetsReturn_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if(this.selectdata.length > 0 && constdata.length > 0){
                            this.get_person = this.ruleForm.get_person;
                            this.des = this.ruleForm.des;
                            let total =0;
                            this.selectdata.map(data => total = total+ data.price*1);
                            this.totalmoney = total.toFixed(2);
                            this.issubmitshow = true;
                        }else{
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });

            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


