import { Payment_list } from '../../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const payment = {
    state: {
        payment_table:[],//表数据
    },
    mutations: {
        SET_payment_table: (state, data) => {
            state.payment_table = data;
        },
    },
    actions: {
        //表格数据
        Payment_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Payment_list(data).then(response => {
                    let data = response.data.data;
                    //console.log(data);
                    commit('SET_payment_table',data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
    }
}

export default payment;


