import { Brand_add,Brand_update,Brand_list,Brand_info } from '../../../api/basic';


const assetBrand = {
    state: {
        beankclass_table:[],//表数据

    },
    mutations: {
        SET_brand_table: (state, data) => {
            state.beankclass_table = data;
        },
    },
    actions: {
        //获取品牌列表
        Brandlist_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Brand_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_brand_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //添加品牌类型
        BrandAdd_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Brand_add(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //编辑品牌类型
        BrandUpdate_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Brand_update(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
};


export default assetBrand;


