<template>
    <div class="attachment-wrap">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">开店申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{ date }}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{ create_person }}</td>
                    <td class="b_gray">门店城市</td>
                    <td class="t-l">{{ city }}</td>
                </tr>
                <tr>
                    <td class="b_gray">门店选址</td>
                    <td class="t-l">{{ address }}</td>
                    <td class="b_gray">门店租金</td>
                    <td class="t-l">{{ rental }} 元</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" height="150" width="100">门店概述</td>
                    <td colspan="5" width="150" class="t-l">{{ summary }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="100">审批人</td>
                    <td class="t-l">{{constperson}}</td>
                    <td class="b_gray" width="100">抄送人</td>
                    <td class="t-l">{{copyperson==''?'--':copyperson}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    export default{
        name:'ReimburseSheet',
        props:[
            'tableData',
            'tips',
            'date',
            'applyID',
            'org_full_name',
            'create_person',
            'constperson',
            'copyperson',
            'city',
            'address',
            'rental',
            'summary',
        ],
        data(){
            return{

            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        padding:0 20px 25px 20px;
        border-radius: 4px;
        border: 1px solid #E9E9E9;
        .const_wrap {
            text-align: center;

            h1 {
                font-weight: 500;
            }
            table {
                width: 100%;
                border-collapse: collapse;
                border: 0;

                tr:last-child {

                    td {
                        border-bottom: 0;
                    }
                }
                td {
                    border: 1px solid #E9E9E9;
                    padding: 8px;
                    line-height: 23px;
                    font-size: 12px;
                    color: rgba(0,0,0,0.65);

                    &
                    .b_gray {
                        background: #F2F2F2;
                    }
                }
            }
            table:last-child {
                tr:last-child {
                    td {
                        border-bottom: 1px solid #E9E9E9;
                    }
                }
            }
        }
    }
</style>


