import { 
    resume_List, 
    resume_Add, 
    resume_Approval, 
    join_Talent, 
    employ_Talent 
} from '../../../api/hire';

const talent = {
    state: {
        resumes_table:[],//表数据
    },
    mutations: {
        SET_resumes_table: (state, data) => {
            state.resumes_table = data;
        },
    },
    actions: {
        //表格数据
        Resumes_Table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                resume_List(data).then(response => {
                    let data = response.data.data;
                    commit('SET_resumes_table',response.data.data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
        //添加简历
        Resume_Add:({commit}, data) =>{
            return new Promise((resolve, reject) => {
                resume_Add(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //招聘操作
        Resume_Approval:({commit}, data) =>{
            return new Promise((resolve, reject) => {
                resume_Approval(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //招聘操作
        Join_Talent:({commit}, data) =>{
            return new Promise((resolve, reject) => {
                join_Talent(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        Employ_Talent:({commit}, data) =>{
            return new Promise((resolve, reject) => {
                employ_Talent(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
}

export default talent;


