<template>
	<div class="container">
        <h3 class="mb-md">
        	<a class="back" href="javascript:history.back()"><span class="el-icon-arrow-left"></span>返回</a>
        	<span class="splitter">|</span> 
        	<span>{{name}}的简历</span>
        </h3>	

        <info :resumeId="resumeId" @setName="setName"></info><!-- 基本信息 -->
		<expect :resumeId="resumeId"></expect><!-- 求职方向 -->
		<workexp :resumeId="resumeId"></workexp><!-- 工作经验 -->
		<projectexp :resumeId="resumeId"></projectexp><!-- 工作经验 -->
		<eduexp :resumeId="resumeId"></eduexp><!-- 教育经历 -->


	</div>
</template>

<script>
	import info from '../../components/talentInfo/info.vue';
	import expect from '../../components/talentInfo/expect.vue';
	import workexp from '../../components/talentInfo/workexp.vue';
	import projectexp from '../../components/talentInfo/projectexp.vue';
	import eduexp from '../../components/talentInfo/eduexp.vue';


	export default {
        components: { 
        	info,  
        	expect,
        	workexp,
        	projectexp,
        	eduexp,
        },
        name:'resume',
        data(){
        	return {
        		name: '',
        	}
        },
        computed: {
        	resumeId(){
        		return this.$route.query.talent_id;
        	},
        },
        create(){
        },
        methods: {
        	setName(name){
        		return this.name = name;
        	}
        }

    }
</script>

<style rel="stylesheet/less" lang="less" scoped>
	h3 {
		margin-bottom: 40px;
	}
	.back {
		font-weight: normal;
	}	
	.splitter {
		margin-left: 10px;
		margin-right: 10px;
		color: #999;
	}
</style>


