import { Apply_list,Enter_Storage_List,Enter_Storage_Update_data} from '../../../api/administration';

const ApplyList = {
    state: {
        Apply_table:[],//请购列表数据
        EnterStorageListData:[],//入库列表，
    },
    mutations: {
        SET_Apply_table: (state, data) => {
            state.Apply_table = data;
        },
        SET_EnterStorageListData: (state, data) => {
            state.EnterStorageListData = data;
        },
    },
    actions: {
        //获取请购列表
        ApplyList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Apply_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Apply_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //
        Enter_Storage:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Enter_Storage_List(data).then(response => {
                    let data = response.data.data;
                    commit('SET_EnterStorageListData',data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //确认入库
        Enter_Storage_Update:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Enter_Storage_Update_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
};


export default ApplyList;


