<template>
    <div class="approval_wrap">
        <ul >
            <li v-for="(item,key) in datavalue">
                <div class="approval_left"><img
                        src="data:image/png;base64,"
                        width="58" height="58">
                    <span>{{item.name}}</span>
                    <a v-if="istype === '1'" href="javascript:;" :data-value="key" @click="delconstperson">删除</a>
                    <a v-else-if="istype === '2'" href="javascript:;" :data-value="key" @click="delcopyperson">删除</a>
                </div>
                <div class="approval_right" :style="isapplyrightdata">
                    <i class="el-icon-caret-right"></i>
                </div>
            </li>
            <li class="add_approval" v-if="istype === '1'" :style="isaddapplyproval"  @click="AddConstPersonal" :data-id="istype"><i class="el-icon-plus"></i></li>
            <li class="add_approval" v-else-if="istype === '2'" :style="isaddapplyproval"  @click="AddCopyPersonal"  :data-id="istype"><i class="el-icon-plus"></i></li>
        </ul>
    </div>
</template>
<script>
    export default {
        name:"ConstCopyPerson",
        props:['isapplyright','data','choicetype'],//isapplyright是否显示又边箭头,data 已选择审批抄送人
        data(){
            return {
                isaddapplyproval:()=>{
                    if(this.constdata.length > 7){
                        return 'display:none';
                    }else{
                        return 'display:block';
                    }
                }

            }
        },
        computed: {
            datavalue(){
                let data = this.data || [],newdata = [];
                //console.log(data);
                if(data.length > 0){
                    data.map(data => newdata.push({name:data.split('/')[1].split(" ")[0]}));
                    return newdata;
                }else{
                    return this.data
                }

            },
            isapplyrightdata(){
                let ds = '';
                if(this.isapplyright){
                    ds = 'display:block';
                }else{
                    ds = 'display:none';
                }
                return ds;
            },
            istype(){
                if(this.choicetype == 1){
                    return '1';
                }else{
                    return '2';
                }
            },
            cpPerson(){
                return this.$store.state.global.cpPerson;
            }
        },
        methods: {
            AddConstPersonal(){
                if(this.cpPerson == null){
                    this.$store.dispatch('CpPersonInit').then(response=>{
                        if(response.data.code == 200){
                            this.$emit('ToggleConstDialog',{isconstshow:true,selectloading:false});
                        }
                    });
                }
                this.$emit('ToggleConstDialog',{isconstshow:true,selectloading:false});
            },
            AddCopyPersonal(){
                if(this.cpPerson == null) {
                    this.$store.dispatch('CpPersonInit');
                }
                this.$emit('ToggleCopyDialog',{iscopyshow:true});
            },
            delconstperson(e){
                let newdata = this.data;
                let index = e.target.getAttribute("data-value");
                newdata.splice(index,1);
                this.$emit('DeleteConstPerson',newdata);
            },
            delcopyperson(e){
                let newdata = this.data;
                let index = e.target.getAttribute("data-value");
                newdata.splice(index,1);
                this.$store.dispatch('DeleteCopyPerson',newdata);
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .approval_wrap {
        font-size: 0;
        ul{
            overflow: hidden;
            padding-left: 0;
        }
        .add_approval {
            display: inline-block;
            width: 58px;
            height: 58px;
            line-height: 54px;
            border-radius: 50%;
            border: dashed 1px #ddd;
            font-size: 25px;
            text-align: center;
            cursor: pointer;
            i{
                font-size: 17px;
                color: rgba(0,0,0,0.43);
            }
            &:hover{
                i {
                    color: #108ee9;
                }
            }
        }
        li {
            float: left;
            overflow: hidden;
            .approval_left {
                float: left;
                margin-right: 10px;
                img {
                    width: 58px;
                    height: 58px;
                    margin: 0 auto;
                    display: block;
                    border-radius: 50%;
                }
                span, a {
                    display: block;
                    color: #333;
                    line-height: 20px;
                    text-align: center;
                    font-size: 14px;
                }
                span {
                    line-height: 22px;
                    text-align: center;
                }
                a{
                    color: dodgerblue;
                    text-decoration: none;
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }
            .approval_right {
                float: left;
                margin-top: 5px;
                font-size: 30px;
                margin-right: 10px;
                .el-icon-caret-right:before{
                    font-size: 14px;
                    color: #ccc;
                }
            }
            &:last-child {
                .approval_right {
                    display: none !important;
                }
            }
        }
        li:hover {
            border-color: #108ee9;
        }
    }
</style>


