<template>
    <div class="container">
        <el-form :model="ruleForm" ref="ruleForm"  class="demo-ruleForm">
            <h3 class="mb-md">调职申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>所属部门：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="old_org" :rules="[{ required: true, message: '请选择部门'}]">
                        <el-cascader
                                v-model="ruleForm.old_org"
                                placeholder="请选择"
                                :options="org"
                                filterable
                                style="width:100%;"
                        ></el-cascader>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>职位：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="old_profession" :rules="[{ required: true, message: '请选择职位'}]">
                        <el-select v-model="ruleForm.old_profession" placeholder="请选择" style="width:100%;">
                            <el-option :key="key" :label="item.label" :value="item.value+'/'+item.label" v-for="(item,key) in positions"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>入职日期：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="entry_time" :rules="[{ required: true, message: '请选择入职日期'}]">
                        <el-date-picker v-model="ruleForm.entry_time" type="date" placeholder="选择日期" style="width:100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>调职原因：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="des" :rules="[{ required: true, message: '请选择调整前职位'}]">
                        <el-input type="textarea" v-model="ruleForm.des" placeholder="请输入调职原因" style="width:100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>调职后部门：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="target_org" :rules="[{ required: true, message: '请选择部门'}]">
                        <el-cascader
                                v-model="ruleForm.target_org"
                                placeholder="请选择"
                                :options="org"
                                filterable
                                style="width:100%;"
                        ></el-cascader>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>调整后职位：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="target_profession" :rules="[{ required: true, message: '请选择职位'}]">
                        <el-select v-model="ruleForm.target_profession" placeholder="请选择" style="width:100%;">
                            <el-option :key="key" :label="item.label" :value="item.value+'/'+item.label" v-for="(item,key) in positions"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>调职日期：
                </el-col>
                <el-col :xs="24" :sm="10">
                    <el-form-item prop="transfer_time" :rules="[{ required: true, message: '请选择入职日期'}]">
                        <el-date-picker v-model="ruleForm.transfer_time" type="date" placeholder="选择日期" style="width:100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="10">
                    <el-upload class="upload-demo" multiple name="files" :action="BaseUrl" :on-success="handlefileChange" :on-remove="handlefileRemove">
                        <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                    </el-upload>
                </el-col>
            </el-row>
            <h3 class="mt-md mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>抄送人：
                </el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w110 f14 mb-sm t-r t-xs-l"></el-col>
                <el-col :xs="24" :sm="10" class="mb-lg">
                    <el-button @click="onsubmitTransfer('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
      <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
      ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
       <SubmitModal
               :issubmitshow="issubmitshow"
               :loading="loading"
               :entry_time="entry_time"
               :old_org="oldorgname"
               :target_org="targetorgname"
               :old_profession="old_profession"
               :target_profession="target_profession"
               :transfer_time="transfer_time"
               :des="des"
               @Onsubmit="Onsubmit"
               @TogglesubmitDialog="TogglesubmitDialog"
       ></SubmitModal>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/ApproveTransfer/submitModal.vue';

    let name='';
    export default {
        name:'approveTransfer',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    entry_time:'',
                    old_org:[],
                    target_org:[],
                    old_profession:'',
                    target_profession:'',
                    transfer_time:'',
                    des:'',
                },
                oldorgname:'',
                targetorgname:'',
                entry_time:'',//入职时间
                old_org:'',//原部门
                target_org:'',//调职后部门
                old_profession:'',//原职位
                target_profession:'',//调职后职位
                transfer_time:'',//调制后时间
                des:'',//调职原因
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false,
                loading:false,
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.approveTransfer.transfer_constdata;
            },
            copydata(){
                return this.$store.state.approveTransfer.transfer_copydata;
            },
            //部门初始化数据
            org(){
                return this.$store.state.global.organizationalStructure;
            },
            //职位选择初始化数据
            positions(){
                return this.$store.state.global.position_data;
            }
        },
        created(){
            this.$store.dispatch('Position_List');//职位数据初始化
            this.$store.dispatch('OrganizationalStructure');//部门数据初始化
        },
        methods: {
            isVisible(formName){
                this.issubmitshow = false;
                if(typeof(formName)!='function'){
                    this.$refs[formName].resetFields();
                }
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_transfer_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_transfer_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },

            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_transfer_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_transfer_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.approveTransfer.transfer_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.approveTransfer.transfer_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }

                let apply_id = this.$store.state.approveTransfer.transfer_addApplyID;
                let fujian=[];
                if(this.fileList != null){
                    fujian = (this.fileList).map(data => data.response.data);
                }
                let data = {
                        apply_id:apply_id,
                        approval_persons:resultconstdata,
                        copy_person:resulcopydata,
                        entry_time:this.entry_time,
                        old_org_id:this.old_org[this.old_org.length -1],
                        old_profession_id:parseInt(this.old_profession[0]),
                        target_org_id:this.target_org[this.old_org.length -1],
                        target_profession_id:parseInt(this.target_profession[0]),
                        transfer_time:this.transfer_time,
                        des:this.des,
                        files:fujian,
                    };


                this.$store.dispatch('Transfer_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:12}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitTransfer(formName){
                let constdata = this.$store.state.approveTransfer.transfer_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0) {
                            this.entry_time = this.ruleForm.entry_time.Format("yyyy-MM-dd");
                            let oldorgname='',targetorgname='';
                            let orgs =  this.org;
                            this.old_org = this.ruleForm.old_org;
                            for(var j = 0; j < this.old_org.length; j++){
                                if(j == this.old_org.length-1){
                                    oldorgname += this.eachArray(orgs,this.old_org[j]);
                                }else{
                                    oldorgname += this.eachArray(orgs,this.old_org[j]) +'-';
                                }
                            }

                            this.target_org = this.ruleForm.target_org;;
                            for(var h = 0; h < this.target_org.length; h++){
                                if(h == this.target_org.length-1){
                                    targetorgname += this.eachArray(orgs,this.target_org[h]);
                                }else{
                                    targetorgname += this.eachArray(orgs,this.target_org[h]) +'-';
                                }
                            }

                            this.oldorgname = oldorgname;
                            this.targetorgname = targetorgname;

                            this.old_profession = this.ruleForm.old_profession;
                            this.target_profession = this.ruleForm.target_profession;

                            this.transfer_time = this.ruleForm.transfer_time.Format("yyyy-MM-dd");
                            this.des = this.ruleForm.des;
                            this.issubmitshow = true;

                        }else{
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            },
            //递归遍历数组
            eachArray (obj,b){
                for(var i = 0; i < obj.length;i++){
                    if(obj[i].value == b){
                        name = obj[i].label;
                        break;
                    }else{
                        if(obj[i].children){
                            this.eachArray(obj[i].children,b);
                        }
                    }
                }
                return name;
            }

        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
        text-decoration: none;
        color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


