import { Store_list,Store_Statistics} from '../../../api/store';


const StoreClass = {
    state: {
        StoreClass_table:[],//门店列表数据
        StoreStatistics_table:[],//门店统计数据

    },
    mutations: {
        SET_store_table: (state, data) => {
            state.StoreClass_table = data;
        },
    },
    actions: {
        //获取门店列表
        storeList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Store_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_store_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        storeStatistics_table:({ },data) =>{
            return new Promise((resolve, reject) => {
                Store_Statistics(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
};


export default StoreClass;


