import fetch from '../utils/fetch';
import qs from 'qs';


export function AssetsRecipients_kl_data(params) {
    return fetch({
        url: '/oa_v1/asset/can-get-asset',
        method: 'get',
        params:params
    });
}

export function AssetsRecipients_Create(params) {
    return fetch({
        url: '/oa_v1/asset/get',
        method: 'post',
        data:qs.stringify(params)
    });
}



