<template>
	<el-dialog title="确认入职" size="tiny" :visible="staffentryFormVisible" :before-close="hidedialog" :close-on-click-modal="false" :close-on-press-escape="false">
		<el-form :model="ruleForm" label-width="100px" :rules="ruleForm" ref="ruleForm">
			<el-form-item label="企业邮箱：" prop="email" :rules="[{ required: true, message: '请输入企业邮箱'}]">
                <el-col :span="16">
					<el-input type="email" v-model="ruleForm.email"></el-input>
				</el-col>
			</el-form-item>
			<el-form-item label="企业QQ：" prop="qq">
                <el-col :span="16">
					<el-input v-model="ruleForm.qq"></el-input>
				</el-col>
			</el-form-item>
			<el-form-item label="工作手机号：" prop="phone">
                <el-col :span="16">
					<el-input type="tel" v-model="ruleForm.phone"></el-input>
				</el-col>
			</el-form-item>
		</el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="hidedialog">取 消</el-button>
            <el-button @click="onsubmitform('ruleForm')" type="primary" :loading="loading">确 定</el-button>
        </div>
	</el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';

	export default {
		name: 'staffEntry',
		props:[ 'staffentryFormVisible', 'staffId', 'staffName' ],
	    data() {
	    	return {
	    		ruleForm: {
	    			name: '',
	    			phone: '',
	    			qq: '',
	    			email: '',
	    		},
	    		hide: false,
	    		loading: false,
	    	};
	    },
	    methods: {
	    	//关闭对话框
	    	hidedialog(){
	    		this.$refs['ruleForm'].resetFields();
	    		this.$emit('hideDialog', 'entry');	
	    	},

	    	//提交表单
	    	onsubmitform(formName){
	    		this.loading = true;
	    		this.$refs[formName].validate((valid) => {
                    if (valid) {
			    		let data = {
			    				employee_id: this.staffId,
			    				email: this.ruleForm.email,
			    				qq: this.ruleForm.qq,
			    				phone: this.ruleForm.phone
			    			}; 

			    		this.$store.dispatch('Staff_Entry',data).then( response => {
		                    this.loading = false;
		                    let data = response.data;
		                    if (data.code == 200) {
		                    	this.$message.success('确认入职成功');
			                    this.$emit('refresh');
			                    this.$refs[formName].resetFields();
			                    this.hidedialog();
		                    } else {
		                    	this.$message.error(data.data);	
		                    }
		                }).catch(err => {
		                    this.loading = false;
		                });
                    } else {                    	
		                this.loading = false;
                    }
                });


	    	}

	    }
	};
</script>


