import { AddApplyID,AssetsReturn_Create,AssetsReturn_kh_data } from '../../../api/public';


const assetsReturn = {
    state: {
        assetsReturn_tabledata: [],//已选中审批人
        assetsReturn_constdata: [],//已选中审批人
        assetsReturn_copydata: [],//已选中抄送人
        assetsReturn_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_assetsReturn_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.assetsReturn_constdata.push(data);
            }else{
                state.assetsReturn_constdata = data;
            }
        },
        SET_assetsReturn_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.assetsReturn_copydata).push(data);
            }else{
                state.assetsReturn_copydata = data;
            }
        },
        SET_assetsReturn_addApplyID: (state, data) => {
            state.assetsReturn_addApplyID = data;
        },
    },
    actions: {
        //添加审批人
        Add_assetsReturn_constsubmit:({ commit },data) =>{
            commit('SET_assetsReturn_constdata',data.consttext);
        },
        Delete_assetsReturn_ConstPerson:({ commit },data) =>{
            commit('SET_assetsReturn_constdata',data);
        },

        //添加抄送人
        Add_assetsReturn_copysubmit:({ commit },data) =>{
            commit('SET_assetsReturn_copydata',data.consttext);
        },
        Delete_assetsReturn_CopyPerson:({ commit },data) =>{
            commit('SET_assetsReturn_copydata',data);
        },

        //获取addApplyID
        AssetsReturn_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_assetsReturn_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //获取可还列表数据
        AssetsReturn_kh_list:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AssetsReturn_kh_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        AssetsReturn_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                AssetsReturn_Create(data).then(response => {
                    commit('SET_assetsReturn_constdata',[]);
                    commit('SET_assetsReturn_copydata',[]);
                    commit('SET_assetsReturn_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  assetsReturn;


