import { Inventory_list,add_asset,Asset_child_list,Scrap_lose,Inventory_details_list,Asset_trajectory,add_sn} from '../../../api/administration';


const InventoryList = {
    state: {
        Inventory_table:[],//请购列表数据
        Asset_child_table:[],//库存列表明细数据
        assetTrajectory_table:[],//物品使用轨迹
        Asset_table_pid:0,
    },
    mutations: {
        //请购列表数据
        SET_Inventory_table: (state, data) => {
            state.Inventory_table = data;
        },
        //库存列表明细数据
        SET_Asset_table: (state, data) => {
            state.Asset_child_table = data;
        },
        //物品使用轨迹
        SET_Trajectory_table: (state, data) => {
            state.assetTrajectory_table = data;
        },
        //库存列表明细pid
        SET_Asset_table_pid:(state, data) => {
            state.Asset_table_pid = data;
        },
    },
    actions: {
        //获取请购列表
        InventoryList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Inventory_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Inventory_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //新增库存
        AddAsset_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                add_asset(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //库存列表明细
        Asset_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Asset_child_list(data).then(response => {
                    let data = response.data.data;
                    //console.log(data.list);
                    commit('SET_Asset_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //库存列表明细pid
        Asset_table_pid:({ commit },data) =>{
            commit('SET_Asset_table_pid',data);
        },
        //库存报废、丢失
        ScrapLose_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Scrap_lose(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //库存列表详情
        InventoryDetails_table:({ commit},data) =>{
            return new Promise((resolve, reject) => {
                Inventory_details_list(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //物品使用轨迹
        AssetTrajectory_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Asset_trajectory(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Trajectory_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //增加sn号
        addSn_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                add_sn(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

    }
};


export default InventoryList;


