import { AddApplyID } from '../../../api/public';
import { AssetsRecipients_Create,AssetsRecipients_kl_data } from '../../../api/assetsRecipients';

const assetsRecipients = {
    state: {
        assetsRecipients_constdata: [],//已选中审批人
        assetsRecipients_copydata: [],//已选中抄送人
        assetsRecipients_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_assetsRecipients_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.assetsRecipients_constdata.push(data);
            }else{
                state.assetsRecipients_constdata = data;
            }
        },
        SET_assetsRecipients_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.assetsRecipients_copydata).push(data);
            }else{
                state.assetsRecipients_copydata = data;
            }
        },
        SET_assetsRecipients_addApplyID: (state, data) => {
            state.assetsRecipients_addApplyID = data;
        },
    },
    actions: {
        //添加审批人
        Add_assetsRecipients_constsubmit:({ commit },data) =>{
            commit('SET_assetsRecipients_constdata',data.consttext);
        },
        Delete_assetsRecipients_ConstPerson:({ commit },data) =>{
            commit('SET_assetsRecipients_constdata',data);
        },

        //添加抄送人
        Add_assetsRecipients_copysubmit:({ commit },data) =>{
            commit('SET_assetsRecipients_copydata',data.consttext);
        },
        Delete_assetsRecipients_CopyPerson:({ commit },data) =>{
            commit('SET_assetsRecipients_copydata',data);
        },

        //获取addApplyID
        AssetsRecipients_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_assetsRecipients_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //可领取物资列表数据
        AssetsRecipients_kl_list:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AssetsRecipients_kl_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        AssetsRecipients_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                AssetsRecipients_Create(data).then(response => {
                    commit('SET_assetsRecipients_constdata',[]);
                    commit('SET_assetsRecipients_copydata',[]);
                    commit('SET_assetsRecipients_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  assetsRecipients;


