<template>
    <el-dialog title="确认付款单" :visible="issubmitshow" custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">付款单</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{curdate}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{ org_full_name }}</td>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{ name }}</td>
                </tr>
                <tr>
                    <td class="b_gray">对方名称</td>
                    <td class="t-l">{{ toname }}</td>
                    <td class="b_gray">对方银行卡</td>
                    <td class="t-l">{{ to_bankcard }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">对方开户行</td>
                    <td colspan="3" class="t-l">{{ bankname }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="100">付款类型</td>
                    <td class="t-l">{{ consttype }}</td>
                    <td class="b_gray" width="100">金额</td>
                    <td class="t-l">￥ {{ money }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="100">事由</td>
                    <td colspan="3" class="t-l">{{ des==''?'--':des }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="15%">审批人</td>
                    <td class="t-l" width="20%">{{constperson}}</td>
                    <td class="b_gray" width="15%">抄送人</td>
                    <td class="t-l" width="20%">{{copyperson==''?'--':copyperson}}</td>
                    <td class="b_gray" width="15%">财务确认</td>
                    <td class="t-l" width="15%">--</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible">取 消</el-button>
            <el-button @click="onsubmitform" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'forpayment_SubmitModal',
        props:['issubmitshow','toname','to_bankcard','bankname','consttype','money','des','loading'],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'org_full_name',
                'name',
                'curdate'
            ]),
            applyID(){
                return this.$store.state.forpayment.forpayment_addApplyID;
            },
            constperson(){
                let constperson = "";
                if (this.$store.state.forpayment.forpayment_constdata.length > 0) {
                    constperson = (this.$store.state.forpayment.forpayment_constdata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return constperson;
            },
            copyperson(){
                let copyperson = "";
                if (this.$store.state.forpayment.forpayment_copydata.length > 0) {
                    copyperson = (this.$store.state.forpayment.forpayment_copydata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return copyperson;
            }
        },
        created(){
            this.$store.dispatch('Forpayment_AddApplyID',{type:4});
        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid #E9E9E9;
                padding: 8px;
                line-height: 23px;
                font-size: 12px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {
            tr:last-child {
                td {
                    border-bottom: 1px solid #E9E9E9;
                }
            }
        }
    }
</style>


