import fetch from '../utils/fetch';

export function Repayment_Record(params) {
    return fetch({
        url: '/oa_v1/back/can-back',
        method: 'post',
        data: params
    });
}

export function xxx(params) {
    //return axios.post('http://www.cheoa.com/oa_v1/apply/add-bankcard', qs.stringify(params));
    return fetch({
        url: '/oa_v1/apply/add-bankcard',
        method: 'post',
        data: params
    });
}



