<template>
    <div class="container">
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">付款申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>付款类型：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="consttype" :rules="[{ required: true, message: '请选择付款类型'}]">
                        <el-select v-model="ruleForm.consttype" placeholder="请选择" style="width: 100%" >
                            <el-option :key="key" :label="item.label" :value="item.value+'/'+item.label" v-for="(item,key) in Income_Expenditure"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>金额：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="money" :rules="[{ required: true, message: '请输入金额'}]">
                        <el-input v-model="ruleForm.money" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>事由：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="des" :rules="[{ required: true, message: '请输入事由'}]">
                        <el-input
                                type="textarea"
                                :rows="3"
                                placeholder="请输入"
                                v-model="ruleForm.des">
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>对方名称：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="to_name" :rules="[{ required: true, message: '请输入对方名称'}]">
                        <el-input v-model="ruleForm.to_name" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>对方卡号：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="bank_card" :rules="[{ required: true, message: '请输入对方卡号'}]">
                        <el-input v-model="ruleForm.bank_card" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>开户行：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="bank_name" :rules="[{ required: true, message: '请输入开户行'}]">
                        <el-input v-model="ruleForm.bank_name" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="20">
                    <el-upload class="upload-demo" multiple name="files" :action="BaseUrl"
                               method="post" :on-success="handlefileChange" :on-remove="handlefileRemove">
                        <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                        <span class="el-upload__text f12">请拍照或扫描后上传合同等相应文件</span>
                    </el-upload>
                </el-col>
            </el-row>
            <h3 class="mt-lg mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-15">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata"
                                       @ToggleConstDialog="ToggleConstDialog"></const-copy-person>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-15">
                    抄送人：
                </el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata"
                                       @ToggleCopyDialog="ToggleCopyDialog"></const-copy-person>
                </el-col>
            </el-row>
            <el-col class="ml-92 mb-lg">
                <el-button @click="OnCurrentSubmit('ruleForm')" type="primary">确 定</el-button>
                <el-button @click="isVisible('ruleForm')">取 消</el-button>
            </el-col>
        </el-form>
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :toname="to_name"
                :to_bankcard="bank_card_id"
                :bankname="bank_name"
                :consttype="consttypename"
                :money="money"
                :des="des"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></SubmitModal>
    </div>
</template>

<script>
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/forpayment/submitModal.vue';
    import { mapGetters } from 'vuex';
    export default {
        name:'forpaymemt',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    consttype:'',
                    money:'',
                    des:'',
                    to_name:'',
                    bank_card:'',
                    bank_name: '',
                },
                consttype:'',
                consttypename:'',
                money:'',
                des:'',
                to_name:'',
                bank_card_id:'',
                bank_name: '',
                fileList: [],
                loading:false,
                dialogImageUrl:'',
                dialogVisible:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false
            }
        },
        computed:{
            ...mapGetters([
                'Income_Expenditure',
                'BaseUrl'
            ]),
            constType(){
                return this.$store.state.global.constType;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.forpayment.forpayment_constdata;
            },
            copydata(){
                return this.$store.state.forpayment.forpayment_copydata;
            },
            applyID(){
                return this.$store.state.forpayment.forpayment_addApplyID;
            }
        },
        created(){
            this.$store.dispatch('Income_Expenditure_type',{id:2});
        },
        methods: {
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },

            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_forpayment_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_forpayment_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },

            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_forpayment_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_forpayment_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            //核对信息提交按钮
            OnCurrentSubmit(formName){
                let constdata = this.$store.state.forpayment.forpayment_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if(constdata.length > 0){
                            this.consttype = this.ruleForm.consttype.split("/")[0];
                            this.consttypename = this.ruleForm.consttype.split("/")[1];
                            this.money = this.ruleForm.money;
                            this.des = this.ruleForm.des;
                            this.to_name = this.ruleForm.to_name;
                            this.bank_card_id = this.ruleForm.bank_card;
                            this.bank_name = this.ruleForm.bank_name;
                            this.issubmitshow = true;
                        }else{
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            },
            Onsubmit(data){
                let constdata = this.$store.state.forpayment.forpayment_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.forpayment.forpayment_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }
                let fujian=[];
                if(this.fileList != null){
                    fujian = (this.fileList).map(data => data.response.data);
                }
                data = {
                    apply_id: this.applyID,
                    approval_persons: resultconstdata,
                    copy_person: resulcopydata,
                    pay_type: parseInt(this.consttype),
                    money: this.money,
                    to_name: this.to_name,
                    bank_card_id: this.bank_card_id,
                    bank_name: this.bank_name,
                    des: this.des,
                    files: fujian,
                };
                this.$store.dispatch('Forpayment_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:4}});
                }).catch(err => {
                    this.loading = false;
                });;
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


