import {
    constPerson,
    Card,
    AddCard,
    constType,
    Organizational_Structure,
    Approve_update_data,
    Payment_confirm_data,
    Payment_confirm_update,
    Collection_confirm_data,
    Collection_confirm_update,
    Baoxiao_Form_success_data,
    Position_list,
    Income_Expenditure_type_data,
    Citys_init_data,

} from '../../../api/public';

function CurDate(){
    const date = new Date();
    const year = date.getFullYear()+'年';
    const month = date.getMonth()+1+'月';
    const day = date.getDate()+'日';
    const dateTime = year + month + day;
    return dateTime;
}

const Global = {
    state: {
        BaseUrl:'/oa_v1/upload/file',
        serverUrl:'',
        curDate:CurDate(),
        bankCard:[],//初始化银行卡信息
        constType:null,//报销类型
        cpPerson:null,//初始化审批人、抄送人
        organizationalStructure:[],//组织架构
        position_data:[],//职位数据
        Income_Expenditure_data:[],//收入支出类型
        assetclass_data:[],//资产类别
        citys_data:[],
    },
    mutations: {
        SET_bankCard: (state, data) => {
            state.bankCard = data;
        },
        SET_constType: (state, data) => {
            //console.log(data);
            state.constType = data;
        },
        SET_cpPerson : (state, data) => {
            state.cpPerson = data;
            //console.log(state.cpPerson);
        },
        SET_organizationalStructure: (state, data) => {
            state.organizationalStructure = data;
        },
        SET_position_list: (state, data) => {
            state.position_data = data;
        },
        SET_Income_Expenditure_data: (state, data) => {
            state.Income_Expenditure_data = data;
        },
        SET_assetclass_data: (state, data) => {
            state.assetclass_data = data;
        },
        SET_citys_data: (state, data) => {
            state.citys_data = data;
        },
    },
    actions:{
        //报销类型选项初始化
        ConstTypeInit: ({commit, state}) => {
            return new Promise((resolve, reject) => {
                constType().then(response => {
                    const data = response.data;
                    commit('SET_constType', data.data)
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //审批人、抄送人选项初始化
        CpPersonInit: ({commit, state}) => {
            return new Promise((resolve, reject) => {
                constPerson().then(response => {
                    const data = response.data;
                    commit('SET_cpPerson', data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加附件



        //银行卡选项初始化
        InitCard:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Card().then(response => {
                    let newdata = [] ;
                    let xdata = response.data.data;
                    xdata.map(data => newdata.push({value:data.bank_name+"/"+data.card_id+"/"+ data.bank_des,label:data.bank_name+""+data.card_id}));
                    commit('SET_bankCard',newdata);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加银行卡
        Add_cardsubmit:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddCard({card_id:data.carddata.card_id,
                    bank_name:data.carddata.bank_name}).then(response => {
                    /*const data = response.data;
                    commit('SET_bankCard',data.data);*/
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //组织架构
        OrganizationalStructure:({ commit }) =>{
            return new Promise((resolve, reject) => {
                Organizational_Structure().then(response => {
                    let data = response.data.data;
                    commit('SET_organizationalStructure',data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //收入支出类别
        Income_Expenditure_type:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Income_Expenditure_type_data(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Income_Expenditure_data',data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //报销成功页数据请求
        Baoxiao_Form_success:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Baoxiao_Form_success_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //审批数据请求
        Approve_update:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Approve_update_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //付款确认数据初始化
        Payment_confirm_init:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Payment_confirm_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //付款确认更新状态
        Payment_confirm_form:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Payment_confirm_update(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //收款确认数据初始化
        Collection_confirm_init:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Collection_confirm_data(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //收款确认更新状态
        Collection_confirm_form:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Collection_confirm_update(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //职位列表
        Position_List:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Position_list(data).then(response => {
                    commit('SET_position_list',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //资产类别
        Assetclass_List:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Assetclass_list_data(data).then(response => {
                    commit('SET_assetclass_data',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //用章申请
        Chapter_add:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Chapter_Create(data).then(response => {
                    commit('SET_assetclass_data',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //城市数据初始化
        Citys_init:({ commit },data) => {
            return new Promise((resolve, reject) => {
                Citys_init_data(data).then(response => {
                    commit('SET_citys_data',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

    }
}
export default Global


