<template>
    <el-menu class="header" mode="horizontal" v-if="isnav == 1">
        <Hamburger class="hamburger-container" :toggleClick="toggleSideBar" :isActive="Boolean(sidebar.opened)"></Hamburger>
        <el-dropdown class="avatar-container" trigger="click">
            <div class="avatar-wrapper">
                <img class="user-avatar" src="../../assets/status_avatar.png" />{{name}}
                <i class="el-icon-caret-bottom"/>
            </div>
            <el-dropdown-menu class="user-dropdown" slot="dropdown">
                <el-dropdown-item><span @click="logout" style="display:block;">退出登录</span></el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </el-menu>
</template>
<script>
    import { mapGetters } from 'vuex';
    import Hamburger from '../../components/Hamburger/index.vue';
    /*import axios from 'axios';*/

    export default {
        name: 'Headbar',
        components: {
            Hamburger
        },
        data() {
            return {
                bodywidth:'',
                title:'',
                isClick:false,
                isChildClick:false,
            }
        },
        computed: {
            ...mapGetters([
                'sidebar',
                'name',
                'isnav'
            ]),
        },
        created(){
           /* const response =  axios.get('http://doc.checheng.net/docs/show/205').then(function(res){
                    console.log(res);
                })
                .catch(function(err){
                    console.log(err);
                });*/
        },
        methods: {
            toggleSideBar() {
                this.$store.dispatch('ToggleSideBar',document.body.clientWidth);
            },
            togglePagenation(){
                this.$store.dispatch('Setpagenation',document.body.clientWidth);
            },
            logout() {
                this.$store.dispatch('LoginOut').then(response => {
                    location.href = response.data.data.login_url;
                });
            },
            checkurl(){
                if(this.$route.name == '首页'){
                    this.homelink();
                }else{
                    this.childlink();
                }
            },
            homelink(){
                window.JavaJs.back();
            },
            childlink(){
                this.$router.push({path:'/adminhome',query:{isnav:0,pagenation:1}});
            }
        },
        created () {
            this.togglePagenation();
            window.onresize = ()=>{
                this.togglePagenation();
            }
            this.title = this.$route.name == '首页' ? '车城OA系统':this.$route.name;
        },
        watch: {
            '$route' (to, from) {
                this.title = to.name == '首页' ? '车城OA系统':to.name;
            }
        }
    }

</script>
<style rel="stylesheet/less" lang="less" scoped>
    .header {
        height: 50px;
        line-height: 50px;
        border-radius: 0px !important;
        background-color: #32aafa;
        .hamburger-container {
            line-height: 58px;
            height: 50px;
            float: left;
            padding: 0 10px;
        }
        .avatar-container {
            height: 50px;
            display: inline-block;
            position: absolute;
            right: 0;
            .avatar-wrapper {
                cursor: pointer;
                position: relative;
                height: 50px;
                line-height: 50px;
                padding: 0 10px;
                color: #fff;
                .user-avatar {
                    float: left;
                    width: 30px;
                    height: 30px;
                    margin-right: 7px;
                    margin-top:10px;
                    border-radius: 10px;
                }
                .el-icon-caret-bottom {
                    margin-left: 5px;
                }
            }
        }
    }
</style>



