<template>
	<div>
		<el-row :gutter="10">
			<el-col :xs="24">
				<el-button  type="primary" @click="showForm">新增</el-button>
			</el-col>
		</el-row>

		<el-dialog title="新增招聘信息" size="tiny" :visible="formVisible" custom-class="newresume-dialog" :before-close="hideForm" :close-on-click-modal="hide" :close-on-press-escape="hide">
			<el-form :model="ruleForm" label-width="100px" :rules="ruleForm" ref="ruleForm">
				<el-form-item label="姓名：" prop="name" :rules="[{ required: true, message: '请输入姓名'}]">
	                <el-col :span="16">
						<el-input v-model="ruleForm.name"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="手机号码：" prop="phone" :rules="[{ required: true, message: '请输入手机号码'}]">
	                <el-col :span="16">
						<el-input v-model="ruleForm.phone"></el-input>
					</el-col>
				</el-form-item>
	            <el-form-item label="应聘职位：" prop="job" :rules="[{ required: true, message: '请选择应聘职位'}]">
	                <el-col :span="16">
	                    <el-select v-model="ruleForm.job" placeholder="请选择">
	                    	<el-option v-for="item in profession" :key="item.index" :label="item.label" :value="item.value"></el-option>
	                    </el-select>
	                </el-col>
	            </el-form-item>
	            <el-form-item label="性别：" prop="sex" :rules="[{ required: true, message: '请选择性别'}]">
	                <el-col :span="16">
	                    <el-select v-model="ruleForm.sex" placeholder="请选择">
	                        <el-option key="0" label="男" value="2" ></el-option>
	                        <el-option key="1" label="女" value="1"></el-option>
	                    </el-select>
	                </el-col>
	            </el-form-item>
				<el-form-item label="年龄：" prop="age" :rules="[{ required: true, message: '请输入年龄'}]">
	                <el-col :span="16">
						<el-input v-model="ruleForm.age"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="学历：" prop="educational" :rules="[{ required: true, message: '请选择学历'}]">
	                <el-col :span="16">
	                    <el-select v-model="ruleForm.educational" placeholder="请选择">
	                    	<el-option v-for="item in edu_list" :key="item.index" :label="item.label" :value="item.value"></el-option>
	                    </el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="工作年限：" prop="work_time" :rules="[{ required: true, message: '请输入工作年限'}]">
	                <el-col :span="16">
						<el-input v-model="ruleForm.work_time"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="所在地：" prop="district" :rules="[{ required: true, message: '请输入所在地'}]">
	                <el-col :span="16">
						<el-cascader v-model="ruleForm.district" placeholder="请选择" :options="cities"></el-cascader>
					</el-col>
				</el-form-item>
			</el-form>
	        <div slot="footer" class="dialog-footer">
	            <el-button @click="hideForm">取 消</el-button>
	            <el-button @click="onsubmitform('ruleForm')" type="primary" :loading="loading">确 定</el-button>
	        </div>
		</el-dialog>
	</div>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';

	export default {
		name: 'newResume',
		props:[ ],
	    data() {
	    	return {
	    		ruleForm: {
	    			name: '',
	    			phone: '',
	    			job: '',
	    			sex: '',
	    			age: '',
	    			educational: '',
	    			work_time: '',
	    			district: []
	    		},
	    		hide: false,
	    		loading: false,
	    		formVisible: false,
	    	};
	    },
	    computed: {
	    	cities(){
                return this.$store.state.global.citys_data;
            },
            profession(){
            	return this.$store.state.global.position_data;
            },
            edu_list(){
            	return this.$store.state.hire.edu_list;
            },
	    }, 
	    created(){
            this.$store.dispatch('Citys_init');
            this.$store.dispatch('Position_List');
            this.$store.dispatch('Edu_List');
        },
	    methods: {
	    	//显示表单
	    	showForm(){
	    		this.formVisible = true;
	    	},

	    	//关闭对话框
	    	hideForm(){
	    		this.formVisible = false;
	    	},

	    	//提交表单
	    	onsubmitform(formName){
	    		this.loading = true;
	    		this.$refs[formName].validate((valid) => {
                    if (valid) {
			    		let data = {
			    				name: this.ruleForm.name,
			    				phone: this.ruleForm.phone,
			    				job: this.ruleForm.job,
			    				sex: this.ruleForm.sex,
			    				age: this.ruleForm.age,
			    				educational: this.ruleForm.educational,
			    				work_time: this.ruleForm.work_time,
			    				current_location: this.ruleForm.district[this.ruleForm.district.length - 1]
			    			}; 

			    		this.$store.dispatch('Resume_Add',data).then( response => {
		                    this.loading = false;
		                    let data = response.data;
		                    if (data.code == 200) {
		                    	this.$message.success(data.data);
			                    this.$emit('refreshResumes');
			                    this.$refs[formName].resetFields();
			                    this.hideForm();
		                    } else {
		                    	this.$message.error(data.data);	
		                    }
		                }).catch(err => {
		                    this.loading = false;
		                });
                    }
                });


	    	}

	    }
	};
</script>



