<template>
    <el-dialog title="确认费用报销单" :visible="issubmitshow" custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">费用报销单</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{curdate}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{applyID}}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{org_full_name}}</td>
                    <td class="b_gray">报销人</td>
                    <td class="t-l">{{name}}</td>
                </tr>
                <tr>
                    <td class="b_gray">开户行名称</td>
                    <td class="t-l">{{ bankdata[0] }}</td>
                    <td class="b_gray">银行卡号</td>
                    <td class="t-l">{{ bankdata[1] }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="34%">类别</td>
                    <td class="b_gray" width="33%">金额</td>
                    <td class="b_gray" width="33%">明细</td>
                </tr>
                <tr v-for="item in this.$store.state.reimburse.reimburse_tabledata">
                    <td>{{item.type_name}}</td>
                    <td class="t-l">￥ {{(item.money*1).toFixed(2)}}</td>
                    <td class="t-l">{{ item.des==''?'--':item.des }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                    <tr>
                        <td class="t-c" rowspan="2" width="17%">金额合计</td>
                        <td width="17.2%">小写</td>
                        <td class="t-l" width="33%">￥ {{total}}</td>
                        <td>&nbsp;</td>
                    </tr>
                    <tr>
                        <td>大写</td>
                        <td class="t-l">{{dxtotal}}</td>
                        <td>&nbsp;</td>
                    </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="15%">审批人</td>
                    <td class="t-l" width="20%">{{constperson}}</td>
                    <td class="b_gray" width="15%">抄送人</td>
                    <td class="t-l" width="20%">{{ copyperson==''?'--':copyperson}}</td>
                    <td class="b_gray" width="15%">财务确认</td>
                    <td class="t-l" width="15%">--</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible">取 消</el-button>
            <el-button @click="onsubmitform" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import { digitUppercase } from '../common';

    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'SubmitModal',
        props:['issubmitshow','bank','files','loading'],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'org_full_name',
                'name',
                'curdate'
            ]),
            ...mapState({
                bankdata(){
                    let b='';
                    if(this.bank != null){
                        return b=(this.bank).split("/");
                    }else{
                        return b;
                    }
                },
                filesdata(){
                    return this.files;
                },
                applyID(){
                    //console.log(this.$store.state.reimburse.reimburse_addApplyID);
                    return this.$store.state.reimburse.reimburse_addApplyID;
                },
                total(){
                    let total = 0;
                    (this.$store.state.reimburse.reimburse_tabledata).map(data => total = total + data.money*1 );
                    return total.toFixed(2);
                },
                dxtotal(){
                    let total = 0;
                    (this.$store.state.reimburse.reimburse_tabledata).map(data => total = total + data.money*1 );
                    return digitUppercase(total);
                },
                constperson(){
                    let constperson = "";
                    if(this.$store.state.reimburse.reimburse_constdata.length > 0){
                         constperson = (this.$store.state.reimburse.reimburse_constdata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                    }
                    return constperson;
                },
                copyperson(){
                    let copyperson = "",resultdata="";
                    if(this.$store.state.reimburse.reimburse_copydata.length > 0){
                         copyperson = (this.$store.state.reimburse.reimburse_copydata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                    }
                    return copyperson;
                }
            })
        },
        created(){
            this.$store.dispatch('Reimburse_AddApplyID',{type:1});
        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid #E9E9E9;
                padding: 8px;
                line-height: 23px;
                font-size: 12px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {
            tr:last-child {
                td {
                    border-bottom: 1px solid #E9E9E9;
                }
            }
        }
    }
</style>


