import fetch from '../utils/fetch';
/*import axios from 'axios';*/
import qs from 'qs';

//资产类别详情
export function AssetClass_list(params) {
    return fetch({
        url: '/oa_v1/basic/get-type-info',
        method: 'get',
        params:params
    });
}
//资产类别添加
export function AssetClass_add(params) {
    return fetch({
        url: '/oa_v1/basic/add-type',
        method: 'post',
        data:qs.stringify(params)
    });
}
//资产类别修改
export function AssetClass_update(params) {
    return fetch({
        url: '/oa_v1/basic/update-type',
        method: 'post',
        data:qs.stringify(params)
    })
}
// 资产类别列表
export function AssetClass_map(params) {
    return fetch({
        url: '/oa_v1/basic/get-type-list',
        method: 'get',
        params:params
    })
}
// 添加资产子类别
export function AssetAdd_child(params) {
    return fetch({
        url: '/oa_v1/basic/add-child',
        method: 'post',
        data:qs.stringify(params)
    })
}


