<template>
    <el-dialog title="转正申请单" :visible="issubmitshow" custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">调职申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{curdate}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{name}}</td>
                    <td class="b_gray">入职日期</td>
                    <td class="t-l">{{ entry_time }}</td>
                </tr>
                <tr>
                    <td class="b_gray">原部门</td>
                    <td class="t-l">{{ old_org }}</td>
                    <td class="b_gray">调职后部门</td>
                    <td class="t-l">{{ target_org }}</td>
                </tr>
                <tr>
                    <td class="b_gray">原职位</td>
                    <td class="t-l">{{ old_profession.split('/')[1] }}</td>
                    <td class="b_gray">调职后职位</td>
                    <td class="t-l">{{ target_profession.split('/')[1] }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                    <tr>
                        <td class="b_gray" width="100">调职日期</td>
                        <td colspan="3" class="t-l">{{ transfer_time }}</td>
                    </tr>
                    <tr>
                        <td class="b_gray" width="100">调职原因</td>
                        <td colspan="3" class="t-l">{{ des==''?'--':des }}</td>
                    </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">审批人</td>
                    <td class="t-l">{{constperson}}</td>
                    <td class="b_gray" width="100">抄送人</td>
                    <td class="t-l">{{copyperson==''?'--':copyperson}}</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible">取 消</el-button>
            <el-button @click="onsubmitform" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'SubmitModal',
        props:['issubmitshow','entry_time','old_org','target_org','old_profession','target_profession','transfer_time','des','loading'],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'name',
                'curdate'
            ]),
            applyID(){
                return this.$store.state.approveTransfer.transfer_addApplyID;
            },
            constperson(){
                let constperson = "";
                if (this.$store.state.approveTransfer.transfer_constdata.length > 0) {
                    constperson = (this.$store.state.approveTransfer.transfer_constdata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return constperson;
            },
            copyperson(){
                let copyperson = "";
                if (this.$store.state.approveTransfer.transfer_copydata.length > 0) {
                    copyperson = (this.$store.state.approveTransfer.transfer_copydata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return copyperson;
            }
        },
        created(){
            this.$store.dispatch('Transfer_AddApplyID',{type:12});
        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid #E9E9E9;
                padding: 8px;
                line-height: 23px;
                font-size: 12px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {
            tr:last-child {
                td {
                    border-bottom: 1px solid #E9E9E9;
                }
            }
        }
    }
</style>


