import { Person_add,Person_update,Person_list,Person_info } from '../../../api/basic';


const PersonnelClass = {
    state: {
        Personclass_table:[],//表数据

    },
    mutations: {
        SET_person_table: (state, data) => {
            state.Personclass_table = data;
        },
    },
    actions: {
        //获取人才列表
        PersonList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Person_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_person_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //添加人才类型
        PersonAdd_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Person_add(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //编辑人才类型
        PersonUpdate_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Person_update(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
};


export default PersonnelClass;


