<template>
    <el-dialog class="constom-md-dialog" title="增加请购明细" :visible.sync="istableshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm">
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>类别：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="asset_type" :rules="[{ required: true, message: '请选择类别'}]">
                        <el-cascader :options="qgtypedata" v-model="ruleForm.asset_type" style="width: 100%"></el-cascader>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>品牌：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="brand" :rules="[{ required: true, message: '品牌'}]">
                        <el-select v-model="ruleForm.brand" placeholder="请选择" style="width: 100%">
                            <el-option :key="key" :label="item.name" :value="item.id+'/'+item.name" v-for="(item,key) in brand"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>名称：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="name" :rules="[{ required: true, message: '请输入名称'}]">
                        <el-input v-model="ruleForm.name" placeholder="请输入" value="111" style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>单价：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="price" :rules="[{ required: true, message: '请输入单价'}]">
                        <el-input v-model="ruleForm.price" placeholder="请输入" style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>数量：
                </el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="amount" :rules="[{ required: true, message: '请输入数量'}]">
                        <el-input-number v-model="ruleForm.amount" :step="1" :min="1" style="width: 100%"></el-input-number>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button @click="onsubmitform('ruleForm')" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import { mapGetters  } from 'vuex'
    let name ='';
    export default {
        name:'TableModal',
        props:['istableshow','','qgtype',''],
        data(){
            return {
                ruleForm: {
                    asset_type:[],
                    brand:'',
                    name:'',
                    price:'',
                    amount:'',
                },
                hide:false,
                loading:false
            }
        },
        computed: {
            ...mapGetters([
                'assetclass_data'
            ]),
            qgtypedata (){
                return this.$store.state.purchase.purchase_typedata;
            },
            brand(){
                return this.$store.state.purchase.purchase_branddata;
            }
        },
        created(){
            this.$store.dispatch('Purchase_type');
            this.$store.dispatch('Brand_type');
        },
        methods: {
            isVisible(formName){
                this.$emit('ToggleTableDialog',{istableshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            checkmoney(rule, value, callback){
                var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
                if (!value) {
                    return callback(new Error('单价不能为空'));
                }else{
                    if (!reg.test(value)) {
                        callback(new Error('单价格式错误'));
                    } else {
                        callback();
                    }
                };
            },
            //递归遍历数组
            eachArray(obj,b){
                for(var i = 0; i < obj.length;i++){
                    if(obj[i].value == b){
                        name = obj[i].label;
                        break;
                    }else{
                        if(obj[i].children){
                            this.eachArray(obj[i].children,b);
                        }
                    }
                }
                return name;
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let asset_typename='', asset_typeid;
                        for(var j = 0; j < this.ruleForm.asset_type.length; j++){
                            asset_typeid = this.ruleForm.asset_type[j]
                            if(j == this.ruleForm.asset_type.length-1){
                                asset_typename += this.eachArray(this.qgtypedata,this.ruleForm.asset_type[j]);
                            }else{
                                asset_typename += this.eachArray(this.qgtypedata,this.ruleForm.asset_type[j]) +'-';
                            }
                        }

                        let data = {
                            asset_type_id:asset_typeid,
                            asset_typename:asset_typename,
                            asset_brand_id:this.ruleForm.brand.split("/")[0],
                            asset_brandname:this.ruleForm.brand.split("/")[1],
                            name:this.ruleForm.name,
                            price:(this.ruleForm.price*1).toFixed(2),
                            amount:this.ruleForm.amount,
                            total:(this.ruleForm.price*this.ruleForm.amount).toFixed(2),
                        };

                        this.loading = true;
                        this.$emit('ontableadd',data);
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleTableDialog',{istableshow:false});
                        },300);
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


