import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import router from './router';
import App from './App.vue'
import store from './store'
import NProgress from 'nprogress'; // Progress 进度条
import 'nprogress/nprogress.css'

Vue.use(ElementUI);

// permissiom judge
function hasPermission(roles, permissionRoles) {
    //if (roles.indexOf('126') >= 0) return true; // admin权限 直接通过
    if (!permissionRoles) return true;
    return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

router.beforeEach((to, from, next) => {
    NProgress.start(); // 开启Progress
    if (store.getters.roles.length === 0) { // 判断当前用户是否已拉取完user_info信息
        store.dispatch('GetInfo').then(res => { // 拉取user_info
            if(res.data.code == 200){
                let roles = [res.data.data.roleName];
                store.dispatch('GenerateRoutes', {roles}).then((response) => { // 生成可访问的路由表
                    router.addRoutes(store.getters.addRouters); // 动态添加可访问路由表
                    next({path: to.path , query: to.query }); // hack方法 确保addRoutes已完成
                })
            }else if(res.data.code == 401){
                location.href=res.data.data.login_url;
            }
        }).catch(err => {
            console.log(err);
        });
    }
    else {
        //next();
        // 没有动态改变权限的需求可直接next() 删除下方权限判断 ↓
        if (hasPermission(store.getters.roles, to.meta.role)) {
            next();
        } else {
            next({path: '/loginout', query: {noGoBack: true}});
        }
        // 可删 ↑
    }
});


router.afterEach(() => {
    NProgress.done(); // 结束Progress
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');



