import { AddApplyID } from '../../../api/public';
import { Positive_Create } from '../../../api/hire';
/*import Cookies from 'js-cookie';*/

const positive = {
    state: {
        positive_constdata: [],//已选中审批人
        positive_copydata: [],//已选中抄送人
        positive_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_positive_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.positive_constdata.push(data);

                //console.log(state.constdata);
            }else{
                //console.log(constdata);
                state.positive_constdata = data;
            }
        },
        SET_positive_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.positive_copydata).push(data);
                //console.log(state.copydata);
            }else{
                //console.log(copydata);
                state.positive_copydata = data;
            }
        },
        SET_positive_addApplyID: (state, data) => {
            state.positive_addApplyID = data;
        }
    },
    actions: {
        //添加审批人
        Add_positive_constsubmit:({ commit },data) =>{
            commit('SET_positive_constdata',data.consttext);
        },
        Delete_positive_ConstPerson:({ commit },data) =>{
            commit('SET_positive_constdata',data);
        },

        //添加抄送人
        Add_positive_copysubmit:({ commit },data) =>{
            commit('SET_positive_copydata',data.consttext);
        },
        Delete_positive_CopyPerson:({ commit },data) =>{
            commit('SET_positive_copydata',data);
        },

        //获取addApplyID
        Positive_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    //debugger
                    commit('SET_positive_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        Positive_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                Positive_Create(data).then(response => {
                    commit('SET_positive_constdata',[]);
                    commit('SET_positive_copydata',[]);
                    commit('SET_positive_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  positive;


