<template>
    <el-dialog title="固定资产" size="large" :visible.sync="istableshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">

            <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm mt-0">
                <el-row class="search-wrap mb-lg">
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="keywords">
                                    <el-input v-model="ruleForm.keywords" placeholder="请输入名称/库存编号/使用人"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="applyDate">
                                    <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="24" :md="8" class="t-r">
                        <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isClear('ruleForm')">清除</el-button>
                    </el-col>
                </el-row>
            </el-form>

            <el-row >
                <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                    <el-table-column prop="index" label="序号" ></el-table-column>
                    <el-table-column prop="created_at" label="入库时间" ></el-table-column>
                    <el-table-column prop="stock_number" label="库存编号" ></el-table-column>
                    <el-table-column prop="asset_number" label="资产编号" ></el-table-column>
                    <el-table-column prop="price" label="采购价格" ></el-table-column>
                    <el-table-column prop="status" label="状态" ></el-table-column>
                    <el-table-column prop="use_person" label="使用人" ></el-table-column>
                    <el-table-column prop="org" label="部门" ></el-table-column>
                    <el-table-column prop="use_day" label="已使用" ></el-table-column>
                    <el-table-column prop="operation" label="操作" width="160">
                        <template scope="scope" >
                            <el-button  @click="To_link(scope.row)"type="text" size="small" v-if="scope.row.status=='未使用'" >详情</el-button>
                            <el-button  @click="showPopup('Scrap',scope.row)" type="text" size="small" v-if="scope.row.status=='未使用'">报废</el-button>
                            <el-button  @click="showPopup('Lose',scope.row)" type="text" size="small"  v-if="scope.row.status=='未使用'">丢失</el-button>
                            <el-button  @click="To_link(scope.row)" type="text" size="small" v-else>详情</el-button>
                        </template>

                    </el-table-column>
                </el-table>
            </el-row>
            <el-row class="t-r mt-lg">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next, jumper"
                        :total="total">
                </el-pagination>
            </el-row>
        <!-- :page-sizes="[10, 20, 30, 40]"-->
        <!--  新增弹窗-->
        <el-row >
            <transition name="fade">
                <el-col :span="24" class="Popup_background" v-show="Popup" transition="fade">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title" v-if="parameter=='edit'">确认已请购</div>
                            <div class="el-message-box__title" v-if="parameter=='Scrap'">报废</div>
                            <div class="el-message-box__title" v-if="parameter=='Lose'">丢失</div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn">
                                <i class="el-message-box__close el-icon-close" @click="hidePopup()"></i>
                            </button>
                        </div>
                        <div class="el-message-box__content">
                            <el-form :model="PopupForm" :rules="rules" ref="PopupForm"
                                     label-width="100px" class="demo-ruleForm">

                                <el-form-item label="报废说明:" prop="names" v-if="parameter=='Scrap'">
                                    <el-input v-model="PopupForm.names" type="textarea"
                                              :rows="3" placeholder="请输入备注"></el-input>
                                </el-form-item>
                                <el-form-item label="丢失说明:" prop="names" v-if="parameter=='Lose'">
                                    <el-input v-model="PopupForm.names" type="textarea"
                                              :rows="3" placeholder="请输入备注"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="hidePopup()">
                                <span>取消</span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary "
                                    @click="submitForm('PopupForm')">
                                <span>确定</span>
                            </button>
                        </div>
                    </div>

                </el-col>
            </transition>
        </el-row>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')" type="primary">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import { mapGetters  } from 'vuex'

    export default {
        name:'assetList',
        props:{
            istableshow: {
                type: Boolean,
            },
        },
        data(){
            return {
                hide:false,
                loading:false,
                ruleForm:{
                    keywords:'',
                    applyDate:'',
                    status:''
                },
                PopupForm:{
                    names:''
                },
                pageSize:10,
                currentPage:1,
                total:1,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                status:'',
                type:1,
                Popup:false,
                parameter:'',
                id:0,
                item:'',
                radio3: '请购全部',
                asset_id:0,
                asset_list_id:0,
                rules: {
                    names:[
                        {required: true, message: '输入品牌类别', trigger: 'blur'}
                    ]
                },
                options: [{
                    value: '未使用',
                    label: '未使用'
                }, {
                    value: '使用中',
                    label: '使用中'
                }, {
                    value: '已报废',
                    label: '已报废'
                }, {
                    value: '已丢失',
                    label: '已丢失'
                },]
            }
        },
        created(){
            this.asset_id = this.$store.state.InventoryList.Asset_table_pid;
            let data = {
                page:1,
                page_size:10,
                asset_id:this.asset_id
            }
            this.$store.dispatch('Asset_table',data).then(response=>{
                if(response.data.code==200){
                    this.currentPage = response.data.data.pages.currentPage;
                    this.total = response.data.data.pages.totalCount;
                }
            });
        },
        computed:{
            tableData(){
                return this.$store.state.InventoryList.Asset_child_table;
            }
        },
        methods:{
            showPopup(parameter,obj) {

                this.parameter=parameter;
                if(parameter == 'Scrap'){
                    this.Popup = true;
                    this.PopupForm.names='';
                    this.asset_list_id=obj.id;
                }
               else if (parameter == 'Lose'){
                    this.Popup = true;
                    this.PopupForm.names='';
                    this.asset_list_id=obj.id;

                }
                else {
                    return false;
                }
            },
            hidePopup() {
                this.Popup = false;
                if(this.parameter == 'Lose'){
                    this.PopupForm.names='';
                    return false;
                }
                else if(this.parameter == 'Scrap'){
                    this.PopupForm.names='';
                }
                else {
                    return false;
                }
            },
            isVisible(){
                this.$emit('ToggleTableDialog',{istableshow:false});
            },
            //弹窗
            submitForm(popupForm) {
                let para = this.parameter;
                //报废
                if( para=='Scrap'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let des=this.PopupForm.names;
                            this.$store.dispatch('ScrapLose_table',{des:des,status:3,asset_list_id:this.asset_list_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //丢失
                else if (para=='Lose')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let des=this.PopupForm.names;
                            this.$store.dispatch('ScrapLose_table',{des:des,status:4,asset_list_id:this.asset_list_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else
                {
                    return false;
                }
            },
            searchData(value){
                if(value != null){
                    this.currentPage = value;
                }
                return {
                    page:this.currentPage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time,
                    status:this.status,
                    asset_id:this.asset_id
                };
            },
            ondispatch(){
                this.$store.dispatch('Asset_table',this.searchData()).then(response=>{
                    if(response.data.code == 200){
                        this.currentPage = Number(response.data.data.pages.currentPage);
                        this.childTotalCount = Number(response.data.data.pages.totalCount);
                    }
                });
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        this.status = this.ruleForm.status;
                        console.log(this.status);
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            To_link(row) {
                console.log(row);
                this.$router.push({ path: '/Administration/inventoryDetails' ,query:{apply_id:row.apply_id,type:row.type}});
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family: Helvetica, sans-serif;
        .Popup_background{
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            padding-top: 16%;
            z-index: 10000;
            width:100%;
            height: 100%;
            text-align: center;
            overflow: auto;
            background: rgba(7,17,27,0.8);
        }
    }
</style>



