<template>
    <div class="container">
        <h3 class="mb-md">请购列表</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm mb-lg">
            <el-row class="search-wrap">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10" class="mb-0">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder="请输入编号"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10" class="mb-0">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isClear('ruleForm')">清除</el-button>
                </el-col>
            </el-row>
        </el-form>
        <el-row >
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                <el-table-column type="index" prop="id" label="序号" ></el-table-column>
                <el-table-column prop="create_time" label="申请时间" width="150" ></el-table-column>
                <el-table-column prop="apply_id" label="审批单编号" width="190" ></el-table-column>
                <el-table-column prop="person" label="申请人" width="80" ></el-table-column>
                <el-table-column prop="org" label="部门" ></el-table-column>
                <el-table-column prop="detail" label="金额" width="150" >
                    <template scope="scope">
                        ￥{{ scope.row.money }}
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="状态" width="100"></el-table-column>
                <el-table-column prop="operation" label="操作" width="150">
                    <template scope="scope">
                        <el-button @click="Tolink(scope.$index,scope.row)" type="text" size="small">详情</el-button>
                        <el-button  @click="showPopup('edit',scope.row)" type="text" size="small">确认入库</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>

        <!--  新增弹窗-->
        <el-row >
            <transition name="fade">
                <el-dialog title="入库" :visible="Popup" :before-close="hidedialog" :close-on-click-modal="hide" :close-on-press-escape="hide">
                    <div v-show="into">
                        <div class="enter-storage-steps">
                            <div class="enter-storage-step finish">
                                <b>1</b>
                                <span>提交入库</span>
                                <div class="line"></div>
                            </div>
                            <div class="enter-storage-step ">
                                <b>2</b>
                                <span>完成</span>
                            </div>
                        </div>
                        <div class="tips" :class="{'none':istips}"><i class="el-icon-warning"></i>请勾选需要入库的物品。<i class="el-icon-close" @click="closetips"></i></div>
                        <el-form :model="searchForm" ref="searchForm">
                            <!--<el-form-item class="t-r" prop="keywords">
                                 <el-input class="mr-sm" v-model="searchForm.keywords" size="small" placeholder="请输入关键词" value="" style="width: 250px"></el-input>
                                 <el-button @click="ondialogsearch" size="small">搜索</el-button>
                            </el-form-item>-->
                            <el-table :data="enterStorageListData" @sort-change="onsorter" @filter-change="onfilter" @selection-change="handleSelectionChange" border style="width: 100%">
                                <el-table-column type="selection" width="55"></el-table-column>
                                <el-table-column prop="asset_type_name" label="类别" ></el-table-column>
                                <el-table-column prop="name" label="名称" ></el-table-column>
                                <el-table-column prop="amount" label="请购数量" >
                                    <template scope="scope">
                                        {{scope.row.in_amount + '/' + scope.row.amount }}
                                    </template>
                                </el-table-column>
                                <el-table-column prop="in_amount" label="入库数量">
                                    <template scope="scope">
                                        <input class="el-input__inner instorge-input" v-if="scope.row.amount - scope.row.in_amount !=0" value="0" ref="input" @blur="inputchange(scope.$index,scope.row)"/>
                                        <span v-else>已完全入库</span>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-form>
                    </div>
                    <div v-show="success">
                        <div class="enter-storage-steps">
                            <div class="enter-storage-step finish">
                                <b>1</b>
                                <span>提交入库</span>
                                <div class="line"></div>
                            </div>
                            <div class="enter-storage-step finish">
                                <b>2</b>
                                <span>完成</span>
                            </div>
                        </div>
                        <div class="instorge-success  mt-lg t-c">
                            <i class="el-icon-circle-check"></i>
                            <div class="mt-lg">入库成功</div>
                        </div>
                    </div>
                    <div slot="footer" class="dialog-footer">
                        <el-button v-show="into" type="primary" @click="submitForm" :loading="loading">下一步</el-button>
                        <el-button v-show="success" type="primary" @click="hidedialog">确定</el-button>
                    </div>
                </el-dialog>
            </transition>
        </el-row>
    </div>
</template>

<script>
    export default {
        components: {},
        name:'ApplyList',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                searchForm:{
                    keywords:''
                },
                hide:false,
                Popup:false,
                istips:false,
                loading:false,
                into:false,
                success:false,
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                applyID:'',
                acount:'',
                multipleSelection:[],
                in_amount:[],

            };
        },
        created(){
            this.$store.dispatch('ApplyList_table',{page:1,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.pages.currentPage;
                this.total = data.pages.totalCount;
            });
        },
        computed:{
            tableData(){
                return this.$store.state.ApplyList.Apply_table;
            },
            enterStorageListData(){
                return this.$store.state.ApplyList.EnterStorageListData;
            }
        },
        methods:{
            isVisible(formName){
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
                this.Popup = false;
                this.into = true;
                this.success = false;
            },
            hidedialog(){
                this.$refs['searchForm'].resetFields();
                this.Popup = false;
                this.into = true;
                this.success = false;
            },
            closetips(){
                this.istips = true;
            },
            showPopup(index,row) {
                this.Popup = true;
                this.into = true;
                this.success = false;
                this.applyID = row.apply_id;
                this.$store.dispatch('Enter_Storage',{apply_id:this.applyID});
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            inputchange(index,row){
                if(this.$refs.input.value <= (row.amount - row.in_amount)){
                    this.in_amount[index] = {id:row.id,val:this.$refs.input.value};
                }else{
                    this.$message.error('输入的数量大于请购数量');
                }
            },
            //弹窗
            submitForm(popupForm) {
                let Selections = this.multipleSelection;
                let list = [];
                //console.log(this.multipleSelection.length);
                if(this.multipleSelection.length > 0){
                    if(this.in_amount.length>0){
                        for(let i=0;i<Selections.length;i++){
                            for(let j=0;j<this.in_amount.length;j++){
                                if(this.in_amount[j].id==Selections[i].id){
                                    if(this.in_amount[j].val <= Selections[i].amount-Selections[i].in_amount){
                                        list.push({apply_buy_id:Selections[i].id,amount:this.in_amount[j].val});
                                    }else{
                                        return;
                                    }
                                }
                            }
                        }
                        this.$store.dispatch('Enter_Storage_Update',{apply_id:this.applyID,list:list}).then(response=>{
                            if(response.data.code == 200){
                                list.length = 0;
                                this.into = false;
                                this.success = true;
                            }
                        });
                    }else{
                        this.$message.warning('请输入要入库的数量！');
                    }
                }else{
                    this.$message.warning('请选择选择要入库的选项！');
                }

            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('ApplyList_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.pages.currentPage;
                        this.total = data.pages.totalCount;
                        this.pageSize = data.pages.perPage;
                    }
                );
            },
            ondialogsearch(){
                this.$store.dispatch('ApplyList_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.pages.currentPage;
                        this.total = data.pages.totalCount;
                        this.pageSize = data.pages.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            Tolink(index,row) {
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:5}});
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        .el-input__inner.instorge-input{
            height: 30px;
        }
        .instorge-success{
            i{
                font-size: 64px;
                color: #00A854;
            }
            div{
                font-size: 20px;
                font-weight: 700;
            }
        }
        .enter-storage-steps{
            font-size: 0;
            text-align: center;
            .enter-storage-step{
                position: relative;
                display: inline-block;
                width: 212px;
                font-size: 14px;
                text-align: left;
                b{
                    display: inline-block;
                    width: 28px;
                    height: 28px;
                    border-radius: 50%;
                    background-color: #eee;
                    border:1px solid #ddd;
                    color: #333;
                    text-align: center;
                    line-height: 28px;
                }
                &.finish{
                    b{
                        color: #fff;
                        background-color: #108ee9;
                        border:1px solid #108ee9;
                    }
                }
                .line{
                    position: absolute;
                    top: 13px;
                    left: 100px;
                    width: 98px;
                    height: 2px;
                    background-color: #108ee9;
                }
            }
            .enter-storage-step:last-child{
                width:65px;
            }
        }
        .tips{
            border:1px solid rgba(251, 233, 197, 1);
            background-color: #FEF9ED;
            padding: 5px;
            border-radius: 4px;
            margin-top: 10px;
            i{
                &.el-icon-warning{
                    float: left;
                    font-size: 20px;
                    color: #FAC450;
                    margin-top: 0px;
                    margin-right: 8px;
                }
                &.el-icon-close{
                    float: right;
                    font-size: 12px;
                    padding: 5px;
                    cursor: pointer;
                }
            }
        }
    }
</style>






