<template>
    <div class="container">
        <Asset-list v-if="istableshow" :istableshow="istableshow" @ToggleTableDialog="Toggle"></Asset-list>
        <h3 class="mb-md">库存管理</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10" class="">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keyword">
                                <el-input v-model="ruleForm.keyword" placeholder="请输入名称"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary">搜索</el-button>
                    <el-button @click="isClear('ruleForm')">清除</el-button>
                </el-col>
            </el-row>
        </el-form>
        <el-row class="t-l mb-lg">
            <el-col :span="24" >
                <el-button type="primary" @click="showPopup('add')" style="padding: 5px">新增库存</el-button>
                <el-button type="primary" @click="showPopup('upload')" style="padding: 5px">导入库存</el-button>
            </el-col>
        </el-row>
        <el-row >
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">

                <el-table-column type="index" prop="id" label="序号" width="80"></el-table-column>
                <el-table-column prop="asset_type_name" label="类别" width="200"></el-table-column>
                <el-table-column prop="asset_brand_name" label="品牌" width="200"></el-table-column>
                <el-table-column prop="name" label="名称" ></el-table-column>
                <el-table-column prop="amount" label="数量"  width="150"></el-table-column>
                <el-table-column prop="price" label="价格总计" width="150" ></el-table-column>
                <el-table-column prop="operation" label="操作" width="150" >
                    <template scope="scope">
                        <el-button  @click="showPopup('asset-list',scope.row)" type="text" size="small" width="100">详情
                        </el-button>
                    </template>
                </el-table-column>

            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>
        <!--  新增弹窗-->
        <el-row >
            <transition name="fade">
                <el-dialog class="constom-md-dialog"  :title="titles" :visible="Popup" :before-close="isVisible"
                           :close-on-click-modal="hide" :close-on-press-escape="hide">
                    <div class="enter-storage-steps"  v-if="parameter=='upload'">
                        <div class="enter-storage-step process">
                            <span class="number1">1</span>
                            <span>提交入库</span>
                            <div  :class="lines"></div>
                        </div>
                        <div class="enter-storage-step finish"  >
                            <span :class="numbers">2</span>
                            <span :class="number3">完成</span>
                        </div>
                    </div>

                    <div v-if="parameter=='upload'">
                        <el-row type="flex" justify="center"  v-show="next_shows">
                            <el-col :xs="8" :sm="8">
                                <el-button  class="tips" type="primary"  style="padding: 5px">
                                    下载导入库存模板
                                </el-button>
                            </el-col>
                        </el-row>

                        <el-row type="flex" justify="center"  v-show="next_main">
                            <el-col  :xs="8" :sm="8" :span="8" :offset="6">
                                <i class="el-icon-check"> </i>
                            </el-col>
                        </el-row>
                        <el-row type="flex" justify="center"  v-show="next_main">
                            <el-col  :xs="8" :sm="8" :span="8" :offset="5">
                                <span class="check_span">导入完成</span>
                            </el-col>
                        </el-row>
                        <el-row type="flex" class="row-bg" justify="center"  v-show="next_main">
                            <el-col :span="15" :xs="15" :sm="15" :offset="2">
                                共计100条，导入成功50条，失败50条。
                            </el-col>
                        </el-row>
                        <el-row type="flex" class="row-bg" justify="center"  v-show="next_main">
                            <el-col   :span="15" :xs="15" :sm="15" :offset="9">
                                <div class="grid-content bg-purple">
                                    <button type="button" class="el-button el-button--default check_button"     >
                                        <span>下载失败数据</span>
                                    </button>
                                </div>
                            </el-col>
                        </el-row>
                    </div>


                    <el-form :model="PopupForm" ref="PopupForm" class="demo-ruleForm mt-0" v-if="parameter=='add'">

                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                                <span class="c-red mr4">*</span>类别：
                            </el-col>
                            <el-col :xs="24" :sm="18">
                                <el-form-item prop="asset_type" :rules="[{ required: true, message: '请选择类别'}]">
                                    <el-cascader :options="qgtypedata" v-model="PopupForm.asset_type"
                                                 style="width: 100%"></el-cascader>
                                </el-form-item>

                            </el-col>
                        </el-row>

                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                                <span class="c-red mr4">*</span>品牌：
                            </el-col>
                            <el-col :xs="24" :sm="18">
                                <el-form-item prop="brand" :rules="[{ required: true, message: '品牌'}]">
                                    <el-select v-model="PopupForm.brand" placeholder="请选择" style="width: 100%">
                                        <el-option :key="key" :label="item.name" :value="item.id+'/'+item.name"
                                                   v-for="(item,key) in brand"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                                <span class="c-red mr4">*</span>名称：
                            </el-col>
                            <el-col :xs="24" :sm="18">
                                <el-form-item prop="name" :rules="[{ required: true, message: '请输入名称'}]">
                                    <el-input v-model="PopupForm.name" placeholder="请输入" value="111"
                                              style="width: 100%"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                                <span class="c-red mr4">*</span>单价：
                            </el-col>
                            <el-col :xs="24" :sm="18">
                                <el-form-item prop="price" :rules="[{ required: true, message: '请输入单价'}]">
                                    <el-input v-model="PopupForm.price" placeholder="请输入"
                                              style="width: 100%"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row :gutter="10" class="mb-0">
                            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                                <span class="c-red mr4">*</span>数量：
                            </el-col>
                            <el-col :xs="24" :sm="18">
                                <el-form-item prop="amount" :rules="[{ required: true, message: '请输入数量'}]">
                                    <el-input-number v-model="PopupForm.amount" :step="1" :min="1"
                                                     style="width: 100%"></el-input-number>
                                </el-form-item>
                            </el-col>
                        </el-row>

                    </el-form>
                    <el-form :model="PopupForm" ref="PopupForm" class="demo-ruleForm"
                             v-if="parameter=='upload'" v-show="next_shows">
                        <el-row :gutter="10" type="flex" justify="center">
                            <el-col :xs="18" :sm="18">
                                <el-upload
                                        class="upload-demo"
                                        drag
                                        action="#"
                                        multiple>
                                    <i class="el-icon-upload"></i>
                                    <div class="el-upload__text">将文件拖到此处，或点击上传</div>
                                </el-upload>
                            </el-col>
                        </el-row>
                    </el-form>

                    <div slot="footer" class="dialog-footer" v-if="parameter=='add'">
                        <el-button type="button" class="el-button el-button--default"
                                @click="hidePopup()">
                            <span>取消</span>
                        </el-button>
                        <el-button type="button" class="el-button el-button--default el-button--primary "
                                @click="submitForm('PopupForm')">
                            <span>确定</span>
                        </el-button>
                    </div>
                    <div slot="footer" class="dialog-footer" v-if="parameter=='upload'">
                        <el-button type="button" class="el-button el-button--default"  v-show="upload_next"
                                @click="hidePopup()" >
                            <span>取消</span>
                        </el-button>
                        <el-button type="button" class="el-button el-button--default el-button--primary "  v-show="upload_next"
                                @click="showPopup('next','next-title')" >
                            <span>下一步</span>
                        </el-button>
                        <el-button type="button" class="el-button el-button--default el-button--primary "
                                @click="submitForm('PopupForm','next_button')"
                                v-show="!upload_next">
                            <span>确定</span>
                        </el-button>
                    </div>
                </el-dialog>
            </transition>

        </el-row>
    </div>
</template>

<script>
    import AssetList from '../../components/Administration/assetList.vue';
    export default {
        components: {
            AssetList
        },
        name:'personnelClass',
        data(){
            return {
                ruleForm:{
                    keyword:'',
                    applyDate:''
                },
                PopupForm:{
                    names:'',
                    asset_type:[],
                    brand:'',
                    name:'',
                    price:'',
                    amount:'',
                    number:1
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keyword:'',
                start_time:'',
                end_time:'',
                type:1,
                hide:false,
                Popup:false,
                parameter:'',
                id:0,
                item:'',
                radio3: '请购全部',
                istableshow:false,
                titles:'',
                assetId:0,
                rules: {
                    names:[
                        {required: true, message: '输入品牌类别', trigger: 'blur'}
                    ]
                },
                childCurpage:1,
                childTotalCount:1,
            };
        },
        created(){
            this.$store.dispatch('InventoryList_table',{page:1,page_size:10}).then(response=>{
                if(response.data.code == 200){
                    let data = response.data.data;
                    this.currentpage = data.pages.currentPage;
                    this.total = data.pages.totalCount;
                }
            });
            this.$store.dispatch('Purchase_type');
            this.$store.dispatch('Brand_type');
        },
        computed: {
            tableData(){
                return this.$store.state.InventoryList.Inventory_table;
            },
            qgtypedata(){
                return this.$store.state.purchase.purchase_typedata;
            },
            brand(){
                return this.$store.state.purchase.purchase_branddata;
            },
            assetlistData(){
                return this.$store.state.InventoryList.Asset_child_table;
            }
        },
        methods:{
            showPopup(parameter,obj) {
                this.parameter=parameter;
                if(parameter == 'asset-list'){
                    this.assetId = parseInt(obj.id);
                    let data = {
                        page:1,
                        page_size:10,
                        asset_id:this.assetId
                    }
                    this.$store.dispatch('Asset_table_pid',this.assetId);
                    this.istableshow=true;
                }
                else if (parameter == 'add'){
                    this.Popup = true;
                    this.titles = "新增库存";
                }
                else if (parameter == 'upload'){
                    this.Popup = true;
                    this.titles = "导入库存";
                    this.upload_next = true;
                }
                else if (parameter == 'next'){
                    if(obj == "next-title"){
                        this.parameter = "upload";
                        this.next_main = true;
                        this.upload_next = false;
                    }

                    this.lines.line = true,
                    this.lines.outline = false,
                    this.numbers.number1 = true,
                    this.numbers.number2 = false,
                    this.number3.number3_1 = false,
                    this.next_shows = false;
                }
                else {
                    return false;
                }
            },
            searchChildList(data){

            },
            hidePopup() {
                this.titles = "新增库存";
                this.Popup = false;
                if(this.parameter == 'edit'){
                    return false;
                }
                else if(this.parameter == 'add'){
                    this.PopupForm.names='';
                }
                else if(this.parameter == 'next'){
                    this.titles = "新增库存";
                }
                else {
                    return false;
                }
            },
            isVisible(formName){
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
                this.Popup = false;
                this.titles = "新增库存";
                this.next_main = false;
                this.next_shows = true;

                    this.lines.line = false,
                    this.lines.outline = true,
                    this.numbers.number1 = false,
                    this.numbers.number2 = true,
                    this.number3.number3_1 = true
            },
            closetips(){
                this.istips = true;
            },
            Toggle(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            //弹窗
            submitForm(popupForm,next_button) {
                let para = this.parameter;
                //新增库存
                if( para=='add'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {

                            let asset_typeid;
                            for(var j = 0; j < this.PopupForm.asset_type.length; j++){
                                asset_typeid = this.PopupForm.asset_type[j];
                            }
                            let data = {
                                asset_type_id:asset_typeid,
                                asset_brand_id:this.PopupForm.brand.split("/")[0],
                                name:this.PopupForm.name,
                                price:(this.PopupForm.price*1).toFixed(2),
                                amount:this.PopupForm.amount,
                            };
                            this.$store.dispatch('AddAsset_table',data).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else if(next_button == 'next_button'){
                    this.next_shows = true;
                    this.Popup = false;
                    this.titles = "新增库存";
                    this.next_main = false;
                    this.lines.line = false,
                    this.lines.outline = true,
                    this.numbers.number1 = false,
                    this.numbers.number2 = true,
                    this.number3.number3_1 = true
                }
                //编辑类型
                else if (para=='upload')
                {

                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_names=this.PopupForm.names;
                            let type_id = this.id;
                            this.$store.dispatch('PersonUpdate_table',{name:type_names,id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else
                {
                    return false;
                }
            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keyword,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('InventoryList_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.pages.currentPage;
                        this.total = data.pages.totalCount;
                        this.pageSize = data.pages.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keyword ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keyword = this.ruleForm.keyword;
                        console.log(this.keyword);
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            Tolink(index,row) {
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:row.type}});
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        .enter-storage-steps{
            font-size: 0;
            text-align: center;
            .enter-storage-step{
                position: relative;
                display: inline-block;
                width: 212px;
                font-size: 14px;
                text-align: left;
                .number1{
                    display: inline-block;
                    width: 28px;
                    height: 28px;
                    border-radius: 50%;
                    background-color: #108ee9;
                    color: #fff;
                    text-align: center;
                    line-height: 28px;
                }
                .number2{
                    display: inline-block;
                    width: 26px;
                    height: 28px;
                    border-radius: 50%;
                    background-color: rgba(0, 0, 0, 0.17);
                    color: #fff;
                    text-align: center;
                    line-height: 28px;
                }
                .number3_1{
                    color: rgba(0, 0, 0, 0.45);
                }
                .line{
                    position: absolute;
                    top: 13px;
                    left: 100px;
                    width: 98px;
                    height: 2px;
                    background-color: #108ee9;
                }
                .outline{
                    position: absolute;
                    top: 13px;
                    left: 100px;
                    width: 98px;
                    height: 2px;
                    background-color: rgba(153, 153, 153, 0.62);
                }
            }
            .enter-storage-step:last-child{
                width:62px;
            }
        }
        .tips{
           margin-top:10px
        }

            .el-icon-check{
                font-size: 29px;
                background: #00a854;;
                height: 38px;
                width: 47px;
                padding-top: 13px;
                border-radius: 50%;
                padding-left: 7px;
                margin-top: 20px;
                color: #fff;
            }
            .check_span{
                font-size: 17px;
                font-weight: 700;
            }
            .check_button{
            border: none;
            color: #108ee9;
            }

    }
</style>






