<template>
    <div class="attachment-wrap">
        <div class="const_wrap mb-md">
            <h1 class="mb-md">离职申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{date}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{applyID}}</td>
                </tr>
                <tr>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{create_person}}</td>
                    <td class="b_gray">离职日期</td>
                    <td class="t-l">{{leave_time}}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{org_full_name}}</td>
                    <td class="b_gray">职位</td>
                    <td class="t-l">{{job}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray" height="100">离职原因</td>
                    <td class="t-l" colspan="3">{{des}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray">物资是否归还</td>
                    <td class="t-l" colspan="3">{{stock_status}}</td>
                </tr>
                </tbody>
            </table> 
            <el-table :data="stock_list" width="100%">
                <el-table-column prop="type" label="类别"></el-table-column>
                <el-table-column prop="sn" label="库存编号" width="150"></el-table-column>
                <el-table-column prop="brand" label="品牌" width="120"></el-table-column>
                <el-table-column prop="name" label="名称" width="120"></el-table-column>
                <el-table-column prop="price" label="价格" width="100"></el-table-column>
                <el-table-column prop="status" label="状态" width="80"></el-table-column>
            </el-table>
            <table width="100%">            
                <tbody>
                <tr>
                    <td class="t-c b_gray" width="100">财务是否结算</td>
                    <td class="t-l">{{finance_status}}</td>
                </tr>
                </tbody>
            </table> 
            <el-table :data="finance_list" width="100%">
                <el-table-column prop="id" label="序号" width="70"></el-table-column>
                <el-table-column prop="time" label="借款时间" width="150"></el-table-column>
                <el-table-column prop="des" label="借款事由"></el-table-column>
                <el-table-column prop="name" label="借款金额" width="200"></el-table-column>
                <el-table-column prop="status" label="状态" width="80"></el-table-column>
            </el-table>
            <table width="100%">            
                <tbody>
                <tr>
                    <td class="t-c b_gray" width="120">账号密码是否交接</td>
                    <td class="t-l">{{account_status}}</td>
                </tr>
                </tbody>
            </table> 
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="120">企业QQ</td>
                    <td class="t-l" width="19%">{{qq}}</td>
                    <td class="b_gray" width="100">企业邮箱</td>
                    <td class="t-l" width="19%">{{email}}</td>
                    <td class="b_gray" width="100">工作手机号</td>
                    <td class="t-l">{{tel}}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">            
                <tbody>
                <tr>
                    <td class="t-c b_gray" width="120">工作是否交接</td>
                    <td class="t-l">{{work_status}}</td>
                </tr>
                </tbody>
            </table> 
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="120">审批人</td>
                    <td class="t-l" width="19%">{{constperson}}</td>
                    <td class="b_gray" width="100">抄送人</td>
                    <td class="t-l" width="19%">{{copyperson}}</td>
                    <td class="b_gray" width="100">财务确认</td>
                    <td class="t-l">财务确认</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    export default{
        name:'ReimburseSheet',
        props:[
            'tableData',
            'tips',
            'date',
            'applyID',
            'org_full_name',
            'create_person',
            'constperson',
            'copyperson',
            'leave_time', 
            'stock_list',
            'finance_list',
            'stock_status',
            'finance_status',
            'account_status',
            'work_status',
            'des',
            'job',
            'qq',
            'email',
            'tel'
        ],
        data(){
            return{

            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        padding:0 20px 25px 20px;
        border-radius: 4px;
        border: 1px solid #E9E9E9;
        .const_wrap {
            text-align: center;

            h1 {
                font-weight: 500;
            }
            table {
                width: 100%;
                border-collapse: collapse;
                border: 0;

                tr:last-child {

                    td {
                        border-bottom: 0;
                    }
                }
                td {
                    border: 1px solid #E9E9E9;
                    padding: 8px;
                    line-height: 23px;
                    font-size: 12px;
                    color: rgba(0,0,0,0.65);

                    &
                    .b_gray {
                        background: #F2F2F2;
                    }
                }
            }
            table:last-child {
                tr:last-child {
                    td {
                        border-bottom: 1px solid #E9E9E9;
                    }
                }
            }
        }
    }
</style>


