<template>
    <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm mt-lg" v-if="approvable=='1'">
        <el-row :gutter="10">
            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"><span class="c-red mr4">*</span>审批备注：</el-col>
            <el-col :xs="24" class="upload-wrap" style="max-width: 630px;">
                <el-form-item prop="approve" :rules="[{ required: true, message: '请输入审批意见'}]">
                    <el-input placeholder="请输入" v-model="ruleForm.approve" style="width: 100%;"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row :gutter="10">
            <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l"></el-col>
            <el-col :xs="24" :sm="16" class="upload-wrap">
                <el-form-item>
                    <el-button type="primary" @click="passapprove('ruleForm',1)">通过</el-button>
                    <el-button @click="passapprove('ruleForm',0)">不通过</el-button>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
</template>
<script>
    export default {
        props:['status','approvable'],
        data(){
            return{
                ruleForm:{
                    approve:''
                }
            }
        },
        methods:{
            passapprove(formName,status){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        const h = this.$createElement;
                        let msg_t = status==1 ? '通过该申请' : '确认不通过该申请吗？';
                        let msg_c = status==1 ? '确定通过该用户的申请吗？' : '确认不通过后，会中止该申请的继续进行并通知申请人。';
                        this.$msgbox({
                            message: h('div', null, [
                                h('h3',{style:'font-size:20px;font-weight: 500; margin-top:0'},
                                    [
                                        h('i',{class:'f-l el-icon-warning',style:'font-size:23px;color:#ffbf00;margin:4px 5px 0 0;'}),
                                        h('span',{style:'font-size:18px;'},msg_t),
                                    ]
                                ),
                                h('p',{style:'margin:4px 0 0 28px;'}, msg_c)
                            ]),
                            showCancelButton: true,
                            closeOnClickModal: false,
                            closeOnPressEscape:false,
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            beforeClose: (action, instance, done) => {
                                if (action === 'confirm') {
                                    instance.confirmButtonLoading = true;
                                    instance.confirmButtonText = '执行中...';
                                    this.$emit("Pass_approve", {tip: this.ruleForm.approve, status: status});
                                    setTimeout(() => {
                                        instance.confirmButtonLoading = false;
                                        done();
                                    }, 300);
                                } else {
                                    done();
                                }
                            }
                        })/*.then(action => {

                        });*/
                    } else {
                        return false;
                    }
                });
            },
        }
    }
</script>


