<template>
    <div class="attachment-wrap">
        <div class="const_wrap">
            <h1 class="mb-md">费用报销单</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{curdate}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{applyID}}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{org_full_name}}</td>
                    <td class="b_gray">报销人</td>
                    <td class="t-l">{{name}}</td>
                </tr>
                <tr>
                    <td class="b_gray">开户行名称</td>
                    <td class="t-l">{{ bankname }}</td>
                    <td class="b_gray">银行卡号</td>
                    <td class="t-l">{{ bank_card_id }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="34%">类别</td>
                    <td class="b_gray" width="33%">金额</td>
                    <td class="b_gray">明细</td>
                </tr>
                <tr v-for="item in tableData">
                    <td>{{item.type_name}}</td>
                    <td class="t-l">¥ {{(item.money * 1).toFixed(2) }}</td>
                    <td class="t-l">{{item.des==''?'--':item.des}}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="t-c" rowspan="2" width="17%">金额合计</td>
                    <td width="17%">小写</td>
                    <td class="t-l" width="33%">￥ {{counter}}</td>
                    <td>&nbsp;</td>
                </tr>
                <tr>
                    <td>大写</td>
                    <td class="t-l">{{ dxtotal }}</td>
                    <td>&nbsp;</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="15%">审批人</td>
                    <td class="t-l" width="20%">{{constperson}}</td>
                    <td class="b_gray" width="15%">抄送人</td>
                    <td class="t-l" width="20%">{{copyperson==''?'--':copyperson}}</td>
                    <td class="b_gray" width="15%">财务确认</td>
                    <td class="t-l" width="15%">{{ caiwuConfirm }}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    import { digitUppercase } from '../common'
    export default{
        name:'ReimburseSheet',
        props:[
            'tableData',
            'curdate',
            'applyID',
            'org_full_name',
            'name',
            'bankname',
            'bank_card_id',
            'constperson',
            'copyperson',
            'counter',
            'caiwuConfirm'
        ],
        data(){
            return{
                dxtotal:''
            }
        },
        updated(){
           this.dxtotal = digitUppercase(this.counter);
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        padding:0 20px 25px 20px;
        border-radius: 4px;
        border: 1px solid #E9E9E9;
        .const_wrap {
            text-align: center;

            h1 {
                font-weight: 500;
            }
            table {
                width: 100%;
                border-collapse: collapse;
                border: 0;

                tr:last-child {

                    td {
                        border-bottom: 0;
                    }
                }
                td {
                    border: 1px solid #E9E9E9;
                    padding: 8px;
                    line-height: 23px;
                    font-size: 12px;
                    color: rgba(0,0,0,0.65);

                    &
                    .b_gray {
                        background: #F2F2F2;
                    }
                }
            }
            table:last-child {
                tr:last-child {
                    td {
                        border-bottom: 1px solid #E9E9E9;
                    }
                }
            }
        }
    }
</style>


