<template>
    <div class="container">
        <h3 class="mb-md">资产品牌管理</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10" class="mb-0">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder="请输入品牌"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10" class="mb-0">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围"
                                                style="width: 100%"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isClear('ruleForm')">清除</el-button>
                </el-col>
            </el-row>
        </el-form>
        <el-row class="t-l mb-sm">
            <el-col :span="6" style="width: 120px;" >
                <el-button type="primary" @click="showPopup('add')" style="width:100%;padding: 5px">新增品牌</el-button>
            </el-col>
        </el-row>
        <el-row >
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                <el-table-column prop="number" label="序号" width="200"></el-table-column>
                <el-table-column prop="name" label="品牌"></el-table-column>
                <el-table-column prop="update_time" label="更新时间" sortable="custom" width="350"></el-table-column>
                <el-table-column prop="operation" label="操作" width="150">
                    <template scope="scope">
                        <el-button  @click="showPopup('edit',scope.row)" type="text" size="small">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>

        <!--  新增弹窗-->
        <el-row >
            <transition name="fade">
                <el-col :span="24" class="Popup_background" v-show="Popup" transition="fade">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title" v-if="parameter=='add'">新增品牌</div>
                            <div class="el-message-box__title" v-if="parameter=='edit'">编辑类别</div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn">
                                <i class="el-message-box__close el-icon-close" @click="hidePopup()"></i>
                            </button>
                        </div>
                        <div class="el-message-box__content">
                            <el-form :model="PopupForm" :rules="rules" ref="PopupForm"
                                     label-width="100px" class="demo-ruleForm">
                                <el-form-item label="类别-添加" prop="names" v-if="parameter=='add'">
                                    <el-input v-model="PopupForm.names"></el-input>
                                </el-form-item>
                                <el-form-item label="类别-编辑" prop="names" v-if="parameter=='edit'">
                                    <el-input v-model="PopupForm.names"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="hidePopup()">
                                <span>取消</span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary "
                                    @click="submitForm('PopupForm')">
                                <span>确定</span>
                            </button>
                        </div>
                    </div>

                </el-col>
            </transition>
        </el-row>
    </div>
</template>

<script>
    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";
    export default {
        components: {
            ElCol,
            ElRow
        },
        name:'AssetBrand',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                PopupForm:{
                    names:''
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
                Popup:false,
                parameter:'',
                id:0,
                item:'',
                rules: {
                    names:[
                        {required: true, message: '输入品牌类别', trigger: 'blur'}
                    ]
                }
            };
        },
        created(){
            this.$store.dispatch('Brandlist_table',{type:1,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.page.currentPage;
                this.total = data.page.totalCount;
            });
            Date.prototype.Format = function (fmt) { //author: meizz
                var o = {
                    "M+": this.getMonth() + 1, //月份
                    "d+": this.getDate(), //日
                    "h+": this.getHours(), //小时
                    "m+": this.getMinutes(), //分
                    "s+": this.getSeconds(), //秒
                    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
                    "S": this.getMilliseconds() //毫秒
                };
                if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
                for (var k in o)
                    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
                return fmt;
            }
        },
        computed:{
            tableData(){
                return this.$store.state.assetBrand.beankclass_table;
            },
        },
        methods:{
            showPopup(parameter,obj) {
                this.Popup = true;
                this.parameter=parameter;
                if(parameter == 'edit'){
                    this.id = obj.id;
                    this.PopupForm.names = obj.name;
                }
                else if (parameter == 'add'){
                    this.PopupForm.names='';
                }
                else {
                    return false;
                }
            },
            hidePopup() {
                this.Popup = false;
                if(this.parameter == 'edit'){
                    return false;
                }
                else if(this.parameter == 'add'){
                    this.PopupForm.names='';
                }
                else {
                    return false;
                }
            },
            //弹窗
            submitForm(popupForm) {
                let para = this.parameter;
                //添加类型
                if( para=='add'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let brand_name=this.PopupForm.names;
                            this.$store.dispatch('BrandAdd_table',{brand_name:brand_name}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //编辑类型
                else if (para=='edit')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_names=this.PopupForm.names;
                            let type_id = this.id;
                            this.$store.dispatch('BrandUpdate_table',{brand_name:type_names,brand_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else
                {
                    return false;
                }
            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('Brandlist_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family: Helvetica, sans-serif;
        .Popup_background{
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            padding-top: 16%;
            z-index: 10000;
            width:100%;
            height: 100%;
            text-align: center;
            overflow: auto;
            background: rgba(7,17,27,0.8);
        }
    }
</style>






