import { constPerson,Card,AddCard,Department,AddApplyID,constType } from '../../../api/public';
import { baoxiao_Create } from '../../../api/reimburse';
import Cookies from 'js-cookie';

const reimburse = {
    state: {
        reimburse_tabledata:[],//新增的报销明细表记录
        reimburse_constdata:[],//已选中审批人
        reimburse_copydata:[],//已选中抄送人
        reimburse_addApplyID:null,//报销单ID
    },
    mutations: {
        SET_reimburse_tabledata: (state, data) => {
            if(Object.prototype.toString.call(data) == '[object Object]'){
                state.reimburse_tabledata.push(data);
            }else{
                state.reimburse_tabledata = data;
            }
        },
        SET_reimburse_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.reimburse_constdata.push(data);

               //console.log(state.constdata);
            }else{
                //console.log(constdata);
                state.reimburse_constdata = data;
            }
        },
        SET_reimburse_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.reimburse_copydata).push(data);
                //console.log(state.copydata);
            }else{
                //console.log(copydata);
                state.reimburse_copydata = data;
            }
        },
        SET_reimburse_addApplyID: (state, data) => {
            state.reimburse_addApplyID = data;
        }
    },
    actions: {
        //添加一条报销记录
        Add_reimburse_tablesubmit:({ commit },data) => {
            let bxType='',result = {},ctype='';
            bxType = data.tabledata.bxType[data.tabledata.bxType.length-1];
            for(let key of data.constType){
                if(key.children != null){
                    for(let item of key.children) {
                        if(item.value == bxType){
                            ctype = item.label
                        }
                    }
                }else{
                    if(key.value == bxType){
                        ctype = key.label
                    }
                }
            }
            //console.log(data.tabledata.des);
            result = {money:parseFloat(data.tabledata.money).toFixed(2),type_name:ctype,des:data.tabledata.des==''?'--':data.tabledata.des,type:bxType};
            commit('SET_reimburse_tabledata',result);
        },
        //删除一条报销记录
        Deleterows:({ commit },data) => {
            commit('SET_reimburse_tabledata',data);
        },

        //添加审批人
        Add_reimburse_constsubmit:({ commit },data) =>{
            commit('SET_reimburse_constdata',data.consttext);
        },
        Delete_reimburse_ConstPerson:({ commit },data) =>{
            commit('SET_reimburse_constdata',data);
        },

        //添加抄送人
        Add_reimburse_copysubmit:({ commit },data) =>{
            commit('SET_reimburse_copydata',data.consttext);
        },
        Delete_reimburse_CopyPerson:({ commit },data) =>{
            commit('SET_reimburse_copydata',data);
        },

        //获取addApplyID
        Reimburse_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID({type:data.type}).then(response => {
                    let apply_id = response.data.data.apply_id;
                    //debugger
                    commit('SET_reimburse_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //提交报销单
        Reimburse_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                baoxiao_Create(data).then(response => {
                    commit('SET_reimburse_tabledata',[]);
                    commit('SET_reimburse_constdata',[]);
                    commit('SET_reimburse_copydata',[]);
                    commit('SET_reimburse_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });


        }

    }
}

export default reimburse;


