<template>
	<el-dialog title="新增员工" size="tiny" :visible="newstaffFormVisible" :before-close="hidedialog" :close-on-click-modal="hide" :close-on-press-escape="hide">
		<el-form :model="ruleForm" label-width="100px" :rules="ruleForm" ref="ruleForm">
			<el-form-item label="所属组织：" prop="org" :rules="[{ required: true, message: '请选择所属组织'}]">
                <el-col :span="16">
					<el-cascader v-model="ruleForm.org" placeholder="请选择" :options="org"></el-cascader>
				</el-col>
			</el-form-item>
			<el-form-item label="姓名：" prop="name" :rules="[{ required: true, message: '请输入姓名'}]">
                <el-col :span="16">
					<el-input v-model="ruleForm.name"></el-input>
				</el-col>
			</el-form-item>
			<el-form-item label="手机号码：" prop="phone" :rules="[{ required: true, message: '请输入手机号码'}]">
                <el-col :span="16">
					<el-input v-model="ruleForm.phone"></el-input>
				</el-col>
			</el-form-item>
            <el-form-item label="职位：" prop="job" :rules="[{ required: true, message: '请选择职位'}]">
                <el-col :span="16">
                    <el-select v-model="ruleForm.job" placeholder="请选择">
                    	<el-option v-for="item in profession" :key="item.index" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="入职时间：" prop="entry_time" :rules="[{ required: true, message: '请选择入职时间'}]">
                <el-col :span="16">
                	<el-date-picker v-model="ruleForm.entry_time" type="date" placeholder="选择日期"></el-date-picker>
                </el-col>
            </el-form-item>
		</el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="hidedialog">取 消</el-button>
            <el-button @click="onsubmitform('ruleForm')" type="primary" :loading="loading">确 定</el-button>
        </div>
	</el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';

	export default {
		name: 'newStaff',
		props:[ 'newstaffFormVisible','org' ],
	    data() {
	    	return {
	    		ruleForm: {
	    			org: [],
	    			name: '',
	    			phone: '',
	    			job: '',
	    			entry_time: '',
	    		},
	    		hide: false,
	    		loading: false,
	    	};
	    },
	    computed: {
            profession(){
            	return this.$store.state.global.position_data;
            }
	    }, 
	    created(){
            this.$store.dispatch('Position_List');
        },
	    methods: {
	    	//关闭对话框
	    	hidedialog(){
	    		this.$emit('hideDialog', 'newStaff');	
	    	},

	    	//提交表单
	    	onsubmitform(formName){
	    		this.loading = true;
	    		this.$refs[formName].validate((valid) => {
                    if (valid) {
			    		let data = {
			    				org_id: this.ruleForm.org[this.ruleForm.org.length - 1],
			    				name: this.ruleForm.name,
			    				phone: this.ruleForm.phone,
			    				profession: this.ruleForm.job,
			    				entry_time: this.ruleForm.entry_time.Format("yyyy-MM-dd")
			    			}; 

			    		this.$store.dispatch('Staff_Add',data).then( response => {
		                    this.loading = false;
		                    let data = response.data;
		                    if (data.code == 200) {
		                    	this.$message.success('添加员工成功');
			                    this.$emit('refresh');
			                    this.$refs[formName].resetFields();
			                    this.hidedialog();
		                    } else {
		                    	this.$message.error(data.data);	
		                    }
		                }).catch(err => {
		                    this.loading = false;
		                });
                    } else {                    	
		                this.loading = false;
                    }
                });


	    	}

	    }
	};
</script>


