<template>
    <div class="container">
        <h3 class="mb-md">资产类别管理</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder="请输入"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围"
                                                style="width: 100%"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isClear('ruleForm')">清除</el-button>
                </el-col>
            </el-row>
        </el-form>
        <el-row class="t-l mb-md">
            <el-col :span="6" style="width: 120px;" >
                <el-button type="primary" @click="showPopup('add')" style="width:100%;padding: 5px">新增</el-button>
            </el-col>
        </el-row>
        <el-row >
            <el-table class="custom-table" :data="tableData" @sort-change="onsorter" @filter-change="onfilter" @expand="tbclick"
                      border style="width: 100%"
                      :row-key="getRowKeys"
                      :expand-row-keys="expands"
                    >
                    <el-table-column type="expand" width="50">
                        <template scope="props">
                            <el-table :data="props.row.children" :border="!0">
                                <el-table-column
                                        label=""
                                        width="49">
                                </el-table-column>
                                <el-table-column
                                        label=""
                                        width="80">
                                </el-table-column>
                                <el-table-column
                                        prop="name"
                                        label="详情类别"
                                        style="width: 40%">
                                </el-table-column>
                                <el-table-column
                                        prop="time"
                                        label=""
                                        style="width: 40%">
                                </el-table-column>
                                <el-table-column prop="operation" label="">
                                    <template scope="scope">
                                        <el-button @click="showPopup('edit_child',scope.row)" type="text" size="small" width="100">编辑</el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                    </el-table-column>
                    <el-table-column prop="number" label="序号" width="80"></el-table-column>
                    <el-table-column prop="name" label="类别" style="width: 40%"></el-table-column>
                    <el-table-column prop="update_time" label="更新时间" sortable="custom" style="width: 40%"></el-table-column>
                    <el-table-column prop="operation" label="操作" >
                        <template scope="scope">
                            <el-button  @click="showPopup('edit',scope.row)" type="text" size="small">编辑</el-button>
                            <el-button  @click="showPopup('addDetails',scope.row)" type="text" size="small">新增详情类别</el-button>
                        </template>
                    </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>

      <!--  新增弹窗-->
        <el-row >
            <transition name="fade">
                <el-col :span="24" class="Popup_background" v-show="Popup" transition="fade">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title" v-if="parameter=='add'">新增类别</div>
                            <div class="el-message-box__title" v-if="parameter=='edit'">编辑类别</div>
                            <div class="el-message-box__title" v-if="parameter=='addDetails'">新增详情类别</div>
                            <div class="el-message-box__title" v-if="parameter=='edit_child'">详情类别编辑</div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn">
                                <i class="el-message-box__close el-icon-close" @click="hidePopup()"></i>
                            </button>
                        </div>
                        <div class="el-message-box__content">
                            <el-form :model="PopupForm" :rules="rules" ref="PopupForm"
                                     label-width="100px" class="demo-ruleForm">
                                <el-form-item label="类别-添加" prop="names" v-if="parameter=='add'">
                                    <el-input v-model="PopupForm.names"></el-input>
                                </el-form-item>
                                <el-form-item label="类别-编辑" prop="names" v-if="parameter=='edit'">
                                    <el-input v-model="PopupForm.names"></el-input>
                                </el-form-item>
                                <el-form-item label="详情_类别" prop="add_details" v-if="parameter=='addDetails'">
                                    <el-input v-model="PopupForm.add_details" type="textarea"
                                              :rows="4" placeholder="输入详情类别<每行一个详情类别>"></el-input>
                                    <p>输入详情类别(每行一个详情类别)</p>
                                </el-form-item>
                                <el-form-item label="详情_类别编辑" prop="edit_child" v-if="parameter=='edit_child'">
                                    <el-input v-model="PopupForm.edit_child"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="hidePopup()">
                                <span>取消</span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary "
                                    @click="submitForm('PopupForm')">
                               <span>确定</span>
                            </button>
                        </div>
                    </div>

                </el-col>
            </transition>
        </el-row>
    </div>
</template>

<script>
    export default {
        components: {
        },
        name:'AssetClass',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                PopupForm:{
                    add_details:'',
                    names:'',
                    edit_child:""
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
                Popup:false,
                parameter:'',
                id:0,
                item:'',

                rules: {
                    names: [
                        {required: true, message: '输入类别', trigger: 'blur'}
                    ],
                    add_details:[
                        {required: true, message: '输入详情列表，', trigger: 'blur'}
                    ]
                },
                expand:'',
                data_all: [],
                myArr:[],
                data_child:[],
                // 要展开的行，数值的元素是row的key值
                expands: []
            };
        },
        computed:{
            tableData(){
                return this.$store.state.assetClass.assetclass_table;
            },
            tableChlid(){
                return this.$store.state.assetClass.assetChild_table;
            }
        },
        created(){
            this.$store.dispatch('Assetclass_table',{type:1,page_size:10}).then(response=>{
                let data = response.data.data;
                if(data){
                    this.currentpage = data.page.currentPage;
                    this.total = data.page.totalCount;
                }else{
                    this.currentpage = 1;
                    this.total = 1;
                }
            });
        },
        mounted() {

        },
        updated(){

        },
        watch:{
            'tableChlid':{
                handler(curVal,oldVal){
                    conosle.log(curVal,oldVal);
                },
                deep:true
            }
        },
        methods:{
            // 获取row的key值
            getRowKeys(row) {
                return row.id;
            },
            tbclick(row,expanded) {
                /*this.$store.dispatch('AssetChild_table',{index:row.number,pid:row.id,page_size:100}).then(response =>{
                    if (response.data.code == 200) {
                        //this.tableChlid[row.number] = response.data.data.res;
                    }
                });*/
            },
            showPopup(parameter,obj) {
                this.Popup = true;
                this.parameter=parameter;
                if(parameter == 'edit'){
                    this.id = obj.id;
                    this.PopupForm.names = obj.name;
                } else if(parameter == 'edit_child'){
                    this.id = obj.id;
                    this.PopupForm.edit_child = obj.name;
                }
                else if (parameter == 'add'){
                    this.PopupForm.names='';
                }
                else if (parameter == 'addDetails'){
                    this.id = obj.id;
                }
                else {
                    return false;
                }
            },
            hidePopup() {
                this.Popup = false;
                if(this.parameter == 'edit'){
                    return false;
                }
                else if(this.parameter == 'add'){
                    this.PopupForm.names='';
                }else if(this.parameter == 'addDetails'){
                    this.PopupForm.add_details='';
                }
                else if(this.parameter == 'edit_child'){
                    this.PopupForm.edit_child='';
                }
                else {
                    return false;
                }
            },
            //弹窗
            submitForm(popupForm) {
                let para = this.parameter;
                //添加类型
                if( para=='add'){
                    console.log("type_name:"+this.PopupForm.names);
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_name=this.PopupForm.names;
                            this.$store.dispatch('AssetclassAdd_table',{type_name:type_name}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //编辑类型
                else if (para=='edit')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_names=this.PopupForm.names;
                            let type_id = this.id;
                            this.$store.dispatch('AssetclassUpdate_table',{type_name:type_names,type_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else if (para=='edit_child')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let type_names=this.PopupForm.edit_child;
                            let type_id = this.id;
                            this.$store.dispatch('AssetclassUpdate_table',{type_name:type_names,type_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                       /* this.ondispatch();*/
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //添加资产子类别
                else if(para='addDetails'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let detail=this.PopupForm.add_details;
                            let type_id = this.id;
                            this.$store.dispatch('AssetAddChild_table',{child:detail,type_id:type_id}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                else
                {
                    return false;
                }
            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time,
                    pid:this.pid
                };
            },
            ondispatch(){
                this.$store.dispatch('Assetclass_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family: Helvetica, sans-serif;
      .Popup_background{
          position: fixed;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          padding-top: 16%;
          z-index: 10000;
          width:100%;
          height: 100%;
          text-align: center;
          overflow: auto;
          background: rgba(7,17,27,0.8);
      }

    }


</style>






