import { Approval_list,Exit_Approval } from '../../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const ccsend = {
    state: {
        mysend_table:[],//表数据
    },
    mutations: {
        SET_mysend_table: (state, data) => {
            state.mysend_table = data;
        },
    },
    actions: {
        //表格数据
        Mysend_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Approval_list(data).then(response => {
                    let data = response.data.data;
                    //console.log(data.res);
                    commit('SET_mysend_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
        Exit_record:({ commit },data) =>{
            console.log(data);
            return new Promise((resolve, reject) => {
                Exit_Approval(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}

export default ccsend;


