<template>
	<div>
		<el-dialog :title="dialogTitle" size="tiny" :visible="approvalFormVisible" :before-close="hideApprovalForm" :close-on-click-modal="hide" :close-on-press-escape="hide">
	    	<div class="message">{{dialogContent}}</div> 
	        <div slot="footer" class="dialog-footer">
	            <el-button @click="hideApprovalForm">取 消</el-button>
	            <el-button @click="submitApprovalForm" type="primary" :loading="approvalLoading">确 定</el-button>
	        </div>
		</el-dialog>		
		<el-dialog :title="dialogTitle" size="tiny" :visible="employFormVisible" :before-close="hideEmployForm" :close-on-click-modal="hide" :close-on-press-escape="hide">
	    	<div class="message">{{dialogContent}}</div> 	    	
			<el-form :model="employForm" label-width="100px" :rules="employForm" ref="employForm">			
	            <el-form-item label="所属部门：" prop="org" :rules="[{ required: true, message: '请选择所属部门'}]">
	                <el-col :span="16">
	                	<el-cascader change-on-select v-model="employForm.org" :options="org" placeholder="选择部门"></el-cascader>
	                </el-col>
	            </el-form-item>
	            <el-form-item label="入职时间：" prop="entry_time" :rules="[{ required: true, message: '请选择入职时间'}]">
	                <el-col :span="16">
	                	<el-date-picker v-model="employForm.entry_time" type="date" placeholder="选择日期"></el-date-picker>
	                </el-col>
	            </el-form-item>
	        </el-form>
	        <div slot="footer" class="dialog-footer">
	            <el-button @click="hideEmployForm">取 消</el-button>
	            <el-button @click="submitEmployForm" type="primary" :loading="employLoading">确 定</el-button>
	        </div>
		</el-dialog>
		<el-dialog :title="dialogTitle" size="tiny" :visible="jointalentFormVisible" :before-close="hideJointalentForm" :close-on-click-modal="hide" :close-on-press-escape="hide">
	    	<div class="message">{{dialogContent}}</div> 
			<el-form :model="talentForm" label-width="100px" :rules="talentForm" ref="talentForm">
	            <el-form-item label="人才类型：" prop="talent_type" :rules="[{ required: true, message: '请选择人才类型'}]">
	                <el-col :span="16">
	                    <el-select v-model="talentForm.talent_type" placeholder="请选择">
	                    	<el-option v-for="item in talentTypes" :key="item.index" :label="item.label" :value="item.value"></el-option>
	                    </el-select>
	                </el-col>
	            </el-form-item>
	        </el-form>
	        <div slot="footer" class="dialog-footer">
	            <el-button @click="hideJointalentForm">取 消</el-button>
	            <el-button @click="submitJointalentForm" type="primary" :loading="jointalentLoading">确 定</el-button>
	        </div>
		</el-dialog>
	</div>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';

	export default {
		name: 'newResume',
		props:[ 
			'approvalFormVisible', 
			'employFormVisible', 
			'jointalentFormVisible',
			'dialogTitle',
			'dialogContent',
			'talentId',
			'currentStatus',
			'approvalValue',
		],
	    data() {
	    	return {
	    		talentForm: {
	    			talent_type: ''
	    		},
	    		employForm: {
	    			org: [],
	    			entry_time: ''
	    		},
	    		hide: false,
	    		approvalLoading: false,
	    		employLoading: false,
	    		jointalentLoading: false,
	    	};
	    },
	    computed: {
	    	talentTypes(){
	    		return this.$store.state.hire.talent_types;
	    	},
	    	org(){
	    		return this.$store.state.global.organizationalStructure;
	    	}
	    }, 
	    created(){
            this.$store.dispatch('OrganizationalStructure');
            this.$store.dispatch('Talent_Types');
        },
	    methods: {
	    	//关闭对话框
	    	hideApprovalForm(){
	    		return this.$emit('hideDialog', 'approvalFormVisible');
	    	},
	    	hideEmployForm (){
	    		this.$refs['employForm'].resetFields();
	    		return this.$emit('hideDialog', 'employFormVisible');
	    	},
	    	hideJointalentForm (){
	    		this.$refs['talentForm'].resetFields();
	    		return this.$emit('hideDialog', 'jointalentFormVisible');
	    	},

	    	//招聘操作
	    	submitApprovalForm(){
                this.approvalLoading = true;
	    		let data = {
	    				id: this.talentId,
	    				status: this.approvalValue ? 1 : 0,
	    				type: this.currentStatus
	    			};
	    		this.$store.dispatch('Resume_Approval',data).then( response => {
                    this.approvalLoading = false;
                    let data = response.data;
                    if (data.code == 200) {
                    	this.$message.success('操作成功');
	                    this.$emit('refreshResumes');
	                    this.hideApprovalForm();
                    } else {
                    	this.$message.error(data.data);	
                    }
                }).catch(err => {
                    this.approvalLoading = false;
                });
	    	},

	    	//录用
	    	submitEmployForm(){
	    		this.employLoading = true;
	    		this.$refs['employForm'].validate((valid) => {
                    if (valid) {
                    	let data = {
                    			id: this.talentId,
                    			org_id: this.employForm.org[this.employForm.org.length-1],
                    			entry_time: this.employForm.entry_time.Format("yyyy-MM-dd")
                    		};
             			this.$store.dispatch('Employ_Talent',data).then( response => {
		                    this.employLoading = false;
		                    let data = response.data;
		                    if (data.code == 200) {
		                    	this.$message.success('操作成功');
			                    this.$emit('refreshResumes');
			                    this.$refs['employForm'].resetFields();
			                    this.hideEmployForm();
		                    } else {
		                    	this.$message.error(data.data);	
		                    }
		                }).catch(err => {
				    		this.employLoading = false;
		                }); 
                    } else {
			    		this.employLoading = false;
                    }
                })
	    	},

	    	//加入人才库
	    	submitJointalentForm(){
	    		this.jointalentLoading = true;
	    		this.$refs['talentForm'].validate((valid) => {
                    if (valid) {
                    	let data = {
                    			id: this.talentId,
                    			talent_type: this.talentForm.talent_type
                    		};
             			this.$store.dispatch('Join_Talent',data).then( response => {
		                    this.jointalentLoading = false;
		                    let data = response.data;
		                    if (data.code == 200) {
		                    	this.$message.success('操作成功');
			                    this.$emit('refreshResumes');
			                    this.$refs['talentForm'].resetFields();
			                    this.hideJointalentForm();
		                    } else {
		                    	this.$message.error(data.data);	
		                    }
		                }).catch(err => {
		                    this.jointalentLoading = false;
		                }); 
                    } else {                    	
		                this.jointalentLoading = false;
                    }
                });
	    	},

	    }
	};
</script>

<style>
	.el-form {
		margin-top: 30px;
	}
</style>


