import { AddApplyID } from '../../../api/public';
import { Transfer_Create } from '../../../api/hire';
/*import Cookies from 'js-cookie';*/

const approveTransfer = {
    state: {
        transfer_constdata: [],//已选中审批人
        transfer_copydata: [],//已选中抄送人
        transfer_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_transfer_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.transfer_constdata.push(data);
            }else{
                state.transfer_constdata = data;
            }
        },
        SET_transfer_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.transfer_copydata).push(data);
            }else{
                state.transfer_copydata = data;
            }
        },
        SET_transfer_addApplyID: (state, data) => {
            state.transfer_addApplyID = data;
        }
    },
    actions: {
        //添加审批人
        Add_transfer_constsubmit:({ commit },data) =>{
            commit('SET_transfer_constdata',data.consttext);
        },
        Delete_transfer_ConstPerson:({ commit },data) =>{
            commit('SET_transfer_constdata',data);
        },

        //添加抄送人
        Add_transfer_copysubmit:({ commit },data) =>{
            commit('SET_transfer_copydata',data.consttext);
        },
        Delete_transfer_CopyPerson:({ commit },data) =>{
            commit('SET_transfer_copydata',data);
        },

        //获取addApplyID
        Transfer_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_transfer_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加申请调职
        Transfer_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                Transfer_Create(data).then(response => {
                    commit('SET_transfer_constdata',[]);
                    commit('SET_transfer_copydata',[]);
                    commit('SET_transfer_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  approveTransfer;


