<template>
    <div class="attachment-wrap">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">请购申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{ curdate }}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{ org_full_name }}</td>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{ name }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="100">对方名称</td>
                    <td class="t-l">{{ to_name }}</td>
                    <td class="b_gray">对方银行卡</td>
                    <td class="t-l">{{ bank_card_id }}</td>
                </tr>
                <tr>
                    <td class="b_gray">对方开户行</td>
                    <td colspan="3" class="t-l">{{ bankname }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td width="15%">类别</td>
                    <td width="80">品牌</td>
                    <td>名称</td>
                    <td width="80">单价</td>
                    <td width="80">数量</td>
                    <td width="80">小计</td>
                </tr>
                <tr v-for="item in tableData">
                    <td >{{item.asset_type_name}}</td>
                    <td width="80">{{ item.asset_brand_name }}</td>
                    <td width="150">{{ item.name }}</td>
                    <td width="80">{{ item.price }}</td>
                    <td width="80">{{ item.amount }}</td>
                    <td width="80">￥ {{ item.price*item.amount }}</td>
                </tr>
                <tr>
                    <td >&nbsp;</td>
                    <td width="80">&nbsp;</td>
                    <td width="150">&nbsp;</td>
                    <td width="80">&nbsp;</td>
                    <td width="80" class="t-c">总计</td>
                    <td width="80">￥ {{ counter }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="15%">说明</td>
                    <td colspan="5" width="150" class="t-l">{{ des==''?'--':des }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="15%">审批人</td>
                    <td class="t-l" width="20%">{{constperson}}</td>
                    <td class="b_gray" width="15%">抄送人</td>
                    <td class="t-l" width="20%">{{copyperson==''?'--':copyperson}}</td>
                    <td class="b_gray" width="15%">财务确认</td>
                    <td class="t-l" width="15%">财务确认</td>
                </tr>
                </tbody>
            </table>
        </div>
	</div>
</template>
<script>
    export default{
        name:'ReimburseSheet',
        props:[
            'tableData',
            'curdate',
            'applyID',
            'org_full_name',
            'name',
            'bankname',
            'bank_card_id',
            'to_name',
            'constperson',
            'copyperson',
            'counter',
            'des',
        ],
        data(){
            return{

            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        padding:0 20px 25px 20px;
        border-radius: 4px;
        border: 1px solid #E9E9E9;
        .const_wrap {
            text-align: center;

            h1 {
                font-weight: 500;
            }
            table {
                width: 100%;
                border-collapse: collapse;
                border: 0;

                tr:last-child {

                    td {
                        border-bottom: 0;
                    }
                }
                td {
                    border: 1px solid #E9E9E9;
                    padding: 8px;
                    line-height: 23px;
                    font-size: 12px;
                    color: rgba(0,0,0,0.65);

                    &
                    .b_gray {
                        background: #F2F2F2;
                    }
                }
            }
            table:last-child {
                tr:last-child {
                    td {
                        border-bottom: 1px solid #E9E9E9;
                    }
                }
            }
        }
    }
</style>


