<template>
    <div class="container">
            <h3 class="mb-md mt-0">待我审批</h3>
            <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
                <el-row class="search-wrap mb-lg">
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="keywords">
                                    <el-input v-model="ruleForm.keywords" placeholder="请输入"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="applyDate">
                                    <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="24" class="t-r">
                        <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isClear('ruleForm')">清除</el-button>
                    </el-col>
                </el-row>
            </el-form>
            <el-row>
                <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                    <el-table-column prop="id" label="序号" width="65"></el-table-column>
                    <el-table-column prop="date" label="申请时间" sortable="custom" width="150"></el-table-column>
                    <el-table-column prop="apply_id" label="审批单编号" width="190"></el-table-column>
                    <el-table-column
                            prop="type_value"
                            label="类型"
                            width="85"
                            :filters="[
                                { text: '报销', value:1},
                                { text: '借款', value:2},
                                { text: '还款', value:3},
                                { text: '用章', value:7},
                            ]"
                            filter-placement="bottom-end">
                    </el-table-column>
                    <el-table-column prop="title" label="标题"  width="170"></el-table-column>
                    <el-table-column prop="person" label="发起人" width="80"></el-table-column>
                    <el-table-column prop="approval_persons" label="审批人"></el-table-column>
                    <el-table-column prop="copy_person" label="抄送人"></el-table-column>
                    <el-table-column prop="next_des" label="状态" width="145"></el-table-column>
                    <el-table-column prop="operation" label="操作" width="65">
                        <template scope="scope">
                            <el-button
                                    @click="Tolink(scope.$index,scope.row)"
                                    type="text"
                                    size="small">
                                审批
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row class="t-r mt-lg">
                <el-pagination v-if="pagenation == 1"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentpage"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                </el-pagination>
                <el-pagination v-else small
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentpage"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="pageSize"
                        layout="prev, pager, next"
                        :total="total">
                </el-pagination>
            </el-row>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    export default {
        components: {},
        name:'WaitmeApproval',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
            }
        },
        computed:{
            ...mapGetters([
                'pagenation'
            ]),
            tableData(){
                return this.$store.state.waitmeapproval.waitmeapproval_table;
            },
        },
        created(){
            this.$store.dispatch('WaitmeApproval_table',{type:1,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.page.currentPage;
                this.total = data.page.totalCount;
            });
        },
        methods:{
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    at:this.filter,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('WaitmeApproval_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            Tolink(index,row) {
                //console.log(index, row);
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:row.type,approvable:1}});
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            }
        },
    }
</script>




