<template>
    <div class="success_wrap">
        <div class="t-c">
            <i class="el-icon-circle-check" style="color: #13ce66; font-size: 48px"></i>
        </div>
        <h1 class="t-c mb-sm" style="margin-bottom:8px">申请成功</h1>
        <p class="t-c mb-lg" >您的申请已提交成功，请耐心等待审批人审批，您可以点击“进入列表”到我发起的列表中查看审批进度，也可以点击“查看项目”，进入项目详情查看申请情况。</p>
        <div class="stepbox">
            <h3 class="mb-md">{{ title }}</h3>
            <el-row class="mb-lg" :gutter="16">
                <el-col :xs="24" :sm='8' class="mb-sm">发起时间：{{ create_time }}</el-col>
                <el-col :xs="24" :sm='8' class="mb-sm">审批单编号：{{ apply_id }}</el-col>
                <el-col :xs="24" :sm='8' class="mb-sm">抄送人：{{ copyperson==''?'--':copyperson }}</el-col>
            </el-row>
            <!--<el-steps :active="1" :center="isCenter">
                <el-step :key="key" :title="item.title" :description="item.name" v-for="(item,key) in stepdata"></el-step>
            </el-steps>-->
            <div class="steps">
                <div v-for="(item,index) in stepdata" class="step" :class="item.status_name">
                    <div class="step-header">
                        <div class="icon">
                            <span class="el-icon-check" v-if="item.status == 2"></span>
                            <span class="el-icon-close" v-if="item.status == 3"></span>
                            <span v-if="item.status < 2">{{index+1}}</span>
                        </div>
                        <div class="line"></div>
                    </div>
                    <div class="step-body">
                        <h2 class="step-title">{{item.title}}</h2>
                    </div>
                </div>
            </div>
        </div>
        <div class="t-c mt-md">
            <el-button type="primary" size="small" class="mr-md" @click="handleListClick">进入列表</el-button>
            <el-button size="small" @click="handleDetailClick" >查看项目</el-button>
        </div>
    </div>
</template>

<script>
    export default{
        components: {},
        name:'Success',
        data(){
            return{
                title:'',
                type:'',
                create_time:'',
                apply_id:'',
                copyperson:'--',
                stepdata:'',
                isCenter:true,
            }
        },
        created(){
            this.type = this.$route.query.type;
            this.apply_id = this.$route.query.apply_id;
            return new Promise((resolve, reject) => {
                this.$store.dispatch('Baoxiao_Form_success',{apply_id:this.apply_id,type:this.type}).then(response=>{
                    let data = response.data.data;
                        this.title = data.title;
                        this.stepdata = data.flow;
                        this.copyperson = data.copy_person;
                        this.create_time = data.create_time;

                        for (let i = 0; i < this.stepdata.length; i++) {
                            switch(this.stepdata[i].status) {
                                case 0:
                                    this.stepdata[i].status_name = 'wait'
                                    break;
                                case 2:
                                    this.stepdata[i].status_name = i!=this.stepdata.length-1 ? 'finish' : 'success';
                                    break;
                                case 3:
                                    this.stepdata[i].status_name = 'error'
                                    break;
                                default:
                                    this.stepdata[i].status_name = 'process'
                                    break;
                            }
                        }
                });
            }).catch(error => {
                reject(error);
            });
        },
        computed:{

        },

        methods:{
            handleListClick(){
                this.$router.push({path:'/approvals/mysend'});
            },
            handleDetailClick(){
                this.$router.push({path:'/approvals/details',query:{apply_id:this.apply_id,type:this.type}});
            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .success_wrap {
        width: 90%;
        margin: 0 auto;
        padding:50px 0;
        p{
            line-height: 1.5;
            padding: 0 15px;


        }
        .stepbox {
            padding: 16px 32px;
            background-color: #F7F7F7;
            .ant-steps {
                .ant-steps-title {
                    background: none !important;
                }
            }
        }
    }

    @media (max-width: 768px) {
        .success_wrap {
            width: 100%;
            p{
                text-align: left;
            }
        }
    }

    .steps {
        margin: 64px auto;
        display: flex;
        width: 100%;

        .step {
            flex: 20;
            color: #999;
            box-sizing: border-box;

            .step-header {
                position: relative;
                margin-bottom: 10px;
                width: 100%;
                height: 28px;
                .icon {
                    width: 28px;
                    height: 28px;
                    border-radius: 50%;
                    font-size: 14px;
                    line-height: 28px;
                    text-align: center;
                    border: 1px solid #999;                    
                    box-sizing: border-box;
                }
                .line {
                    position: absolute;
                    left: 0;
                    top: 14px;
                    width: 100%;
                    height: 1px;
                    padding-left: 56px;
                    padding-right: 28px;
                    background: none;
                    box-sizing: border-box;

                    &:before {
                        content: "";
                        width: 100%;
                        height: 100%;
                        background: #999;
                        display: block;
                    }
                }
            }

            .step-title {
                margin: 10px 0;
                font-size: 14px;
                font-weight: normal;
                white-space: nowrap;
            }
            .step-content {
                font-size: 12px;
                font-weight: normal;
                padding-bottom: 20px;
            }

            &:last-child {
                flex: 1;
                .step-header .line:before {
                    background: none;
                }
            }

            &.process {
                .step-header {
                    .icon {
                        border-color: #108ee9;
                        color: white;
                        background: #108ee9;
                    }
                }
                .step-title {
                    color: black;
                }
            }
            &.finish {
                .step-header {
                    .icon {
                        border-color: #108ee9;
                        color: #108ee9;
                    }
                    .line:before {
                        background: #108ee9;
                    }
                }
            }
            &.error {
                .step-header {
                    .icon {
                        border-color: #fc5746;
                        color: #fc5746;
                    }
                }
                .step-title {
                    color: #fc5746;
                }
            }
            &.success {                
                flex: auto;
                .step-header {
                    .icon {
                        border-color: #13ce66;
                        color: white;
                        background: #13ce66;
                    }
                }
            }
        }
    }

    @media screen and (max-width:768px) {
        .success_wrap {
            padding-top: 104px;
            p{
                text-align: justify;
            }
        }
        .steps {
            flex-direction: column;
            margin: 10px 0;
            .step {
                display: flex;
                flex-direction: row;

                .step-header {
                    margin: 0;
                    flex: 1;
                    height: 65px;
                    .icon {
                        width: 24px;
                        height: 24px;
                        line-height: 24px;
                        font-size: 10px;
                    }
                    .line {
                        left: 14px;
                        top: 0;
                        padding: 40px 0 12px 0;
                        width: 1px;
                        height: 100%;
                    }
                }

                .step-body {
                    padding: 0 0 0 20px;
                    flex: 9;
                    .step-title {
                        margin-top: 0;
                        line-height: 26px;
                    }
                }
            }
        }

    }
</style>


