import { AddApplyID } from '../../../api/public';
import { Assets_type,Purchase_Create,Brand_type_data } from '../../../api/purchase';

const purchase = {
    state: {
        purchase_constdata: [],//已选中审批人
        purchase_copydata: [],//已选中抄送人
        purchase_addApplyID: null,//报销单ID
        purchase_typedata:[],//资产类型
        purchase_branddata:[],//品牌
    },
    mutations: {
        SET_purchase_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.purchase_constdata.push(data);
            }else{
                state.purchase_constdata = data;
            }
        },
        SET_purchase_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.purchase_copydata).push(data);
            }else{
                state.purchase_copydata = data;
            }
        },
        SET_purchase_addApplyID: (state, data) => {
            state.purchase_addApplyID = data;
        },
        SET_purchase_typedata: (state, data) => {
            state.purchase_typedata = data;
        },
        SET_purchase_branddata: (state, data) => {
            state.purchase_branddata = data;
        }
    },
    actions: {
        //添加审批人
        Add_purchase_constsubmit:({ commit },data) =>{
            commit('SET_purchase_constdata',data.consttext);
        },
        Delete_purchase_ConstPerson:({ commit },data) =>{
            commit('SET_purchase_constdata',data);
        },

        //添加抄送人
        Add_purchase_copysubmit:({ commit },data) =>{
            commit('SET_purchase_copydata',data.consttext);
        },
        Delete_purchase_CopyPerson:({ commit },data) =>{
            commit('SET_purchase_copydata',data);
        },

        //获取addApplyID
        Purchase_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_purchase_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //资产类型下拉框数据
        Purchase_type:({ commit }) => {
            return new Promise((resolve, reject) => {
                Assets_type().then(response => {
                    commit('SET_purchase_typedata',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //品牌下拉框数据
        Brand_type:({ commit }) => {
            return new Promise((resolve, reject) => {
                Brand_type_data().then(response => {
                    commit('SET_purchase_branddata',response.data.data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        Purchase_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                Purchase_Create(data).then(response => {
                    commit('SET_purchase_constdata',[]);
                    commit('SET_purchase_copydata',[]);
                    commit('SET_purchase_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  purchase;


