<template>
    <div class="container">
        <details-head
                :title="stitle"
                :createperson="create_person"
                :copyperson="copyperson"
                :createtime="create_time"
                :applyid="apply_id"
                :status="status"
                :steps="steps"
                :next_des="next_des"
        >
        </details-head>
        <div class="line mt-lg mb-sm"></div>
        <h3>需审批内容 <el-button @click="approve_attachment_download" type="text" size="small">下载审批单</el-button></h3>
        <reimburse-sheet v-if="type=='1'"
                         :tableData="tableData"
                         :curdate="date"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :name="create_person"
                         :bankname="bank_name"
                         :bank_card_id="bank_card_id"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :counter = "counter"
                         :caiwuConfirm="caiwuConfirm"
        ></reimburse-sheet>
        <applyloan-sheet v-if="type=='2'"
                         :tableData="tableData"
                         :curdate="date"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :name="create_person"
                         :bankname="bank_name"
                         :bank_card_id="bank_card_id"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :counter = "counter"
                         :tips="tips"
                         :caiwuConfirm="caiwuConfirm"
        ></applyloan-sheet>
        <repayment-sheet v-if="type=='3'"
                         :tableData="tableData"
                         :curdate="date"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :name="create_person"
                         :bankname="bank_name"
                         :bank_card_id="bank_card_id"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :counter = "counter"
                         :tips="tips"
                         :caiwuConfirm="caiwuConfirm"
        ></repayment-sheet>
        <payment-sheet v-if="type=='4'"
                         :tableData="tableData"
                         :curdate="date"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :name="create_person"
                         :bankname="bank_name"
                         :bank_card_id="bank_card_id"
                         :to_name="to_name"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :counter = "counter"
                         :tips="tips"
                         :des="des"
                         :consttypename="consttypename"
        ></payment-sheet>
        <purchase-sheet v-if="type=='5'"
                         :tableData="tableData"
                         :curdate="date"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :name="create_person"
                         :bankname="bank_name"
                         :bank_card_id="bank_card_id"
                         :to_name="to_name"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :counter = "counter"
                         :tips="tips"
                         :des="des"
        ></purchase-sheet>
        <demand-sheet v-if="type=='6'"
                         :tableData="tableData"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :des="des"
                         :date="date"
        ></demand-sheet>
        <chapter-sheet v-if="type=='7'"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :chapter_type="chapter_type"
                         :chapter_name="chapter_name"
                         :date="date"
                         :des="des"
        ></chapter-sheet>
        <assetsrecipients-sheet v-if="type=='8'"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :tableData="tableData"
                         :date="date"
                         :counter="counter"
                         :des="des"
        ></assetsrecipients-sheet>
        <assetsreturn-sheet v-if="type=='9'"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :tableData="tableData"
                         :date="date"
                         :counter="counter"
                         :des="des"
        ></assetsreturn-sheet>
        <positive-sheet v-if="type=='10'"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :date="date"
                         :job="job"
                         :entry_time="entry_time"
                         :prosecution="prosecution"
                         :summary="summary"
                         :suggest="suggest"
        ></positive-sheet>
        <applyleave-sheet v-if="type=='11'"
                         :date="date"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :des="des"
                         :job="job"
                         :leave_time="leave_time"
                         :stock_list="stock_list"
                         :finance_list="finance_list"
                         :stock_status="stock_status"
                         :finance_status="finance_status"
                         :account_status="account_status"
                         :work_status="work_status"
                         :qq="qq"
                         :email="email"
                         :tel="tel"
        ></applyleave-sheet>
        <transfer-sheet v-if="type=='12'"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :date="date"
                         :entry_time="entry_time"
                         :old_org="old_org"
                         :old_profession="old_profession"
                         :target_org="target_org"
                         :target_profession="target_profession"
                         :transfer_time="transfer_time"
                         :des="des"
        ></transfer-sheet>
        <newstore-sheet v-if="type=='13'"
                         :applyID="apply_id"
                         :org_full_name="org"
                         :create_person="create_person"
                         :constperson="approval_person"
                         :copyperson="copyperson"
                         :date="date"
                         :summary="summary"
                         :address="address"
                         :city="city"
                         :rental="rental"
        ></newstore-sheet>
        <div class="attachment-table mt-lg" v-if="type != '3' && pagenation == 1">
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">附件：</el-col>
                <el-col :xs="24" :sm="16" class="upload-wrap">
                    <el-table :data="attachment" border style="max-width:661px;" max-height="250">
                        <el-table-column type="index" label="序号" width="150"></el-table-column>
                        <el-table-column prop="name" label="附件名" width="210"></el-table-column>
                        <el-table-column prop="ext" label="格式" width="150"></el-table-column>
                        <el-table-column label="操作" width="150">
                            <template scope="scope">
                                <el-button @click="attachment_download(scope.$index,scope.row)" type="text" size="small">下载
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
        </div>
        <subapprove @Pass_approve="Pass_approve" :approvable="approvable"/>
    </div>
</template>
<script>
    import {SpendTime,spendTime} from '../../components/common.js'
    import DetailsHead from '../../components/Detailopration/steps.vue'
    import subapprove from '../../components/Detailopration/subapprove.vue'
    import ReimburseSheet from '../../components/ApprovalSheet/ReimburseSheet.vue'
    import ApplyloanSheet from '../../components/ApprovalSheet/ApplyloanSheet.vue'
    import RepaymentSheet from '../../components/ApprovalSheet/RepaymentSheet.vue'
    import PaymentSheet from '../../components/ApprovalSheet/PaymentSheet.vue'
    import PurchaseSheet from '../../components/ApprovalSheet/PurchaseSheet.vue'
    import DemandSheet from '../../components/ApprovalSheet/DemandSheet.vue'
    import ChapterSheet from '../../components/ApprovalSheet/ChapterSheet.vue'    
    import AssetsrecipientsSheet from '../../components/ApprovalSheet/AssetsrecipientsSheet.vue'
    import AssetsreturnSheet from '../../components/ApprovalSheet/AssetsreturnSheet.vue'
    import PositiveSheet from '../../components/ApprovalSheet/PositiveSheet.vue'
    import ApplyleaveSheet from '../../components/ApprovalSheet/ApplyleaveSheet.vue'
    import TransferSheet from '../../components/ApprovalSheet/TransferSheet.vue'
    import NewstoreSheet from '../../components/ApprovalSheet/NewstoreSheet.vue'
    import { mapGetters } from 'vuex'
    import md5 from 'js-md5'
    export default{
        components: {
            DetailsHead,
            subapprove,
            ReimburseSheet,
            ApplyloanSheet,
            RepaymentSheet,
            PaymentSheet,
            PurchaseSheet,
            DemandSheet,
            ChapterSheet,
            AssetsrecipientsSheet,
            AssetsreturnSheet,
            PositiveSheet,
            ApplyleaveSheet,
            TransferSheet,
            NewstoreSheet,
        },
        name:'detail',
        data(){
            return{
                type:0,
                title:'',//页面标题
                date: '',//日期
                steps: [],//步骤
                next_des: '',

                apply_id:'',//审批单编号
                stitle:'',//详情头部标题
                create_person:'',//创建人
                create_time:'',//创建时间
                status:'',//审批状态
                attachment:[],//附件数据
                app_attachment_url:null,//审批单链接
                copyperson:'',//抄送人
                approval_person:'',//审批人
                org:'',//组织架构
                attachment_time:'',//审批单时间
                bank_name:'',//银行名称
                bank_card_id:'',//银行卡账号
                tableData:[],//单据动态表数据
                caiwuConfirm:'',//财务确认状态
                pagenation:localStorage.getItem('pagenation') || 1,

                //报销单
                counter:'',//总金额
                //借款单
                tips:'',

                //付款类型
                consttype: {},

                //付款单
                to_name: '',
                des: '',
                consttypename: '',

                //用章单
                chapter_type: '',
                chapter_name: '',

                //转正
                summary: '',
                job: '',
                entry_time: '',
                prosecution: '',
                summary: '',
                suggest: '',

                //调职
                old_org: '',
                old_profession: '',
                target_org: '',
                target_profession: '',
                transfer_time: '',

                //离职                
                leave_time: '',
                stock_list: [],
                finance_list: [],
                stock_status: '',
                finance_status: '',
                account_status: '',
                work_status: '',
                qq: '',
                email: '',
                tel: '',

                //开店
                address: '',
                city: '',
                rental: '',
            }
        },
        computed:{
            ...mapGetters([
                'org_full_name',
                'uid',
                'serverUrl'
            ])
        },
        created(){
            this.type = this.$route.query.type;
            this.apply_id = this.$route.query.apply_id;
            this.approvable = this.$route.query.approvable;
            this.$store.dispatch('Detais_list',{apply_id:this.apply_id,type:this.type}).then(response => {
                let data = response.data.data;
                //console.log(data);
                //步骤条
                this.steps = data.flow;
                for (let i = 0; i < this.steps.length; i += 1) {
                    let step = this.steps[i];
                    if (i != this.steps.length-1) {
                        this.steps[i].content = step.name+' &nbsp; '+step.date+'<br>'+step.org;
                        if (step.diff_time) this.steps[i].content += '<br>耗费: '+ spendTime(step.diff_time);
                        if (step.des) this.steps[i].content += '<br>说明: '+step.des;
                    } else {                       
                        this.steps[i].content = step.status==2 ? step.date+'完成<br>总共耗费: '+ spendTime(step.diff_time) : '';
                    }

                    switch(this.steps[i].status) {
                        case 0:
                            this.steps[i].status_name = 'wait'
                            break;
                        case 2:
                            this.steps[i].status_name = i!=this.steps.length-1 ? 'finish' : 'success';
                            break;
                        case 3:
                            this.steps[i].status_name = 'error'
                            break;
                        default:
                            if (data.status != 3) {
                                this.steps[i].status_name = 'process';
                            } else {
                                this.steps[i].status_name = 'forgo';
                                this.steps[i].content += '<br><span style="color:#ff4949">申请人撤销申请</span>';
                            }
                            break;
                    }
                }

                this.stitle = data.title;
                this.create_person = data.person;
                this.create_time = data.create_time;
                this.next_des = data.next_des;

                this.approval_person = data.approval_persons;
                this.copyperson = data.copy_person ? data.copy_person : '--';
                /*switch (data.status){
                    case 1:
                        this.status = '审批中';
                        break;
                    case 2:
                        this.status = '财务确认';
                        break;
                    case 3:
                        this.status = '撤销';
                        break;
                    case 4:
                        this.status = '审批不通过';
                        break;
                    case 5:
                        this.status = '完成';
                        break
                }*/
                this.attachment = data.info.files == "" ? []:data.info.files;
                this.app_attachment_url = data.pdf;
                //console.log(this.app_attachment_url);
                this.org = this.$store.getters.org_full_name;
                //console.log(this.org_full_name,);
                /*let alldatetime = data.create_time.split(" ")[0].split("-");
                this.attachment_time = alldatetime[0]+'年'+alldatetime[1]+'月'+alldatetime[2];*/
                //this.attachment_time = data.date;
                this.bank_name = data.info.bank_name+' '+data.info.bank_des;
                this.bank_card_id = data.info.bank_card_id;
                this.date = data.date;
                for(let i=0;i<data.flow.length;i++){
                    if(i==data.flow.length-2){
                        switch (data.flow[data.flow.length-2].status){
                            case 0:
                                this.caiwuConfirm = '等待确认';
                                break;
                            case 1:
                                this.caiwuConfirm = '确认中';
                                break;
                            case 2:
                                this.caiwuConfirm = '确认完成';
                                break;
                        }
                    }

                }

                switch((this.$route.query.type).toString()){
                    case '1':
                        this.counter = data.info.money;
                        this.tableData = data.info.list;
                        break;
                    case '2':
                        this.tips = data.info.tips == null ? '--':data.info.tips;
                        this.tableData = [{money:data.info.money,des:data.info.des}];
                        break;
                    case '3':
                        this.tips = data.info.des == null ? '--':data.info.des;
                        this.counter = data.info.money;
                        this.tableData = data.info.list;
                        break;
                    case '4':
                        this.to_name = data.info.to_name;
                        this.des = data.info.des == null ? '--':data.info.des;
                        this.counter = data.info.money;
                        this.consttypename = data.info.pay_type_name;
                        break;
                    case '5':
                        this.to_name = data.info.to_name;
                        this.des = data.info.des == null ? '--':data.info.des;
                        this.counter = data.info.money;
                        this.tableData = data.info.buy_list;
                        this.counter = 0;
                        for (let i in this.tableData) {
                            let price = this.tableData[i].price * this.tableData[i].amount;
                            this.tableData[i].total = price;
                            this.counter += price;
                        }
                        break;
                    case '6':
                        this.des = data.info.des == null ? '--':data.info.des;
                        this.tableData = data.info.demand_list;
                        break;
                    case '7':
                        this.chapter_type = data.info.chapter_type;
                        this.chapter_name = data.info.name;
                        this.des = data.info.des == null ? '--':data.info.des;
                        //console.log(data);
                        break;
                    case '8':
                        this.des = data.info.des;
                        this.tableData = data.info.list;
                        this.counter = 0;                        
                        for (let i in this.tableData)
                            this.counter += parseFloat(this.tableData[i].price);
                        break;
                    case '9':
                        this.des = data.info.des;
                        this.tableData = data.info.list;
                        this.counter = 0;                        
                        for (let i in this.tableData)
                            this.counter += parseFloat(this.tableData[i].price);
                        break;
                    case '10':
                        this.org = data.info.org;
                        this.job = data.info.job;
                        this.entry_time = data.info.entry_time;
                        this.prosecution = data.info.prosecution;
                        this.summary = data.info.summary;
                        this.suggest = data.info.suggest;
                        break;
                    case '11':
                        this.leave_time = data.info.leave_time;
                        this.des = data.info.des;
                        this.job = data.info.profession;
                        this.stock_status = data.info.stock_status;
                        this.finance_status = data.info.finance_status;
                        this.account_status = data.info.account_status;
                        this.work_status = data.info.work_status;
                        this.stock_list = data.info.stock_list;
                        this.finance_list = data.info.finance_list;
                        this.qq = data.info.qq;
                        this.email = data.info.email;
                        this.tel = data.info.tel;
                        break;
                    case '12':
                        this.entry_time = data.info.entry_time;
                        this.old_org = data.info.old_org;
                        this.old_profession = data.info.old_profession;
                        this.target_org = data.info.target_org;
                        this.target_profession = data.info.target_profession;
                        this.transfer_time = data.info.transfer_time;
                        this.des = data.info.des;
                        break;
                    case '13':                    
                        this.address = data.info.address;
                        this.city = data.info.city;
                        this.rental = data.info.rental;
                        this.summary = data.info.summary;
                        break;
                }
            });
            this.Title();
        },
        watch:{
            'stepsdata':(val,oldval)=>{
                console.log(val,oldval);
            }
        },
        methods:{
            Title(){
                if(this.type == '1'){
                    this.title = '报销';
                }
            },
            //审批单下载
            approve_attachment_download(){
                let time = Date.parse(new Date());
                    window.open('http://'+window.location.host+'/oa_v1/default/get-pdf?apply_id='+this.apply_id+'&type=crm&time='+ time +'&sign='+ md5(this.apply_id+time+'crm'));
            },
            //附件下载
            attachment_download(index,row){
                let time = Date.parse(new Date());
                window.open('http://'+window.location.host+'/oa_v1/default/down?path=' + row.url + '&name=' + row.name + '.' + row.ext+'&type=crm&time='+ time +'&sign='+ md5(this.apply_id+time+'crm'));
            },
            Pass_approve(des){
                let data ={
                        apply_id:this.apply_id,
                        person_id:this.uid,
                        des:des.tip,
                        status:des.status,
                        url:'/approvals/waitme'
                    };
                this.$store.dispatch('Approve_update',data).then(response =>{
                    if(response.data.code == 200){
                        this.$message({
                            message: '确认成功!',
                            type: 'success',
                            duration:1000,
                            onClose:()=>{
                                this.$router.push({path:'/approvals/waitme'});
                            }
                        });
                    }
                }).catch(error => {
                    reject(error);
                });
            }

        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        max-width: 960px;
    }
    .attachment-table{

        p{ float: left}
    }
    @media (max-width: 768px){
        .attachment-table {
            label{
                float: none !important;
                display: block;
                width: 100% !important;
                margin-bottom: 8px;
                font-size: 14px;
            }
            .el-table {
                width: 100% !important;
                margin-left: 0 !important;
            }
        }

    }
</style>


