import { basic_Info } from '../../../api/resume';

const resume = {
    state: {
        basic_info: {},//表数据
    },
    mutations: {
        SET_basic_info: (state, data) => {
            state.basic_info = data;
        },
    },
    actions: {
        //基本信息
        Basic_Info:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                basic_Info(data).then(response => {
                    let data = response.data.data;
                    commit('SET_basic_info',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
}

export default resume;


