<template>
    <div class="container">
        <table-modal
                :istableshow="istableshow"
                @ontableadd="ontableadd"
                @ToggleTableDialog="ToggleTableDialog"
        ></table-modal>
        <const-modal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></const-modal>
        <copy-modal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></copy-modal>
        <submit-modal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :files="fileList"
                :toname="toname"
                :bank_card="bank_card"
                :bankname="bankname"
                :tabledata="tabledata"
                :total="totalmoney"
                :des="des"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></submit-modal>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">请购表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>请购明细：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg" >
                    <el-table :data="tabledata" border :summary-method="getSummaries" show-summary style="max-width:891px; margin-bottom:20px">
                        <el-table-column type="index" label="序号" width="65"></el-table-column>
                        <el-table-column prop="asset_typename" label="类别" width="200"></el-table-column>
                        <el-table-column prop="asset_brandname" label="品牌" width="120"></el-table-column>
                        <el-table-column prop="name" label="名称"  width="120"></el-table-column>
                        <el-table-column prop="price" label="单价" width="120"></el-table-column>
                        <el-table-column prop="amount" label="数量" width="80"></el-table-column>
                        <el-table-column prop="total" label="小计" width="120"></el-table-column>
                        <el-table-column prop="option" label="操作" width="65">
                            <template scope="scope">
                                <el-button
                                        @click.native.prevent="deleteRow(scope.$index,tabledata)"
                                        type="text"
                                        size="small">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-button type="primary" size="small" @click="ToggleTableDialog({istableshow:true})">增加请购明细</el-button>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>对方名称：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="toname" :rules="[{ required: true, message: '请输入对方名称'}]">
                        <el-input v-model="ruleForm.toname" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>对方卡号：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="bank_card" :rules="[{ required: true, message: '请输入对方卡号'}]">
                        <el-input v-model="ruleForm.bank_card" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">
                    <span class="c-red mr4">*</span>开户行：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="bankname" :rules="[{ required: true, message: '请输入开户行'}]">
                        <el-input v-model="ruleForm.bankname" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">说明</el-col>
                <el-col :xs="24" :sm="12" :md="10" class="mb-lg">
                    <el-input type="textarea" :rows="3" placeholder="请输入" v-model="ruleForm.des"></el-input>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-sm t-r t-xs-l">上传附件</el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-upload class="upload-demo" name="files" multiple :on-success="handlefileChange" :action="BaseUrl">
                        <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                    </el-upload>
                </el-col>
            </el-row>
            <h3 class="mt-lg mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-15">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"></const-copy-person>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-15">
                    抄送人：
                </el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"></const-copy-person>
                </el-col>
            </el-row>
            <el-col class="ml-92 mb-lg">
                <el-button @click="onsubmitCheck('ruleForm')" type="primary">确 定</el-button>
                <el-button @click="isVisible('ruleForm')">取 消</el-button>
            </el-col>
        </el-form>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import TableModal from '../../components/purchase/TableModal.vue';
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/purchase/SubmitModal.vue';
    export default {
        name:'purchase',
        components:{
            TableModal,
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    name: '',
                    bank_card: '',
                    bankname:'',
                },
                tabledata:[],
                toname:'',
                bank_card:'',
                bankname:'',
                des:'',
                totalmoney:'',
                fileList: [],
                loading:false,
                istableshow:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.purchase.purchase_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.purchase.purchase_constdata;
            },
            copydata(){
                return this.$store.state.purchase.purchase_copydata;
            },
        },
        created(){

        },
        methods: {
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '合计';
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                    if(index === 6){
                        if (!values.every(value => isNaN(value))) {
                            sums[index] = values.reduce((prev, curr) => {
                                const value = parseInt(curr);
                                if (!isNaN(value)) {
                                    let pcvalue = prev*1 + curr*1;
                                    pcvalue = (pcvalue).toFixed(2);
                                    return pcvalue;
                                } else {
                                    return (prev).toFixed(2);
                                }
                            }, 0);
                            sums[index] = '￥ ' + sums[index];
                        } else {
                            sums[index] = '';
                        }
                    }
                });
                return sums;
            },
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            deleteRow(index, rows) {
                rows.splice(index,1);
                this.tabledata.splice(index,1);
            },
            ontableadd(data){
                this.tabledata.push(data);
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_purchase_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_purchase_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_purchase_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_purchase_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },
            //添加银行卡弹窗状态修改
            ToggleCardDialog(data){
                this.iscardshow = data.iscardshow;
            },
            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.purchase.purchase_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.purchase.purchase_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }

                let fujian = (this.fileList).map(data => data.response.data[0]);
                let data = {
                        apply_id:this.ApplyID,
                        approval_persons:resultconstdata,
                        copy_person:resulcopydata,
                        bank_card_id:this.bank_card,
                        bank_name:this.bankname,
                        to_name:this.toname,
                        files:fujian,
                        des:this.des,
                        money:this.totalmoney,
                        buy_list:this.tabledata,
                    };
                /*debugger
                return;*/
                this.$store.dispatch('Purchase_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:5}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitCheck(formName){
                let constdata = this.$store.state.purchase.purchase_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if(this.tabledata.length > 0 &&  constdata.length > 0){
                            this.toname=this.ruleForm.toname;
                            this.bank_card=this.ruleForm.bank_card;
                            this.bankname=this.ruleForm.bankname;
                            this.des=this.ruleForm.des;
                            let total = 0;
                            this.tabledata.map(data => total = total + data.total*1);
                            this.totalmoney = total.toFixed(2);
                            this.issubmitshow = true;
                        }else{
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


