import { talent_Types, edu_List } from '../../../api/hire';

const hire = {
    state: {
        edu_list:[],//表数据
        talent_types: [],
    },
    mutations: {
        SET_edu_list: (state, data) => {
            state.edu_list = data;
        },
        SET_talent_types: (state, data) => {
            state.talent_types = data;
        },
    },
    actions: {
        //学历列表
        Edu_List:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                edu_List(data).then(response => {
                    let data = response.data.data;
                    commit('SET_edu_list',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //人才类型
        Talent_Types:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                talent_Types(data).then(response => {
                    let data = response.data.data;
                    commit('SET_talent_types',response.data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
}

export default hire;


