import { salary_List, salary_Token } from '../../../api/hire';

const paid = {
	state: {
		salary_table: [],
	},
	mutations: {
        SET_salary_table: (state, data) => {
            state.salary_table = data;
        },
    },
    actions: {
        //表格数据
        Salary_Table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                salary_List(data).then(response => {
                    let data = response.data.data;
                    if (response.data.code == 200)
                    	commit('SET_salary_table',response.data.data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
        //薪酬Token
        Salary_Token:({commit}, data) =>{
            return new Promise((resolve, reject) => {
                salary_Token(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }


}

export default paid;


