import { AddApplyID,Chapter_Create } from '../../../api/public';

const chapter = {
    state: {
        chapter_constdata: [],//已选中审批人
        chapter_copydata: [],//已选中抄送人
        chapter_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_chapter_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.chapter_constdata.push(data);
            }else{
                state.chapter_constdata = data;
            }
        },
        SET_chapter_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.chapter_copydata).push(data);
            }else{
                state.chapter_copydata = data;
            }
        },
        SET_chapter_addApplyID: (state, data) => {
            state.chapter_addApplyID = data;
        },
    },
    actions: {
        //添加审批人
        Add_chapter_constsubmit:({ commit },data) =>{
            commit('SET_chapter_constdata',data.consttext);
        },
        Delete_chapter_ConstPerson:({ commit },data) =>{
            commit('SET_chapter_constdata',data);
        },

        //添加抄送人
        Add_chapter_copysubmit:({ commit },data) =>{
            commit('SET_chapter_copydata',data.consttext);
        },
        Delete_chapter_CopyPerson:({ commit },data) =>{
            commit('SET_chapter_copydata',data);
        },

        //获取addApplyID
        Chapter_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_chapter_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        Chapter_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                Chapter_Create(data).then(response => {
                    commit('SET_chapter_constdata',[]);
                    commit('SET_chapter_copydata',[]);
                    commit('SET_chapter_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  chapter;


