<template>
    <section class="app-main" style="min-height: 100%">
        <header v-if="isnav==0">
            <a herf="javascript:;" @click="checkurl"><span></span></a>
            <h1 class="title">{{ title }}</h1>
        </header>
        <Breadbar></Breadbar>
        <transition name="fade" mode="out-in">
            <router-view :key="key"></router-view>
        </transition>
    </section>
</template>

<script>
    import { mapGetters } from 'vuex'
    import Breadbar from './Breadbar.vue';
    export default {
        name: 'AppMain',
        components: {
            Breadbar
        },
        computed: {
            ...mapGetters([
                'isnav'
            ]),
            key() {
                return this.$route.name !== undefined
                    ? this.$route.name + +new Date()
                    : this.$route + +new Date()
            }
        },
        created () {
            this.title = this.$route.name == '首页' ? '车城OA系统':this.$route.name;
        },
        watch: {
            '$route' (to, from) {
                this.title = to.name == '首页' ? '车城OA系统':to.name;
            }
        },
        methods:{
            checkurl(){
                if(this.$route.name == '首页'){
                    this.homelink();
                }else{
                    this.childlink();
                }
            },
            homelink(){
                window.JavaJs.back();
            },
            childlink(){
                this.$router.push({path:'/adminhome',query:{isnav:0,pagenation:1}});
            }
        }
    }
</script>


<style rel="stylesheet/less" lang="less" scoped>
    /*app头*/
    header{
        position:fixed;
        z-index: 1000000;
        width: 100%;
        max-width: 640px;
        margin: 0 auto;
        padding: 20px 0 0;
        background-color: #FDC235;
        a {
            position: absolute;
            left: 0;
            top: 20px;
            padding: 13px 16px;
            span {
                display: block;
                width: 16px;
                height: 16px;
                background: url('../../assets/back.png') 0 0 no-repeat;
                background-size: 100% 100%;
            }
            &:active {
                -webkit-tap-highlight-color: transparent;
            }
        }
        .title {
            margin: 0 44px;
            line-height: 44px;
            text-align: center;
            font-size: 16px;
            font-weight: 500;
        }
    }
</style>


