<template>
    <el-dialog title="选择待领用资产" :visible.sync="istableshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <!--<el-form :model="ruleForm" ref="ruleForm">
            <el-col :span="10">
                <el-form-item prop="keywords">
                    <el-input v-model="ruleForm.keywords" placeholder="请输入关键字"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="2">
                <el-button @click="searchClick">查询</el-button>
            </el-col>
        </el-form>-->
        <el-table :data="kltabledata" ref="multipleTable" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column property="asset_brand" label="类别" width="200"></el-table-column>
            <el-table-column property="name" label="名称"></el-table-column>
        </el-table>
        <!--<el-row>
            <el-col>
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[5, 10, 15]"
                        :page-size="5"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="400">
                </el-pagination>
            </el-col>
        </el-row>-->
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button @click="onsubmitform('ruleForm')" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import { mapState } from 'vuex'
    export default {
        components: {},
        name:'TableModal',
        props:['istableshow','kltabledata','pagedata'],
        data(){
            return {
                /*ruleForm: {
                    keywords: '',
                },*/
                selectdata:'',
                hide:false,
                loading:false
            }
        },
        computed: {
        },
        created(){

        },
        methods: {
            isVisible(formName){
                this.$emit('ToggleTableDialog',{istableshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            handleSelectionChange(val) {
                this.selectdata = val;
            },
            handleSizeChange(){

            },
            handleCurrentChange(){

            },
            searchData(value){
                if(value != ''){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    at:this.filter,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('Ccsend_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },

            onsubmitform(formName){
                if(this.selectdata.length != 0){
                    this.loading = true;
                    this.$emit('alreadydata',this.selectdata);
                    setTimeout(() => {
                        this.loading = false;
                        this.$refs.multipleTable.clearSelection();
                        this.$emit('ToggleTableDialog', {istableshow: false});
                    }, 300);
                }else{
                    this.$message("请选择一行数据");
                }
            }
        }
    }
</script>


