<template>
    <el-dialog title="确认借款单" :visible="issubmitshow"  custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">借款单</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{curdate}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{applyID}}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{org_full_name}}</td>
                    <td class="b_gray">报销人</td>
                    <td class="t-l">{{name}}</td>
                </tr>
                <tr>
                    <td class="b_gray">开户行名称</td>
                    <td class="t-l">{{ bankdata[0]+' '+bankdata[2] }}</td>
                    <td class="b_gray">银行卡号</td>
                    <td class="t-l">{{ bankdata[1] }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="180">借款金额</td>
                    <td class="b_gray">事由</td>
                </tr>
                <tr>
                    <td class="t-c">¥ {{(money*1).toFixed(2)}}</td>
                    <td class="t-l">{{des==''?'--':des}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray">备注</td>
                    <td class="t-l">{{tips==''?'--':tips}}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="15%">审批人</td>
                    <td class="t-l" width="20%">{{constperson}}</td>
                    <td class="b_gray" width="15%">抄送人</td>
                    <td class="t-l" width="20%">{{copyperson==''?'--':copyperson}}</td>
                    <td class="b_gray" width="15%">财务确认</td>
                    <td class="t-l" width="15%">--</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible">取 消</el-button>
            <el-button @click="onsubmitform('ruleform')" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'SubmitModal',
        props:['issubmitshow','money','bank','des','tips','loading'],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'org_full_name',
                'name',
                'curdate'
            ]),
            bankdata(){
                let b = '';
                if (this.bank != null) {
                    return b = (this.bank).split("/");
                } else {
                    return b;
                }
            },
            applyID(){
                return this.$store.state.applyloan.applyloan_addApplyID;
            },
            constperson(){
                let constperson = "";
                if (this.$store.state.applyloan.applyloan_constdata.length > 0) {
                    constperson = (this.$store.state.applyloan.applyloan_constdata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return constperson;
            },
            copyperson(){
                let copyperson = "", resultdata = "";
                if (this.$store.state.applyloan.applyloan_copydata.length > 0) {
                    copyperson = (this.$store.state.applyloan.applyloan_copydata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return copyperson;
            }

        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            }
        },
        created(){
            this.$store.dispatch('Applyloan_AddApplyID',{type:2});
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid rgba(204, 204, 204, 1);
                padding: 8px;
                line-height: 23px;
                font-size: 14px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {

            tr:last-child {

                td {
                    border-bottom: 1px solid rgba(204, 204, 204, 1);
                }

            }
        }
    }
</style>


