import { 
    staff_List, 
    staff_Add,
    staff_Entry
} from '../../../api/hire';

const staff = {
    state: {
        staff_list:[],//表数据
    },
    mutations: {
        SET_staff_list: (state, data) => {
            state.staff_list = data;
        },
    },
    actions: {
        //表格数据
        Staff_List:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                staff_List(data).then(response => {
                    let data = response.data.data;
                    commit('SET_staff_list',response.data.data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        }, 
        //表格数据
        Staff_Add:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                staff_Add(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        }, 
        Staff_Entry:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                staff_Entry(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        }, 
    }
};

export default staff;


