<template>
    <div class="attachment-wrap">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">调职申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{date}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{create_person}}</td>
                    <td class="b_gray">入职日期</td>
                    <td class="t-l">{{ entry_time }}</td>
                </tr>
                <tr>
                    <td class="b_gray">原部门</td>
                    <td class="t-l">{{ old_org }}</td>
                    <td class="b_gray">调职后部门</td>
                    <td class="t-l">{{ target_org }}</td>
                </tr>
                <tr>
                    <td class="b_gray">原职位</td>
                    <td class="t-l">{{ old_profession }}</td>
                    <td class="b_gray">调职后职位</td>
                    <td class="t-l">{{ target_profession }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                    <tr>
                        <td class="b_gray" width="100">调职日期</td>
                        <td colspan="3" class="t-l">{{ transfer_time }}</td>
                    </tr>
                    <tr>
                        <td class="b_gray" width="100">调职原因</td>
                        <td colspan="3" class="t-l">{{ des==''?'--':des }}</td>
                    </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">审批人</td>
                    <td class="t-l">{{constperson}}</td>
                    <td class="b_gray" width="100">抄送人</td>
                    <td class="t-l">{{copyperson==''?'--':copyperson}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    export default{
        name:'ReimburseSheet',
        props:[
            'tableData',
            'tips',
            'date',
            'applyID',
            'org_full_name',
            'create_person',
            'constperson',
            'copyperson',
            'des',
            'entry_time',
            'old_org',
            'old_profession',
            'target_org',
            'target_profession',
            'transfer_time',
        ],
        data(){
            return{

            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        padding:0 20px 25px 20px;
        border-radius: 4px;
        border: 1px solid #E9E9E9;
        .const_wrap {
            text-align: center;

            h1 {
                font-weight: 500;
            }
            table {
                width: 100%;
                border-collapse: collapse;
                border: 0;

                tr:last-child {

                    td {
                        border-bottom: 0;
                    }
                }
                td {
                    border: 1px solid #E9E9E9;
                    padding: 8px;
                    line-height: 23px;
                    font-size: 12px;
                    color: rgba(0,0,0,0.65);

                    &
                    .b_gray {
                        background: #F2F2F2;
                    }
                }
            }
            table:last-child {
                tr:last-child {
                    td {
                        border-bottom: 1px solid #E9E9E9;
                    }
                }
            }
        }
    }
</style>


