<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        />
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        />
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :chapter_type="chapter_typename"
                :chapter_name="chapter_name"
                :des="des"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        />
        <el-form :model="ruleForm" ref="ruleForm"  class="demo-ruleForm">
            <h3 class="mb-md">用章需求单</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>印章类型：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="chapter_type" :rules="[{ required: true, message: '请选择印章类型'}]">
                        <el-select v-model="ruleForm.chapter_type" placeholder="请选择" style="width: 100%" >
                            <el-option :key="1" label="公章" value="1/公章"></el-option>
                            <el-option :key="2" label="财务章" value="2/财务章"></el-option>
                            <el-option :key="3" label="法人章" value="3/法人章"></el-option>
                            <el-option :key="4" label="合同专用章" value="4/合同专用章"></el-option>
                            <el-option :key="5" label="发票专用章" value="5/发票专用章"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>印章名称：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="chapter_name" :rules="[{ required: true, message: '请输入印章名称'}]">
                        <el-input v-model="ruleForm.chapter_name" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">事由：</el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="des">
                        <el-input
                                type="textarea"
                                :rows="3"
                                placeholder="请输入"
                                v-model="ruleForm.des">
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w97 f14 mb-md t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="12" :md="10" class="upload-wrap">
                    <el-upload class="upload-demo" name="files" multiple
                               :file-list="fileList"
                               :on-success="handlefileChange"
                               :on-remove="handlefileRemove"
                               :before-upload="beforeUpload"
                               :action="BaseUrl">
                        <el-button size="small"><i class="el-icon-upload2"></i>点击上传</el-button>
                    </el-upload>
                </el-col>
            </el-row>
            <h3 class="mt-lg mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata"
                                       @ToggleConstDialog="ToggleConstDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10">
                    抄送人：
                </el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata"
                                       @ToggleCopyDialog="ToggleCopyDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10"></el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <el-button @click="onsubmitCheck('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/chapter/submitModal.vue';
    export default {
        name:'chapter',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    chapter_type: '',
                    chapter_name: '',
                    des:''
                },
                chapter_type:'',
                chapter_typename:'',
                chapter_name:'',
                des:'',
                fileList: [],
                loading:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false,
                counter:0
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.chapter.chapter_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.chapter.chapter_constdata;
            },
            copydata(){
                return this.$store.state.chapter.chapter_copydata;
            },
        },
        created(){

        },
        methods: {
            isVisible(){
                this.$router.push({path:'/'});
            },
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '合计';
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                     if(index === 1){
                        if (!values.every(value => isNaN(value))) {
                            sums[index] = values.reduce((prev, curr) => {
                                const value = Number(curr);
                                if (!isNaN(value)) {
                                    let pcvalue = prev*1 + curr*1;
                                    pcvalue = (pcvalue).toFixed(2);
                                    return pcvalue;
                                } else {
                                    return (prev).toFixed(2);
                                }
                            }, 0);
                        } else {
                            sums[index] = '';
                        }
                    }
                });
                return sums;
            },
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            deleteRow(index, rows) {
                rows.splice(index,1);
                this.$store.dispatch('Deleterows',rows);
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            errorchange(err,file,fileList){
                this.$message(err);
            },
            beforeUpload(file){
                //console.log(file.type);
                const isjpg = file.type === 'image/jpeg';
                const isgif = file.type === 'image/gif';
                const ispng = file.type === 'image/png';
                const isxls = file.type === 'application/vnd.ms-excel';
                const isxlsx = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                const isdoc = file.type === 'application/msword';
                const isdocx = file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
                if(!isjpg && !isgif && !ispng && !isxls && !isdoc && !isxlsx && !isdocx){
                    this.$message.error('上传文件格式错误，必须是jpg,gif,png,xls,xlsx,doc,docx中的一种!');
                }
                return isjpg || isgif || ispng || isxls || isxlsx || isdoc || isdocx;
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_chapter_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_chapter_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_chapter_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_chapter_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },
            //添加银行卡弹窗状态修改
            ToggleCardDialog(data){
                this.iscardshow = data.iscardshow;
            },
            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.chapter.chapter_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.chapter.chapter_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }


                let fujian = (this.fileList).map(data => data.response.data[0]);

                let data = {
                    apply_id:this.ApplyID,
                    chapter_type:this.chapter_type,
                    name:this.chapter_name,
                    des:this.des,
                    files:fujian,
                    approval_persons:resultconstdata,
                    copy_person:resulcopydata,
                };

                this.$store.dispatch('Chapter_Add',data).then( response => {
                    this.loading = false;
                    if (response.data.code == 200) {
                        this.$router.push({path: '/success', query: {apply_id: response.data.data, type: 7}});
                    } else {
                        this.$message('系统出错，错误信息：' + response.data.message);
                    }
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitCheck(formName){
                let constdata = this.$store.state.chapter.chapter_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0) {
                            this.chapter_type = parseInt(this.ruleForm.chapter_type.split("/")[0]);
                            this.chapter_typename=this.ruleForm.chapter_type.split("/")[1];
                            this.chapter_name = this.ruleForm.chapter_name;
                            this.des = this.ruleForm.des;
                            this.issubmitshow = true;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


