import { AddApplyID } from '../../../api/public';
import { applyLeave_Handover, applyLeave_Create } from '../../../api/hire';

const applyleave = {
    state: {
        applyleave_constdata: [],//已选中审批人
        applyleave_copydata: [],//已选中抄送人
        applyleave_addApplyID: null,//离职申请单ID
    },
    mutations: {
        SET_applyleave_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.applyleave_constdata.push(data);
            }else{
                state.applyleave_constdata = data;
            }
        },
        SET_applyleave_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.applyleave_copydata).push(data);
            }else{
                state.applyleave_copydata = data;
            }
        },
        SET_applyleave_addApplyID: (state, data) => {
            state.applyleave_addApplyID = data;
        },
    },
    actions: {
        //添加审批人
        Add_applyleave_constsubmit:({ commit },data) =>{
            commit('SET_applyleave_constdata',data.consttext);
        },
        Delete_applyleave_ConstPerson:({ commit },data) =>{
            commit('SET_applyleave_constdata',data);
        },

        //添加抄送人
        Add_applyleave_copysubmit:({ commit },data) =>{
            commit('SET_applyleave_copydata',data.consttext);
        },
        Delete_applyleave_CopyPerson:({ commit },data) =>{
            commit('SET_applyleave_copydata',data);
        },

        //获取addApplyID
        Applyleave_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_applyleave_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //获取交接信息
        Handover_list:({ commit },data) => {
            return new Promise((resolve, reject) => {
                applyLeave_Handover(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },        

        //添加离职申请
        applyleave_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                applyLeave_Create(data).then(response => {
                    commit('SET_applyleave_constdata',[]);
                    commit('SET_applyleave_copydata',[]);
                    commit('SET_applyleave_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}

export  default  applyleave;


