<template>
	<section class="resume-section">
		<header class="section-header">
			<el-row>
				<el-col :xs="12" :sm="12" :md="12">
					<h2>个人信息 <small>{{basicInfo.status}}</small></h2>
				</el-col>
				<el-col class="t-r actions" :xs="12" :sm="12" :md="12">
					<a href="javascript:void(0)" @click="edit" title="编辑"><i class="el-icon-edit"></i></a>
				</el-col>
			</el-row>	
		</header>

		<div class="section-content">
			<el-row>
				<el-col :xs="24" :sm="16" :md="16">
					<el-row>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>姓名：</span>{{basicInfo.name}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>性别：</span>{{basicInfo.sex==1?'女':'男'}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>手机号码：</span>{{basicInfo.phone}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>出生日期：</span>{{basicInfo.birthday}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>电子邮件：</span>{{basicInfo.email}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>年龄：</span>{{basicInfo.age}}岁</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>名族：</span>{{basicInfo.nation}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>学历：</span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>政治面貌：</span>{{basicInfo.political}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>籍贯：</span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>工作年限：</span>{{basicInfo.work_time}}年</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>婚姻状态：</span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>职业状态：</span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>目前所在地：</span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span>到岗时间：</span>{{basicInfo.daogang}}</p>
						</el-col>
					</el-row>
				</el-col>
			</el-row>
			<el-form>
				<el-row :gutter="10">
					<el-col :xs="24" class="w97 f14 mb-sm">：</el-col>
					<el-col :xs="24" :sm="20" class="mb-lg">
						<el-form-item>
							<el-input></el-input>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</div>
	</section>
</template>

<script>
	export default {
        components: {},
        props:[ 'resumeId' ],
	    data() {
	    	return {
	    		formData: {

	    		},
	    	}
	    },
	    computed: {
	    	basicInfo(){
	    		return this.$store.state.resume.basic_info;
	    	}
	    },
	    created(){
	    	this.$store.dispatch('Basic_Info',{id:this.resumeId}).then(response => {
	    		let data = response.data.data;
	    		this.$emit('setName', data.name);
	    	});
	    },
	    methods: {
	    	//编辑
	    	edit(){
	    	}
	    }
	}
</script>

<style res="stylesheet/less" lang="less" scoped>
	@import './style.less';
	
	.photo {
	}
</style>


