<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
        <CardModal :iscardshow="iscardshow"
                   @ToggleCardDialog="ToggleCardDialog"
        ></CardModal>
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :bank="bank"
                :des="des"
                :data="repayment_select_record"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></SubmitModal>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mt-md mb-md">还款申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>选择待还借款：
                </el-col>
                <el-col :xs="24" :sm="20">
                    <el-table
                            :data="repayment_record"
                            border
                            :summary-method="getSummaries"
                            @selection-change="handleSelectionChange"
                            show-summary style="max-width:646px;" class="mb-lg">
                        <el-table-column type="selection" width="55"></el-table-column>
                        <el-table-column type="index" label="序号" width="120"></el-table-column>
                        <el-table-column prop="money" label="借款金额" width="120">
                            <template scope="scope">
                                ￥ {{ scope.row.money }}
                            </template>
                        </el-table-column>
                        <el-table-column prop="get_money_time" label="借款时间" width="150"></el-table-column>
                        <el-table-column prop="des" label="事由" width="200"></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>还款银行卡：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10" class="card-wrap">
                    <el-form-item prop="code" :rules="[{ required: true, message: '请选择银行卡'}]">
                        <el-select v-model="ruleForm.code" placeholder="请选择"  style="width: 100%;">
                            <el-option :key="item.value" :label="item.label" :value="item.value" v-for="item in carddatas"></el-option>
                        </el-select>
                        <a class="f12 addcard" href="javascript:;" @click="ToggleCardDialog({iscardshow:true})">增加银行卡</a>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l">说明：</el-col>
                <el-col :xs="24" :sm="12" :md="10" class="card-wrap">
                    <el-input v-model="ruleForm.des" type="textarea" :rows="3" placeholder="请输入说明"></el-input>
                </el-col>
            </el-row>
            <h3 class="mt-lg mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l mt-10">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l mt-10">
                    抄送人：
                </el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata" @ToggleCopyDialog="ToggleCopyDialog"/>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l mt-10"></el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <el-button @click="onsubmitRepayment('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import CardModal from '../../components/CardModal/CardModal.vue';
    import SubmitModal from '../../components/repayment/submitModal.vue';
    export default {
        name:'repayment',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            CardModal,
            SubmitModal
        },
        data(){
            return {
                ruleForm: {
                    code: '',
                },
                des:'',
                bank:null,
                loading:false,
                isconstshow:false,
                iscopyshow:false,
                iscardshow:false,
                issubmitshow:false,
                repayment_select_record: []
            }
        },
        created(){
            this.$store.dispatch('InitCard');
            this.$store.dispatch('Repayment_Record');
        },
        computed:{
            ApplyID(){
                return this.$store.state.repayment.repayment_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            carddatas(){
                return this.$store.state.global.bankCard;
            },
            constdata(){
                return this.$store.state.repayment.repayment_constdata;
            },
            copydata(){
                return this.$store.state.repayment.repayment_copydata;
            },
            repayment_record(){
                return this.$store.state.repayment.repayment_record;
            }

        },
        methods:{
            isVisible(){
                this.$router.push({path:'/'});
            },
            handleSelectionChange(val) {
                this.repayment_select_record = val;
            },
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '合计';
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                    if(index === 2){
                        if (!values.every(value => isNaN(value))) {
                            sums[index] = values.reduce((prev, curr) => {
                                const value = Number(curr);
                                if (!isNaN(value)) {
                                    let pcvalue = prev*1 + curr*1;
                                    pcvalue = (pcvalue).toFixed(2);
                                    return pcvalue;
                                } else {
                                    return (prev).toFixed(2);
                                }
                            }, 0);
                            sums[index] = '￥ '+ sums[index];
                        } else {
                            sums[index] = '';
                        }
                    }
                });
                return sums;
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_repayment_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_repayment_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_repayment_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_repayment_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },
            //添加银行卡弹窗状态修改
            ToggleCardDialog(data){
                this.iscardshow = data.iscardshow;
            },
            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            //核对信息提交按钮
            onsubmitRepayment(formName){
                let constdata = this.$store.state.repayment.repayment_constdata;
                let apply_ids = this.repayment_select_record.map(data => data.apply_id);
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0 && apply_ids.length > 0) {
                            this.bank = this.ruleForm.code;
                            this.des = this.ruleForm.des;
                            this.issubmitshow = true;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            },
            Onsubmit(){
                this.loading = true;
                let apply_ids = this.repayment_select_record.map(data => data.apply_id);
                let resultconstdata = [],resulcopydata = [];
                let constdata = this.$store.state.repayment.repayment_constdata;
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.repayment.repayment_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }
                let resultdata = {
                        apply_id:this.ApplyID,
                        approval_persons:resultconstdata,
                        copy_person:resulcopydata,
                        bank_card_id:this.bank.split("/")[1],
                        bank_name:this.bank.split("/")[0],
                        bank_des:this.bank.split("/")[2],
                        apply_ids:apply_ids,
                        des:this.des,
                    };
                this.$store.dispatch('Repayment_Add',resultdata).then( response => {
                    this.loading = false;
                    this.issubmitshow = false;
                    if(response.data.code==200){
                        this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:3}});
                    }else{
                        this.$message('系统出错，错误信息：'+response.data.message);
                    }
                }).catch(err => {
                    this.loading = false;
                });
            }
        }
    }
</script>


