<template>
    <div class="container">
        <h3 class="mb-md">{{pageTitle}}</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder="请输入"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isClear('ruleForm')">清除</el-button>
                </el-col>
            </el-row>
        </el-form>
	
		<new-resume 
				@refreshResumes="ondispatch"
                v-if="type==0"
		></new-resume><!-- 添加简历 -->
		<resume-actions
				:dialogTitle="dialogTitle"
				:dialogContent="dialogContent"
				:approvalFormVisible="approvalFormVisible"
				:employFormVisible="employFormVisible"
				:jointalentFormVisible="jointalentFormVisible"
				:talentId="talentId"		
				:currentStatus="currentStatus"
				:approvalValue="approvalValue"
				@refreshResumes="ondispatch"
				@hideDialog="hideDialog"
		></resume-actions><!-- 安排考试、面试，标记不合适，录用，移入人才库 -->

        <el-row>
            <el-table :data="tableData" @filter-change="onFilter" border style="width: 100%">
                <el-table-column type="index" prop="index" label="序号" width="65"></el-table-column>
				<el-table-column prop="name" label="姓名" width="100"></el-table-column>
				<el-table-column prop="phone" label="手机号码" width="130"></el-table-column>
				<el-table-column prop="profession" label="应聘职位" width="160"></el-table-column>
				<el-table-column prop="sex" label="性别" width="65"></el-table-column>
				<el-table-column prop="age"label="年龄"width="65"></el-table-column>
				<el-table-column prop="educational" label="学历" width=""></el-table-column>
				<el-table-column prop="work_time" label="工作年限" width="90"></el-table-column>
				<el-table-column prop="location" label="所在地" width="90"></el-table-column>
				<el-table-column prop="person_type" label="人才类型" width="100" v-if="type==1"></el-table-column>
				<el-table-column 
						prop="status" 
						label="状态" 
						width="100"
						:filters="[
                                { text: '待沟通', value:1},
                                { text: '待考试', value:2},
                                { text: '待面试', value:3},
                                { text: '不合适', value:4},
                                { text: '录用', value:5},
                            ]"
						filter-placement="bottom-end">			
				</el-table-column>
				<el-table-column label="操作" :width="type==1 ? 180 : 250">
					<template scope="scope">
						<div class="t-r">
							<el-button v-if="scope.row.status_value==1" @click="resumeAction(scope.row,true)" type="text" size="small"> 合适 </el-button>
							<el-button v-if="scope.row.status_value==2" @click="resumeAction(scope.row,true)" type="text" size="small"> 考试通过 </el-button>
							<el-button v-if="scope.row.status_value==3" @click="resumeAction(scope.row,true,'employ')" type="text" size="small"> 录用 </el-button>
							<el-button v-if="scope.row.status_value<4" @click="resumeAction(scope.row,false)" type="text" size="small"> 不合适 </el-button>
							<el-button v-if="type!=1 && scope.row.status_value<4" @click="resumeAction(scope.row,true,'join-talent')" type="text" size="small"> 移入人才库 </el-button>
							<el-button @click="Tolink(scope.row)" type="text" size="small"> 详情 </el-button>
						</div>
					</template>
				</el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination v-if="pagenation == 1"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
            <el-pagination v-else small
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="prev, pager, next"
                    :total="total">
            </el-pagination>
        </el-row>
	</div>	
</template>

<script>
    import { mapGetters } from 'vuex';
    import NewResume from '../../components/talent/newResume.vue';
    import ResumeActions from '../../components/talent/resumeActions.vue';

    export default {
        components: { NewResume, ResumeActions  },
        name:'talents',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                type: 0,
                pageTitle: '',

                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',

                approvalFormVisible: false,
                employFormVisible: false,
                jointalentFormVisible: false,
                dialogTitle: '',
                dialogContent: '',

                talentId: {type: Number},
                currentStatus: {type: Number},                
                approvalValue: {type: Boolean},    
            }
        },        
        computed:{
            ...mapGetters([
                'pagenation'
			]),
			tableData(){
				return this.$store.state.talent.resumes_table;
			}
        },
        created(){
        	this.type = this.$route.query.joined=='1' ? 1 : 0;
        	this.pageTitle = this.type==1 ? '人才库管理' : '招聘管理';
            this.$store.dispatch('Resumes_Table',{talent:this.type,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.page.currentPage;
                this.total = data.page.totalCount;
            });
        },
        methods:{
        	//检索数据
            searchData(paged){
                if(paged != null){
                    this.currentpage = paged;
                }
                return {
                    talent:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    status:this.filter,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            //更新简历列表
            ondispatch(){
                this.$store.dispatch('Resumes_Table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            //修改分页大小
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            //跳转到指定页码
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
			//清除搜索表单        	
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time = '';
                this.end_time='';
                this.ondispatch();
            },

        	// 过滤
            onFilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },

        	// 搜索
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },

            // 跳转到详情页
            Tolink(row){
            	this.$router.push({path:'/hire/resume', query:{talent_id: row.talent_id} });
            },

            // 简历的相关操作
            resumeAction(row, val, type='approval') {
            	this.talentId = row.talent_id;
            	this.currentStatus = row.status_value;
            	this.approvalValue = val;

            	if (! val) { //不合适
            		this.approvalFormVisible = true;	
            		this.dialogTitle = '不合适';
            		this.dialogContent = '是否确认该人员不合适，确认则无法招聘';
            	} else {
            		if (type === 'approval') { //安排考试、面试
	            		switch(row.status_value) {
	            			case 1:
			            		this.approvalFormVisible = true;	
			            		this.dialogTitle = '安排考试';
			            		this.dialogContent = '是否确认安排进行考试，确认则安排进行考试';
	            				break;
	            			case 2:
			            		this.approvalFormVisible = true;	
			            		this.dialogTitle = '安排面试';
			            		this.dialogContent = '是否确认安排进行面试，确认则安排进行面试';
	            				break;
	            		}
            		} 
        			if (type === 'join-talent') { //移入人才库
	            		this.jointalentFormVisible = true;	
	            		this.dialogTitle = '移入人才库';
	            		this.dialogContent = '是否确认移入人才库，确认则移入人才库';
        			}
        			if (type === 'employ') { //录用
	            		this.employFormVisible = true;	
	            		this.dialogTitle = '录用';
	            		this.dialogContent = '是否确认录用该人员，确认后则进行入职安排';
        			}
            	}
            },

            // 关闭弹窗
            hideDialog(name){
            	this[name] = false;
            }
        }
   	}
</script>



