import { Demand_list,DemandConfirmData} from '../../../api/administration';


const Demandlist = {
    state: {
        Demand_table:[],//门店列表数据

    },
    mutations: {
        SET_Demand_table: (state, data) => {
            state.Demand_table = data;
        },
    },
    actions: {
        //需求列表
        DemandList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Demand_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Demand_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //需求提交
        DemandConfirm:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                DemandConfirmData(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
};


export default Demandlist;


