<!--详情页步骤组件-->
<template>
    <div class="bill-info">
        <div class="info-wrap">
            <img class="avarter f-l" :src="require('../../assets/avarter.png')" alt="">
            <el-row class="infobox">
                <el-col :span="24" class="blod">{{ title }}</el-col>
                <el-col :span="8" :xs="24">发起人：{{ createperson }}</el-col>
                <el-col :span="16" :xs="24">抄送人：{{ copyperson }}</el-col>
                <el-col :span="8" :xs="24">发起时间：{{ createtime }}</el-col>
                <el-col :span="8" :xs="24">当前状态：{{ next_des }}</el-col>
                <el-col :span="8" :xs="24">审批单编号：{{ applyid }}</el-col>
            </el-row>
        </div>
        <h3 class="mt-lg mb-lg">审批进度</h3>
        <div class="steps">
            <div v-for="(item,index) in steps" class="step" :class="item.status_name">
                <div class="step-header">
                    <div class="icon">
                        <span class="el-icon-check" v-if="item.status == 2"></span>
                        <span class="el-icon-close" v-if="item.status==3 || item.status_name=='forgo'"></span>
                        <span v-if="item.status<2 && item.status_name!='forgo'">{{index+1}}</span>
                    </div>
                    <div class="line"></div>
                </div>
                <div class="step-body">
                    <h2 class="step-title">{{item.title}}</h2>
                    <div class="step-content" v-html="item.content"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        components: { },
        name:'Steps',
        props:['title','createperson','copyperson','createtime','status','applyid', 'steps', 'next_des' ],
        data(){
            return{
                isCenter:true,
            }
        },
        mounted() {
            // 格式化步骤信息
            let steps = document.getElementsByClassName('el-step__description');
            for (let i = 0; i < steps.length; i += 1 ) {
                console.log(steps[i].innerHTML);
                let html = steps[i].innerHTML.replace(/&lt;([^&gt;]+)&gt;/g,'<$1>');
                document.getElementsByClassName('el-step__description')[i].innerHTML = html;
            }
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .bill-info{
        .info-wrap{
            padding:16px 20px;
            border:1px solid #ADD8F7;
            border-radius: 4px;
            background: #ECF6FD;
            .avarter{
                margin-right: 20px;
            }
            .infobox{
                margin-left: 78px;
                font-size: 12px;
                line-height: 22px;

                .blod{
                    font-weight: 700;
                    font-size: 14px;
                }
                .el-col{
                    color: rgba(0,0,0,0.65) !important;
                }
            }
        }
    }

    .steps {
        display: flex;
        width: 100%;

        .step {
            flex: 20;
            color: #999;
            box-sizing: border-box;
            &:last-child{
                .step-header{
                    .line{
                        display: none;
                    }
                }
            }

            .step-header {
                position: relative;
                margin-bottom: 10px;
                width: 100%;
                height: 28px;
                .icon {
                    width: 28px;
                    height: 28px;
                    border-radius: 50%;
                    font-size: 14px;
                    line-height: 28px;
                    text-align: center;
                    border: 1px solid #999;
                    box-sizing: border-box;
                }
                .line {
                    position: absolute;
                    left: 0;
                    top: 14px;
                    width: 100%;
                    height: 1px;
                    padding-left: 56px;
                    padding-right: 28px;
                    background: none;
                    box-sizing: border-box;

                    &:before {
                        content: "";
                        width: 100%;
                        height: 100%;
                        background: #999;
                        display: block;
                    }
                }                
            }

            .step-title {
                margin: 10px 0;
                font-size: 14px;
                font-weight: normal;
                white-space: nowrap;
            } 
            .step-content {
                font-size: 12px;
                font-weight: normal;
                padding-bottom: 20px;
            }

            &:last-child {
                flex: 1;
                .step-header .line:before {
                    background: none;
                }
            }

            &.process {
                .step-header {
                    .icon {
                        border-color: #108ee9;
                        color: white;
                        background: #108ee9;
                    }
                }
                .step-title {
                    color: black;
                }
            }            
            &.finish {
                .step-header {
                    .icon {
                        border-color: #108ee9;
                        color: #108ee9;
                    }
                    .line:before {
                        background: #108ee9;
                    }
                }
            }
            &.error {                
                .step-header {
                    .icon {
                        border-color: #ff4949;
                        color: #ff4949;
                    }
                }
                .step-title {
                    color: #ff4949;
                }
            }
            &.forgo {                
                .step-header {
                    .icon {
                        border-color: #ff4949;
                        color: #ff4949;
                    }
                }
            }
            &.success {                                
                flex: auto;
                .step-header {
                    .icon {
                        border-color: #13ce66;
                        color: white;
                        background: #13ce66;
                    }
                }
            }
        }
    }

    @media screen and (max-width:480px) {        
        .steps {
            flex-direction: column;
            .step {
                display: flex;
                flex-direction: row;

                .step-header {
                    margin: 0;
                    flex: 1;
                    height: 118px;
                    .icon {
                        width: 24px;
                        height: 24px;
                        line-height: 24px;
                        font-size: 10px;
                    }
                    .line {
                        left: 14px;
                        top: 0;
                        padding: 40px 0 12px 0;
                        width: 1px;
                        height: 100%;
                    }
                }

                .step-body {
                    padding: 0 0 0 20px;
                    flex: 9;
                    .step-title {
                        margin-top: 0; 
                        line-height: 26px;
                    }
                }
            }
        }

    }
</style>


