import Vue from 'vue';
import Vuex from 'vuex';
import global from './modules/main/Global';
import app from './modules/main/app';
import userinfo from './modules/main/userinfo';
import permission from './modules/main/permission';
import reimburse from './modules/main/reimburse';
import applyloan from './modules/main/applyloan';
import repayment from './modules/main/repayment';
import waitmeapproval from './modules/approval/WaitmeApproval';
import alreadyapproval from './modules/approval/AlreadyApproval';
import ccsend from './modules/approval/Ccsend';
import mysend from './modules/approval/Mysend';
import RepaymentStatistics from './modules/RepaymentStatistics';
import detail from './modules/main/Details';
import positive from './modules/main/positive'
import applyleave from './modules/main/applyleave'
import getters from './getters';
import payment from './modules/finance/payment';
import make_collections from './modules/finance/make_collections';
import assetBrand from './modules/basicData/assetBrand';
import assetClass from './modules/basicData/assetClass';
import PersonnelClass from './modules/basicData/PersonnelClass';
import StaffInformation from './modules/basicData/StaffInformation';
import approveTransfer from './modules/main/approveTransfer'
import forpayment from './modules/main/forpayment'
import purchase from './modules/main/purchase'
import demand from './modules/main/demand'
import chapter from './modules/main/chapter'
import assetsRecipients from './modules/main/assetsRecipients'
import assetsReturn from './modules/main/assetsReturn'
import newStore from './modules/main/newStore'
//人事管理
import hire from './modules/hire/hire'
import talent from './modules/hire/talent'
import staff from './modules/hire/staff'
import salary from './modules/hire/salary'
import resume from './modules/hire/resume'
//门店列表
import StoreClass from './modules/store/StoreList';
//行政
import DemandList from './modules/administration/DemandList';
import ApplyList from './modules/administration/ApplyList';
import InventoryList from './modules/administration/InventoryManagement';
Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        app,
        userinfo,
        permission,
        global,
        reimburse,
        applyloan,
        repayment,
        waitmeapproval,
        alreadyapproval,
        ccsend,
        mysend,
        RepaymentStatistics,
        payment,
        detail,
        make_collections,
        assetBrand,
        assetClass,
        PersonnelClass,
        StaffInformation,
        positive,
        applyleave,
        StoreClass,
        approveTransfer,
        forpayment,
        purchase,
        demand,
        chapter,
        assetsRecipients,
        assetsReturn,
        newStore,
        DemandList,
        ApplyList,
        InventoryList,
        hire,
        talent,
        staff,
        salary,
        resume
    },
    getters
});

export default store



