import { RepaymentStatistics_list } from '../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const RepaymentStatistics = {
    state: {
        repaymentStatistics_table:[],//表数据
    },
    mutations: {
        SET_repaymentStatistics_table: (state, data) => {
            state.repaymentStatistics_table = data;
        },
    },
    actions: {
        //表格数据
        repaymentStatistics_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                RepaymentStatistics_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_repaymentStatistics_table',data.info);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

    }
}

export default RepaymentStatistics;


