<template>
    <el-menu mode="vertical" theme="dark" :default-active="$route.path" v-if="isnav==1">
        <h1 class="companyname"><img class="logo f-l" src="http://dev.erp.che.com/resource/img/logo_big.png" alt="">车城OA系统</h1>
        <sidebar-item :routes='permission_routers'></sidebar-item>
    </el-menu>
</template>

<script>
    import { mapGetters } from 'vuex';
    import SidebarItem from './SidebarItem.vue';
    export default {
        name:"Sidebar",
        components: { SidebarItem },
        computed: {
            ...mapGetters([
                'permission_routers',
                'isnav'
            ])
        },
        created(){
            //console.log(this.isnav);
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .el-menu {
        min-height: 100%;
    }
    .el-menu-item a{
        color: #bfcbd9;
        text-decoration: none;
    }
    .companyname{
        font-size: 22px;
        margin: 0;
        height: 50px;
        line-height: 50px;
        color: #fff;
        background-color: #009EFC;
    }
    .logo{
        margin-top: 12px;
        margin-left: 15px;
        margin-right: 15px;
    }
</style>



