<template>
	<div class="container">
        <h3 class="mb-md">薪酬管理</h3>

		<el-dialog title="薪酬管理" size="tiny" :visible="dialogVisible" :close-on-press-escape="false" :show-close="false">
			<el-form :model="IDCardForm" label-width="120px" :rules="IDCardForm" ref="IDCardForm">
	            <el-form-item max="6" label="身份证后6位：" prop="id_card_suffix" :rules="[{ required: true, message: '请输入身份证后6位', trigger: 'blur'}, { min: 6, max: 6, message: '请输入身份证后6位', trigger: 'blur'}]">
	                <el-col :span="16">
	                    <el-input v-model="IDCardForm.id_card_suffix" placeholder="请输入身份证后6位"></el-input>
	                </el-col>
	            </el-form-item>
	        </el-form>
	        <div slot="footer" class="dialog-footer">
	        	<el-button @click="historyBack" icon="arrow-left">返 回</el-button>
	            <el-button @click="submitIDCard" type="primary" :loading="loading">确 定</el-button>
	        </div>
		</el-dialog>

        <el-form :model="ruleForm" ref="ruleForm">
            <el-row v-if="controlsVisible">
                <el-col :xs="24" :md="16">
                    <el-row :gutter="10" class="search-row">
                        <el-col :xs="24" :sm="10" :md="10" class="search-label">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder="请输入内容"></el-input>
                            </el-form-item>
	                        <button type="button" class="search-icon el-icon-search" @click="onSearch"></button>
                        </el-col>
                        <el-col :xs="24" :sm="14" :md="14">
							<el-upload class="upload-control" name="file" :action="importUrl" 
									:show-file-list="false"
									:on-progress="uploading"
									:on-success="uploadedFile"
									:on-error="uploadFileFail">
								<el-button type="primary" :loading="importing">导入工资</el-button>
							</el-upload>
							<el-button type="text">
								<a :href="downloadUrl" target="_blank" download>下载导入模版</a>
							</el-button>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8" class="t-r">
			        <el-date-picker
			        	@change="chooseMonth"
						v-model="month"
						type="month"
						placeholder="选择月份">
					</el-date-picker>
                </el-col>
            </el-row>
            <el-row v-else>            
                <el-col :xs="24" :md="24" class="t-r">
			        <el-date-picker
			        	@change="chooseMonth"
						v-model="month"
						type="month"
						placeholder="选择月份">
					</el-date-picker>
                </el-col>
            </el-row>
        </el-form>

		<el-row>
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column prop="empno" label="员工编号" width="100"></el-table-column>
				<el-table-column prop="cost_depart" label="成本中心" width="300"></el-table-column>
				<el-table-column prop="date" label="月份" width="100"></el-table-column>
				<el-table-column prop="depart" label="部门" width="100"></el-table-column>
				<el-table-column prop="position" label="职位" width="120"></el-table-column>
				<el-table-column prop="name" label="姓名" width="100"></el-table-column>
				<el-table-column prop="base_salary" label="基本工资" width="100"></el-table-column>
				<el-table-column prop="jixiao" label="绩效工资" width="100"></el-table-column>
				<el-table-column prop="need_workdays" label="应出勤天数" width="100"></el-table-column>
				<el-table-column prop="static_workdays" label="实出勤天数" width="100"></el-table-column>
				<el-table-column prop="static_salary" label="实发工资" width="100"></el-table-column>
				<el-table-column prop="holiday_salary" label="国定假日加班工资" width="90"></el-table-column>
				<el-table-column prop="away_subsidy" label="出差补贴" width="100"></el-table-column>
				<el-table-column prop="other_subsidy" label="其他补发" width="100"></el-table-column>
				<el-table-column prop="forfeit" label="其他扣款" width="100"></el-table-column>
				<el-table-column prop="staitic_salary" label="应发工资合计" width="120"></el-table-column>
				<el-table-column prop="jixiao_money" label="绩效奖金" width="100"></el-table-column>
				<el-table-column prop="xiao_salary" label="孝工资" width="100"></el-table-column>
				<el-table-column prop="shebao" label="社保扣款" width="100"></el-table-column>
				<el-table-column prop="gongjijin" label="公积金扣款" width="100"></el-table-column>
				<el-table-column prop="before_tax_salary" label="税前工资" width="100"></el-table-column>
				<el-table-column prop="tax" label="个人所得税" width="100"></el-table-column>
				<el-table-column prop="illness_money" label="大病保险" width="100"></el-table-column>
				<el-table-column prop="after_tax_salary" label="税后工资" width="100"></el-table-column>
				<el-table-column prop="after_tax_salary_person" label="税后工资(员工个人)" width="100"></el-table-column>
				<el-table-column prop="des" label="备注" width="150"></el-table-column>
				<el-table-column prop="id_card" label="身份证号码" width="180"></el-table-column>
				<el-table-column prop="bank_card" label="工资卡号" width="180"></el-table-column>
				<el-table-column prop="bank_name_des" label="开户行信息" width="200"></el-table-column>
				<el-table-column prop="yanglao" label="养老" width="100"></el-table-column>
				<el-table-column prop="yiliao" label="医疗" width="100"></el-table-column>
				<el-table-column prop="shiye" label="失业" width="100"></el-table-column>
				<el-table-column prop="entry_time" label="入职日期" width="120"></el-table-column>
			</el-table>
		</el-row>
        <el-row class="t-r mt-lg">
            <el-pagination v-if="pagenation == 1"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
            <el-pagination v-else small
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="prev, pager, next"
                    :total="total">
            </el-pagination>
        </el-row>
    </div>
</template>

<script>
	import { mapGetters } from 'vuex';
	import md5 from 'js-md5';
	import underscore from 'underscore';

	export default {
        components: {  },
        name:'salary',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    month:''
                },
                IDCardForm:{
                	id_card_suffix: '',
                },

                dialogVisible: false,
                hide: false,
                loading: false,
                importing: false,

                _token: '',
                controlsVisible: false,

                currentpage:1,
                pageSize:10,
                total:0,
                keywords:'',
                month:'',
            }
        },        
        computed:{
            ...mapGetters([
                'pagenation'
			]),
			roleInfo(){
				return this.$store.state.userinfo.role_info;
			},
			tableData(){
				return this.$store.state.salary.salary_table;
			},
			importUrl(){
				return this.$store.state.global.serverUrl+'/oa_v1/salary/import';
			},
			downloadUrl(){
				return this.$store.state.global.serverUrl+'/template/车城体系薪酬模版.xlsx';
			},
        },
        created(){
        	if (this.hasPermission()) {
        		this.ondispatch();
        	} else {
        		this.dialogVisible = true;
        	}
        },
        methods:{
        	//是否拥有权限
        	hasPermission(){
        		if (underscore.intersection(['all_salary'], this.roleInfo).length > 0) {
        			this.controlsVisible = true;
	        		return true;
        		}

	        	if (localStorage._token) {
	        		let tokenData = JSON.parse( localStorage._token );
	        		if (tokenData.time + 6*10e4 > (new Date).getTime()) {
	        			this._token = tokenData.data;
	        			return true;
	        		}
	        	}

	        	return false;
        	},
        	//检索数据
            searchData(paged){
                if(paged != null){
                    this.currentpage = paged;
                }
                return {
                    page:this.currentpage,
                    page_size:this.pageSize,
                    keywords:this.keywords,
                    date:this.month,
                    _token: this._token
                };            	
            },
            //更新简历列表
            ondispatch(){
                this.$store.dispatch('Salary_Table',this.searchData()).then(response =>{
	                let data = response.data.data;
	                if (response.data.code == 200) {
		                this.currentpage = data.page.currentPage;
		                this.total = data.page.totalCount;
	                }
	                if (response.data.code == 405) {
	                	localStorage.removeItem('_token');
		        		this.dialogVisible = true;
	                }
                });
            },
            //修改分页大小
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            //跳转到指定页码
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
			//清除搜索表单        	
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time = '';
                this.end_time='';
                this.ondispatch();
            },

            //选择月份
            chooseMonth(value){
            	this.month = value
                this.ondispatch();
            },

        	// 搜索
            onSearch(formName){
                this.keywords = this.ruleForm.keywords;
                this.ondispatch();
            },

            //导入工资
            uploading(){
            	this.importing = true;
            },
            uploadedFile(){
            	this.importing = false;
				this.$message.success('导入工资成功');
				this.ondispatch();
            },
            uploadFileFail(){
            	this.importing = false;
            	this.$message.error('导入工资失败');
            },

            //提交身份证
            submitIDCard(){
            	this.loading = true;
            	this.$refs['IDCardForm'].validate((valid) => {
                    if (valid) {
                    	let data = {
                    			pwd: md5( this.IDCardForm.id_card_suffix )
                    		};
             			this.$store.dispatch('Salary_Token',data).then( response => {
		                    this.loading = false;
		                    let data = response.data;
		                    if (data.code == 200) {
		                    	this.dialogVisible = false;
		                    	this._token = data.data;

		                    	let token = {data:data.data,time:(new Date).getTime()}
					        	localStorage.setItem( '_token', JSON.stringify(token) );
					        	this.ondispatch();
		                    } else {
		                    	this.$message.error('输入有误');	
		                    }
		                }).catch(err => {
		                    this.loading = false;
		                }); 
                    } else {                    	
		                this.loading = false;
                    }
                });
            },

            //返回
            historyBack(){
            	history.back();
            }

        }
   	}	
</script>

<style rel="stylesheet/less" lang="less" scoped>
	.upload-control {
		margin-right: 10px;
		width: 90px;
		display: inline-block;
	}
	.search-row {
		margin-bottom: 0;
	}
	.search-label {
		position: relative;

		.search-icon {
			position: absolute;
			right: 10px;
			top: 3px;
			width: 28px;
			height: 28px;
			font-size: 20px;
			line-height: 28px;
			color: #999;
			background: none;
			border: none;
			&:focus {
				outline: none;
			}
		}
	}
</style>


