<template>
    <div class="container">
        <h3 class="mb-md">需求单列表</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm mb-lg">
            <el-row class="search-wrap">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder="审批单编号/标题"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary">搜索</el-button>
                    <el-button @click="isClear('ruleForm')">清除</el-button>
                </el-col>
            </el-row>
        </el-form>
        <el-row>
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                <el-table-column prop="index" label="序号" width="65" ></el-table-column>
                <el-table-column prop="create_time" label="申请时间" width="150" ></el-table-column>
                <el-table-column prop="apply_id" label="审批单编号" width="200" ></el-table-column>
                <el-table-column prop="person" label="申请人"  width="100"></el-table-column>
                <el-table-column prop="org" label="部门" width="200" ></el-table-column>
                <el-table-column prop="detail" label="需求内容"></el-table-column>
                <el-table-column prop="status" label="状态" sortable="custom"  width="100"></el-table-column>
                <el-table-column prop="operation" label="操作" width="150" >
                    <template scope="scope">
                        <el-button @click="Tolink(scope.$index,scope.row)" type="text" size="small">详情</el-button>
                        <el-button v-if="scope.row.status == '未采购'"  @click="showPopup(scope.$index,scope.row)" type="text" size="small">确认采购</el-button>
                    </template>
                </el-table-column>

            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>

        <!--  新增弹窗-->
        <el-dialog title="确认已请购" class="constom-md-dialog" :visible="Popup" :before-close="isVisible"
                   :close-on-click-modal="hide" :close-on-press-escape="hide">
            <el-form :model="PopupForm" ref="PopupForm">
                <el-row :gutter="10">
                    <el-col :xs="24" class="w120 f14 mb-sm t-r t-xs-l">
                        <span class="c-red mr4">*</span>类型：
                    </el-col>
                    <el-col :xs="16" :sm="17">
                        <el-form-item prop="radio">
                            <el-radio-group v-model="PopupForm.radio" @change="radiochange">
                                <el-radio-button label="请购全部"></el-radio-button>
                                <el-radio-button label="请购部分"></el-radio-button>
                                <el-radio-button label="库存已有"></el-radio-button>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                    <el-col :xs="24" class="w120 f14 mb-sm t-r t-xs-l">
                        <span class="c-red mr4">*</span>请购审批单号：
                    </el-col>
                    <el-col :xs="12" :sm="17">
                        <el-form-item prop="apply_buyID">
                            <el-input v-model="PopupForm.apply_buyID" :value="PopupForm.applyID" ></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :xs="24" class="w120 f14 mb-sm t-r t-xs-l">
                        <span class="c-red mr4">*</span>备注：
                    </el-col>
                    <el-col :xs="24" :sm="17">
                        <el-form-item prop="tips" class="mb-0">
                            <el-input v-model="PopupForm.tips" type="textarea" :rows="3" placeholder="请输入备注>"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="isVisible('PopupForm')">取 消</el-button>
                <el-button type="primary" @click="submitForm('PopupForm')" :loading="loading">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        components: {},
        name:'Demandlist',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                PopupForm:{
                    apply_buyID:'',
                    tips:'',
                    radio: '请购全部',
                    rules: {
                        names:[
                            {required: true, message: '输入品牌类别', trigger: 'blur'}
                        ]
                    },
                },
                hide:false,
                Popup:false,
                loading:false,
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
                id:0,
                item:'',
                buy_type:'',
                apply_id:'',
            };
        },
        created(){
            this.$store.dispatch('DemandList_table',{page:1,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.pages.currentPage;
                this.total = data.pages.totalCount;
            });
        },
        computed:{
            tableData(){
                 return this.$store.state.DemandList.Demand_table;
            },
        },
        methods:{
            isVisible(formName){
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
                this.Popup = false;
            },
            showPopup(index,row) {
                this.Popup = true;
                this.apply_id = row.apply_id;
            },
            radiochange(label){
                //this.buy_type = label;
                switch(label){
                    case '请购全部':
                        this.buy_type = '1';
                        break;
                    case '请购部分':
                        this.buy_type = '2';
                        break;
                    case '库存已有':
                        this.buy_type = '3';
                        break;
                }
            },
            //弹窗
            submitForm(popupForm) {
                this.$refs[popupForm].validate((valid) => {
                    if (valid) {
                        let data ={
                            apply_id:this.apply_id,
                            buy_type:this.buy_type==''?'1':this.buy_type,
                            apply_buy_id:this.PopupForm.apply_buyID,
                            tips:this.PopupForm.tips
                        }
                        /*debugger
                        return*/
                        this.$store.dispatch('DemandConfirm',data).then(response => {
                                if (response.data.code == 200) {
                                    this.$message({
                                        message: '确认采购成功！',
                                        type: 'success'
                                    });
                                    this.ondispatch();
                                }else{
                                    this.$message.error('错误信息：' + response.data.message);
                                }
                            }
                        );
                        this.Popup = false;
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('DemandList_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.pages.currentPage;
                        this.total = data.pages.totalCount;
                        this.pageSize = data.pages.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            Tolink(index,row) {
                //console.log(index, row);
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:6}});
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family: Helvetica, sans-serif;
        .el-row{
            margin-bottom: 0;
        }
    }
</style>






