import fetch from '../utils/fetch';
/*import axios from 'axios';*/
import qs from 'qs';


// 门店列表
export function Store_list(params) {
    return fetch({
        url: '/oa_v1/apply-open/get-open-list',
        method: 'get',
        params:params
    })
}
// 门店统计
export function Store_Statistics(params) {
    return fetch({
        url: '/oa_v1/apply-open/get-open-count',
        method: 'get',
        params:params
    })
}



