import fetch from '../utils/fetch';
/*import axios from 'axios';*/
import qs from 'qs';

export function Approval_list(params) {
    return fetch({
        url: '/oa_v1/apply/get-list',
        method: 'get',
        params:params
    });
   // return axios.get('http://www.cheoa.com?'+qs.stringify());
}

export function Exit_Approval(params) {
    return fetch({
        url: '/oa_v1/apply/revoke',
        method: 'post',
        data:qs.stringify(params)
    });
    // return axios.get('http://www.cheoa.com?'+qs.stringify());
}
//付款确认
export function Payment_list(params) {
    return fetch({
        url: '/oa_v1/pay-confirm/list',
        method: 'post',
        data:qs.stringify(params)
    })
}
// 收款确认
export function Make_Collections_list(params) {
    return fetch({
        url: '/oa_v1/back-confirm/list',
        method: 'post',
        data:qs.stringify(params)
    })
}



//在借款员工明细表
export function RepaymentStatistics_list(params) {
    return fetch({
        url: '/oa_v1/jiekuan/index',
        method: 'get',
        params:params
    });
}



