<template>
    <el-dialog v-if="status== 4 && type=='payment'" :title="title" :visible="isCollectPayment" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm" >
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>付款部门：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="department" :rules="[{ required: true, message: '请选择'}]">
                         <el-select v-model="ruleForm.department" filterable placeholder="请选择" style="width: 100%">
                             <el-option
                                     v-for="(item,key) in departments"
                                     :key="key"
                                     :label="item.label"
                                     :value="item.value">
                             </el-option>
                         </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>付款账号：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="bank" :rules="[{ required: true, message: '请选择'}]">
                        <el-select v-model="ruleForm.bank" filterable placeholder="请选择" style="width: 100%">
                            <el-option
                                    v-for="(item,key) in bank"
                                    :key="key"
                                    :label="item.name_short"
                                    :value="item.number + '/' + item.bank_name_des+'/' + item.name_short +'/'+ item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>付款类型：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="bankType">
                        <el-select v-if="bxtype==1" v-model="ruleForm.bankType" placeholder="自动生成，无需选择" disabled style="width: 100%">
                            <el-option label="1" value="自动生成，无需选择"></el-option>
                        </el-select>
                        <el-select v-else v-model="ruleForm.bankType" placeholder="请选择" style="width: 100%">
                            <el-option :key="key" :label="item.label" :value="item.value" v-for="(item,key) in bankType"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>流水号：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="flow" :rules="[{ required: true, message: '请输入流水号'}]">
                        <el-input placeholder="请输入" v-model="ruleForm.flow"  style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>付款时间：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="applyDate" :rules="[{ required: true, message: '请选择付款时间'}]">
                        <el-date-picker v-model="ruleForm.applyDate"  type="datetime" placeholder="选择时间" format="yyyy-MM-dd HH:mm"  style="width: 100%"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>付款给：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item :key="key" v-for="(item,key) in info">
                        <div>{{item.namebank}}</div>
                        <div>{{item.bank_card_id}}</div>
                        <div>{{item.bank_name_des}}</div>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">备注：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="tips">
                        <el-input v-model="ruleForm.tips" type="textarea" :rows="2" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>财务流水：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="radio">
                        <el-radio-group v-model="radio" @change="radiochange">
                            <el-radio :label="1">自动生成</el-radio>
                            <el-radio :label="2">不生成</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">上传凭证：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item >
                        <el-upload class="upload-demo" size="small" :action="BaseUrl" multiple name="files" :on-success="handlefileChange">
                            <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>
         <div slot="footer" class="dialog-footer">
             <el-button @click="isVisible('ruleForm')">取 消</el-button>
             <el-button type="primary" @click="OnsubmitForm('ruleForm',type)" :loading="loading">确 定</el-button>
         </div>
    </el-dialog>
    <el-dialog v-else-if="status==4 && type=='collection'" :title="title" :visible="isCollectPayment" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">打款人：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item :key="key" v-for="(item,key) in info">
                        <div>{{item.namebank}}</div>
                        <div>{{item.bank_card_id}}</div>
                        <div>{{item.bank_name_des}}</div>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>收款部门：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="department" :rules="[{ required: true, message: '请选择'}]">
                        <el-select v-model="ruleForm.department" filterable placeholder="请选择" style="width: 100%">
                            <el-option
                                    v-for="(item,key) in departments"
                                    :key="key"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>收款账号：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="bank" :rules="[{ required: true, message: '请选择'}]">
                        <el-select v-model="ruleForm.bank" filterable placeholder="请选择" style="width: 100%">
                            <el-option
                                    v-for="(item,key) in bank"
                                    :key="key"
                                    :label="item.name_short"
                                    :value="item.number+'/'+item.bank_name_des+'/'+item.name_short +'/'+ item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>收款类型：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="bankType" :rules="[{ required: true, message: '请选择'}]">
                        <el-select v-model="ruleForm.bankType" placeholder="请选择" style="width: 100%">
                            <el-option :key="key" :label="item.label" :value="item.value" v-for="(item,key) in bankType"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>流水号：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="flow" :rules="[{ required: true, message: '请输入流水号'}]">
                        <el-input placeholder="请输入" v-model="ruleForm.flow"  style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>收款时间：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="applyDate" :rules="[{ required: true, message: '请选择付款时间'}]">
                        <el-date-picker v-model="ruleForm.applyDate" type="datetime" placeholder="选择时间" format="yyyy-MM-dd HH:mm" style="width: 100%"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">备注：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="tips">
                        <el-input type="textarea" :rows="2" placeholder="请输入" v-model="ruleForm.tips" style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>财务流水：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item prop="radio">
                        <el-radio-group v-model="radio" @change="radiochange">
                            <el-radio :label="1">自动生成</el-radio>
                            <el-radio :label="2">不生成</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">上传凭证：</el-col>
                <el-col :xs="24" :sm="18" :md="20">
                    <el-form-item >
                        <el-upload class="upload-demo" size="small" :action="BaseUrl" multiple name="files"
                                   :before-upload="beforeUpload"
                                   :on-success="handlefileChange"
                                   :on-remove="handlefileRemove">
                            <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="OnsubmitForm('ruleForm',type)" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        name:'ConfirmModal',
        props:['apply_id','info','departments','bank','bankType','loading','title','type','isCollectPayment','status','bxtype'],
        data(){
            return{
                ruleForm:{
                    department:'',
                    bank:'',
                    bankType:'',
                    flow:'',
                    applyDate:'',
                },
                radio: 1,
                imgList:'',
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            hide(){
                return false;
            },
        },
        created(){
            //console.log(this.info);
        },
        methods:{
            radiochange(label){
                this.radio = label;
            },
            isVisible(formName){
                this.$emit('ToggleDialog');
                if(typeof(formName)!='function'){
                    this.$refs[formName].resetFields();
                }
            },
            handlefileChange(response, file, fileList){
                if (fileList.length > 5) {
                    message.error('上传文件数量已达上限!');
                } else{
                    this.fileList = fileList;
                }
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            beforeUpload(file){
                console.log(file.type);
                const isjpg = file.type === 'image/jpeg';
                const isgif = file.type === 'image/gif';
                const ispng = file.type === 'image/png';
                const isxls = file.type === 'application/vnd.ms-excel';
                const isdoc = file.type === 'application/msword';
                if(!isjpg && !isgif && !ispng && !isxls && !isdoc){
                    this.$message.error('上传文件格式错误，必须是jpg,gif,png,xls,xlsx,doc,docx中的一种!');
                }
                return isjpg || isgif || ispng || isxls || isdoc ;
            },
            OnsubmitForm(formName,type){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let files = '';
                        if(this.fileList != null){
                            files = (this.fileList).map(data => data.response.data[0]);
                        }
                        if(type != 'payment'){
                            let data ={
                                apply_id:this.apply_id,
                                org_id:this.ruleForm.department,
                                bank_card_id:this.ruleForm.bank.split("/")[0],
                                bank_name:this.ruleForm.bank.split("/")[2].split("-")[0],
                                account_id:this.ruleForm.bank.split("/")[3],
                                shou_kuan_id:this.ruleForm.flow,
                                create_cai_wu_log:this.radio,
                                shou_kuan_time:Date.parse(new Date(this.ruleForm.applyDate)),
                                type:this.ruleForm.bankType,
                                files:files,
                                tips:this.ruleForm.tips,

                            }
                            this.$emit("OnSubmitConfirmModal",data);
                            this.$refs[formName].resetFields();
                        }else{
                            let type = '';
                            if(this.bxtype == 1){
                                type = 2;
                            }else{
                                type = this.ruleForm.bankType;
                            }
                            let data ={
                                apply_id:this.apply_id,
                                org_id:this.ruleForm.department,
                                bank_card_id:this.ruleForm.bank.split("/")[0],
                                bank_name:this.ruleForm.bank.split("/")[2].split("-")[0],
                                account_id:this.ruleForm.bank.split("/")[3],
                                fu_kuan_id:this.ruleForm.flow,
                                create_cai_wu_log:this.radio,
                                fu_kuan_time:Date.parse(new Date(this.ruleForm.applyDate)),
                                type:type,
                                files:files,
                                tips:this.ruleForm.tips,
                            }
                            this.$emit("OnSubmitConfirmModal",data);
                            this.$refs[formName].resetFields();
                        }
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


