<template>
    <div class="container">
        <h3 class="mb-md"><a href="#">返回</a>&nbsp;|&nbsp;库存详情</h3>
        <el-row  class="details">
            <el-col :span="1" class="details_left">
                <div >
                    <img src="">
                </div>
            </el-col>
            <el-col :span="15" class="details_right">
                <el-row :gutter="10">
                    <el-col :span="24">
                        <el-row  class="title_1">
                            <el-col :span="4"  >{{details_table.asset_brand_name}}{{details_table.name}}</el-col>
                            <el-col :span="12">
                                (库存编号：<span class="title_1_1">{{details_table.stock_number}}</span>）
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="24">
                        <el-row class="title_2">
                            <el-col :span="5">类别：{{details_table.asset_type_name}}</el-col>
                            <el-col :span="5">资产编码：{{details_table.asset_number}}</el-col>
                          <!--  <el-col :span="5">SN: {{details_table.sn_number}}</el-col>-->


                            <el-col :span="8" class="edit">
                               <el-row>
                                   <el-col :span="2">SN:</el-col>
                                   <el-col :span="13" v-if="!edit_show">&nbsp;&nbsp;{{details_table.sn_number}}</el-col>
                                   <el-col :span="20" :offset="1" v-if="edit_show" class="edit_1">
                                       <el-row style="margin-bottom: 0px">
                                           <el-col  :span="16">
                                               <el-form :model="PopupForm" :rules="rules" ref="PopupForm"
                                                        class="demo-ruleForm" style="margin-top: -8px">
                                                   <el-form-item  prop="edit" >
                                                       <el-input class="edit_input" v-model="PopupForm.edit" size="mini"
                                                                 placeholder="请输入" ></el-input>
                                                   </el-form-item>
                                               </el-form>
                                           </el-col>
                                           <el-col  :span="3" :offset="1">
                                              <span   @click="showPopup('preservation','PopupForm')">保存</span>
                                           </el-col>
                                           <el-col  :span="3" :offset="1" >
                                               <span @click="showPopup('cancel')">取消</span>
                                           </el-col>
                                       </el-row>

                                   </el-col>
                                   <el-col :span="3" :offset="1" class="edit_2" v-if="!edit_show">
                                          <span @click="showPopup('edit')">编辑</span>

                                   </el-col>
                               </el-row>
                            </el-col>



                        </el-row>
                    </el-col>
                    <el-col :span="24">
                        <el-row class="title_2">
                            <el-col :span="5">品牌：{{details_table.asset_brand_name}}</el-col>
                            <el-col :span="5">采购价格：¥ {{details_table.price}}</el-col>
                            <el-col :span="8" >
                                <el-row>
                                    <el-col :span="5">当前状态：</el-col>
                                    <el-col :span="8" class="title_2_1">
                                        ● {{details_table.status_name}}
                                    </el-col>
                                </el-row>
                               </el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="24">
                        <el-row class="title_2">
                            <el-col :span="5">部门：--</el-col>
                            <el-col :span="5">使用人：--</el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        <h3 class="mb-md">关联信息</h3>
        <el-row class="relation">
            <el-col :span="5">
                关联请购单：<a href="#">--</a>
            </el-col>
            <el-col :span="5">
                关联需求单：<a href="#">--</a>
            </el-col>
            <el-col :span="5">
                关联报销单：<a href="#">--</a>
            </el-col>
        </el-row>
        <h3 class="mb-md">物品使用轨迹</h3>
        <el-row >
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">

                <el-table-column type="index" prop="id" label="序号" width="100"></el-table-column>
                <el-table-column prop="created_at" label="时间" ></el-table-column>
                <el-table-column prop="person_name" label="操作人" width="100"></el-table-column>
                <el-table-column prop="org" label="部门" ></el-table-column>
                <el-table-column prop="type" label="类别" width="100"></el-table-column>
                <el-table-column prop="des" label="说明"  ></el-table-column>

            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </el-row>
        <el-row class=" button_scrap_lose">
            <el-col :span="6" style="margin:0 0 10px 0" >
                <el-button type="primary" @click="showPopup('Scrap')" >报废</el-button>
                <el-button type="primary" @click="showPopup('Lose')">丢失</el-button>
            </el-col>
        </el-row>
        <!--弹窗-->
        <el-row >
            <transition name="fade">
                <el-col :span="24" class="Popup_background" v-show="Popup" transition="fade">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title" v-if="parameter=='edit'">确认已请购</div>
                            <div class="el-message-box__title" v-if="parameter=='Scrap'">报废</div>
                            <div class="el-message-box__title" v-if="parameter=='Lose'">丢失</div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn">
                                <i class="el-message-box__close el-icon-close" @click="hidePopup()"></i>
                            </button>
                        </div>
                        <div class="el-message-box__content">
                            <el-form :model="PopupForm" :rules="rules" ref="PopupForm"
                                     label-width="100px" class="demo-ruleForm">

                                <el-form-item label="报废说明:" prop="names" v-if="parameter=='Scrap'">
                                    <el-input v-model="PopupForm.names" type="textarea"
                                              :rows="3" placeholder="请输入备注"></el-input>
                                </el-form-item>
                                <el-form-item label="丢失说明:" prop="names" v-if="parameter=='Lose'">
                                    <el-input v-model="PopupForm.names" type="textarea"
                                              :rows="3" placeholder="请输入备注"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="hidePopup()">
                                <span>取消</span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary "
                                    @click="submitForm('PopupForm')">
                                <span>确定</span>
                            </button>
                        </div>
                    </div>

                </el-col>
            </transition>
        </el-row>
    </div>
</template>

<script>

    export default {
        name:'personnelClass',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                PopupForm:{
                    names:'',
                    edit:''
                },
                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//筛选
                keywords:'',
                start_time:'',
                end_time:'',
                type:1,
                Popup:false,
                parameter:'',
                id:0,
                item:'',
                radio3: '请购全部',
                details_table:'',
                asset_list_id:"",
                edit_show:false,
                rules: {
                    names:[
                        {required: true, message: '输入品牌类别', trigger: 'blur'}
                    ]
                }
            };
        },
        created(){
            this.$store.dispatch('AssetTrajectory_table',{page:1,page_size:10,asset_list_id:1}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.pages.currentPage;
                this.total = data.pages.totalCount;
            });
            this.$store.dispatch('InventoryDetails_table',{asset_list_id:1}).then(response=>{
                let data=response.data.data;
                this.details_table=data;
                console.log(this.details_table);

            });
            Date.prototype.Format = function (fmt) { //author: meizz
                var o = {
                    "M+": this.getMonth() + 1, //月份
                    "d+": this.getDate(), //日
                    "h+": this.getHours(), //小时
                    "m+": this.getMinutes(), //分
                    "s+": this.getSeconds(), //秒
                    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
                    "S": this.getMilliseconds() //毫秒
                };
                if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
                for (var k in o)
                    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
                return fmt;
            }
        },
        computed:{
            tableData(){
                return this.$store.state.InventoryList.assetTrajectory_table;
            },
        },
        methods:{
            showPopup(parameter,popupForm) {

                this.parameter=parameter;
                console.log("show值："+this.parameter)
                if(parameter == 'Scrap'){
                    this.Popup = true;
                    this.PopupForm.names='';
                  /*  this.asset_list_id=obj.id;*/
                }
                else if (this.parameter == 'Lose'){
                    this.Popup = true;
                    this.PopupForm.names='';
                   /* this.asset_list_id=obj.id;*/
                }
                else if (this.parameter == 'edit'){
                    this.edit_show = true;
                }
                else if (parameter == 'preservation'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let number=this.PopupForm.edit;
                            this.$store.dispatch('addSn_table',{sn_number:number,asset_list_id:1}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                    this.edit_show = false;
                }
                else if (parameter == 'cancel'){
                    this.edit_show = false;

                }
                else {
                    return false;
                }
            },
            hidePopup() {
                this.Popup = false;
                if(this.parameter == 'Lose'){
                    this.PopupForm.names='';
                    return false;
                }
                else if(this.parameter == 'Scrap'){
                    this.PopupForm.names='';
                }
                else {
                    return false;
                }
            },
            //弹窗
            submitForm(popupForm) {
                let para = this.parameter;
                //报废
                if( para=='Scrap'){
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let des=this.PopupForm.names;
                            this.$store.dispatch('ScrapLose_table',{des:des,status:3,asset_list_id:1}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //丢失
                else if (para=='Lose')
                {
                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let des=this.PopupForm.names;
                            this.$store.dispatch('ScrapLose_table',{des:des,status:4,asset_list_id:1}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                }
                //编辑SN号
              /*  else if (para=='preservation')
                {

                    this.$refs[popupForm].validate((valid) => {
                        if (valid) {
                            let number=this.PopupForm.edit;
                            this.$store.dispatch('addSn_table',{sn_number:number,asset_list_id:1}).then(response =>{
                                    if (response.data.code == 200) {
                                        this.ondispatch();
                                    }
                                }
                            );
                            this.Popup = false;
                        } else {
                            console.log('error submit!!');
                            return false;
                        }
                    });
                    this.edit_show = false;
                }*/
                else
                {
                    return false;
                }
            },
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    type:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time,
                    asset_list_id:1
                };
            },
            ondispatch(){
                this.$store.dispatch('DemandList_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        console.log(data);
                        this.currentpage = data.pages.currentPage;
                        this.total = data.pages.totalCount;
                        this.pageSize = data.pages.perPage;
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            Tolink(index,row) {
                //console.log(index, row);
                this.$router.push({ path: '/approvals/details' ,query:{apply_id:row.apply_id,type:row.type}});
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    body {
        font-family:"Microsoft Yahei";
        .details{
            .details_left{
                margin-right: 1%;
                border:1px solid #99a9bf;
                text-align: center;
              img{
                  width:60px;
                  height: 80px;
                  background:red;
              }
            }
            .details_right{
                    .title_1{
                        font-size: 16px;
                        font-weight: bold;
                        color: #48576a;
                        margin-bottom: 10px;
                        .title_1_1{
                            font-size: 12px;
                            font-weight: 300;
                        }
                    }
                    .title_2{
                        font-size: 14px;
                        color: rgba(31, 45, 61, 0.6);
                        margin-bottom: 8px;
                        .title_2_1{
                           color: rgb(75, 175, 69);
                        }
                        .edit{
                            .edit_1{
                                .el-form-item {
                                    margin-bottom: 0px;

                                }
                                span{
                                    display: block;
                                    cursor:pointer;
                                    color: #1d90e6;
                                }
                            }
                            .edit_2{
                                span{
                                    display: block;
                                    cursor:pointer;
                                    color: #1d90e6;
                                }

                            }
                        }
                    }
            }
        }
        .relation{
            font-size: 14px;
            color: rgba(31, 45, 61, 0.6);
            a {
                font-size: 14px;
            }
        }
        .button_scrap_lose{
            box-shadow: 0 -2px 0 rgba(72, 87, 106, 0.1);
            margin-top: 11px;
            padding-top: 12px;
        }
        .Popup_background{
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            padding-top: 16%;
            z-index: 10000;
            width:100%;
            height: 100%;
            text-align: center;
            overflow: auto;
            background: rgba(7,17,27,0.8);
        }
    }
</style>






