const getters = {
    sidebar: state => state.app.sidebar,
    isnav: state => state.app.isnav,
    name: state => state.userinfo.name,
    uid: state => state.userinfo.uid,
    roles:state => state.userinfo.roles,
    status: state => state.userinfo.status,
    permission_routers: state => state.permission.routers,
    addRouters: state => state.permission.addRouters,
    org_full_name:state => state.userinfo.org_full_name,
    curdate:state => state.global.curDate,
    cpPerson:state => state.global.cpPerson,
    Income_Expenditure:state => state.global.Income_Expenditure_data,
    assetclass_data:state => state.assetclass_data,
    BaseUrl:state => state.global.BaseUrl,
    serverUrl:state => state.global.serverUrl,
    pagenation:state => state.app.pagenation
};
export default getters


