<template>
    <div class="container">
        <h3 class="mb-md">收款确认</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-row>
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">关键字：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="keyword">
                                    <el-input v-model="ruleForm.keyword" placeholder="请输入"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :xs="24" :md="8">
                        <el-row :gutter="10">
                            <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">申请时间：</el-col>
                            <el-col :xs="24" :sm="20" :md="16">
                                <el-form-item prop="applyDate">
                                    <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="选择日期范围" style="width: 100%"></el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="24" class="t-r">
                        <el-button @click="onSearch('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isClear('ruleForm')">清除</el-button>
                    </el-col>
                </el-row>
            </el-row>
        </el-form>
        <el-row>
            <el-table :data="tableData" @sort-change="onsorter" @filter-change="onfilter" border style="width: 100%">
                <el-table-column prop="id" label="序号" width="65"></el-table-column>
                <el-table-column prop="create_time" label="申请时间" sortable="custom" width="140"></el-table-column>
                <el-table-column prop="apply_id" label="审批单编号"width="200"></el-table-column>
                <el-table-column prop="type_name" label="类型" width="98" filter-placement="bottom-end"></el-table-column>
                <el-table-column prop="title" label="标题"></el-table-column>
                <el-table-column prop="status" label="状态" width="100">
                    <template scope="scope">
                        <span>
                            {{scope.row.status == 99 ? '已确认':''}}
                            {{scope.row.status == 4 ? '待确认':''}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column prop="money" label="金额" width="150">
                    <template scope="scope">
                        <span>￥ {{scope.row.money}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="operation" label="操作" width="140">
                    <template scope="scope">
                        <el-button @click="Tolink(scope.$index,scope.row)" type="text" size="small">详情</el-button>
                        <el-button v-if="scope.row.status == 4" type="text" size="small" @click="Onconfirm(scope.$index,scope.row)">收款确认</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination v-if="pagenation == 1"
                           @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentpage"
                           :page-sizes="[10, 20, 30, 40]"
                           :page-size="pageSize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="total">
            </el-pagination>
            <el-pagination v-else small
                           @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentpage"
                           :page-sizes="[10, 20, 30, 40]"
                           :page-size="pageSize"
                           layout="prev, pager, next"
                           :total="total">
            </el-pagination>
        </el-row>
        <confirm-modal
                :title="confirm_title"
                :apply_id="apply_id"
                :info="confirm_info"
                :departments="confirm_department"
                :bank="confirm_bank"
                :bankType="confirm_bankType"
                :loading="confirm_loading"
                :type="confirm_type"
                :status="status"
                :isCollectPayment="isCollectPayment"
                @ToggleDialog="ToggleDialog"
                @OnSubmitConfirmModal="OnSubmitConfirmModal"
        ></confirm-modal>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import ConfirmModal from '../../components/Detailopration/confirmModal.vue'
    export default {
        components: {ConfirmModal},
        name:'make_collections',
        data(){
            return {
                ruleForm:{
                    keyword:'',
                    applyDate:''
                },
                currentpage:1,//当前页
                pageSize:10,//一页N天
                total:null,//总记录数
                sort:'',//排序
                filter:[],
                keyword:null,//关键词
                begin_time:'',//开始时间
                end_time:'',//结束时间

                //收付款确认
                apply_id:'',
                status:'',//审批状态
                confirm_info:[],
                confirm_department:[],
                confirm_bank:[],
                confirm_bankType:[],
                confirm_loading:false,
                confirm_type:'',
                isCollectPayment:false,
                confirm_title:'',
                data_type:'',
            }
        },
        created(){
            this.$store.dispatch('Make_collections_table',{page_size:10}).then(response=>{
                let data = response.data.data;
                if(data){
                    this.currentpage = data.pages.currentPage == null ? 1 : data.pages.currentPage;
                    this.total = data.pages.totalCount== null ? '1': data.pages.totalCount;
                    this.pageSize = data.pages.perPage;
                }else{
                    this.currentpage = 1;
                    this.total = 1;
                }
            });

        },
        computed:{
            ...mapGetters([
                'pagenation'
            ]),
            tableData(){
                return this.$store.state.make_collections.make_collections_table;
            },
        },
        methods:{
            searchData(value){
                if(value != null){
                    this.currentpage = value;
                }
                return {
                    page:this.currentpage,
                    page_size:this.pageSize,
                    sort:this.sort,
                    type:this.filter,
                    keyword:this.keyword,
                    begin_time:this.start_time,
                    end_time:this.end_time
                };
            },
            ondispatch(){
                this.$store.dispatch('Make_collections_table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        if(data){
                            this.currentpage = data.pages.currentPage == null ? 1 : data.pages.currentPage;
                            this.total = data.pages.totalCount== null ? '1': data.pages.totalCount;
                            this.pageSize = data.pages.perPage;
                        }else{
                            this.currentpage = 1;
                            this.total = 1;
                        }
                    }
                );
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            Tolink(index,row) {
                //console.log(row);
                let type = '';
                switch (row.type_name){
                    case '退还备用金':
                        type = 3;
                        break;
                }
                this.$router.push({path: '/approvals/confirm' ,query:{apply_id:row.apply_id,type:type}});
            },
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keyword ='';
                this.start_time='';
                this.end_time='';
                this.ondispatch();
            },
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keyword = this.ruleForm.keyword;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },
            onsorter({column,prop,order}){
                if( order == 'ascending' ) {
                    this.sort = 'asc';
                }else if(order == 'descending'){
                    this.sort = 'desc';
                }else{
                    this.sort = ''
                }
                this.ondispatch();
            },
            onfilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },
            ToggleDialog(){
                this.isCollectPayment = false;
            },
            Onconfirm(index,row){
                let stop = row.title.indexOf('的');
                this.confirm_loading = false;
                this.isCollectPayment = true;
                this.apply_id = row.apply_id;
                this.status = row.status;
                switch (row.type_name){
                    case '申请报销':
                        this.type = 1;
                        break;
                    case '申请借款':
                        this.type = 2;
                        break;
                    case '退还备用金':
                        this.type = 3;
                        break;

                }

                let data_type ='';
                if(this.type == 1 || this.type == 2 || this.type == 4 || this.type == 5){
                    this.confirm_type = 'payment';
                    this.confirm_title = '付款确认';
                    data_type='Payment_confirm_init';
                }else if(this.type == 3){
                    this.confirm_type = 'collection';
                    this.confirm_title = '收款确认';
                    data_type='Collection_confirm_init';
                }
                this.$store.dispatch(data_type,{apply_id:this.apply_id}).then(response=>{
                    let data = response.data.data;
                    let {entries} = Object;
                    let org = [];
                    for (let [key,value] of entries(data.pay_org)) {
                        org.push([key,value]);
                    }
                    for (let item in org){
                        this.confirm_department.push({label:org[item][1],value:org[item][0]});
                    }
                    this.confirm_bank = data.pay_bank;
                    this.confirm_bankType = data.tags;
                    this.confirm_info = [{namebank:row.title.substring(0,stop)+" "+data.bank_name,bank_card_id:data.bank_card_id,bank_name_des:data.bank_name_des}];
                })
            },
            OnSubmitConfirmModal(data){
                this.confirm_loading = true;
                let data_type ='';
                if(this.type == 1 || this.type == 2 || this.type == 4 || this.type == 5){
                    data_type='Payment_confirm_form';
                }else if(this.type == 3){
                    data_type='Collection_confirm_form';
                }
                return new Promise((resolve, reject) => {
                    this.$store.dispatch(data_type,data).then(response => {
                        if (response.data.code == 200) {
                            this.confirm_loading = false;
                            this.isCollectPayment = false;
                            this.$message({
                                message: '确认成功!',
                                type: 'success',
                                duration:1000,
                                onClose:()=>{
                                    if(data_type=="Collection_confirm_form"){
                                        this.ondispatch()
                                        //this.$router.push({path: '/finance/make_collections' });
                                    }else{
                                        this.$router.push({path: '/finance/payment' });
                                    }
                                }
                            });
                        }
                    }).catch(error => {
                        reject(error);
                    });
                });
            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .search-wrap{
        padding: 15px;
        background: #fbfbfb;
        border: 1px solid #d9d9d9;
        border-radius: 6px;
    }
</style>



