		},
        beforeCreate(){
		    let isnav = this.$route.query.isnav == null ? 1:this.$route.query.isnav;
            let pagenation = this.$route.query.pagenation == null ? 1:this.$route.query.pagenation;
		        localStorage.isnav = isnav;
            	this.$store.dispatch('SetIsnav',isnav);
            	if(document.body.clientWidth <= 768){
