import fetch from '../utils/fetch';
import qs from 'qs';

export function Assets_type() {
    return fetch({
        url: '/oa_v1/basic/get-type-all',
        method: 'get',
    });
}

export function Brand_type_data(params) {
    return fetch({
        url: '/oa_v1/basic/get-brand-list',
        method: 'get',
        params:params
    });
}

export function Purchase_Create(params) {
    return fetch({
        url: '/oa_v1/apply-buy/index',
        method: 'post',
        data:qs.stringify(params)
    });
}


