import { Approval_list } from '../../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const waitmeaproval = {
    state: {
        waitmeapproval_table:[],//表数据
    },
    mutations: {
        SET_waitmeapproval_table: (state, data) => {
            state.waitmeapproval_table = data;
        },
        /*SET_waitmeapproval_pagenation: (state, data) => {
            state.waitmeapproval_pagenation = data;
        },
        SET_waitmeapproval_search_data: (state, data) => {
            state.waitmeapproval_search_data = data;
        },
        SET_waitmeapproval_sortfilter_data: (state, data) => {
            state.waitmeapproval_sortfilter_data = data;
        }*/
    },
    actions: {
        //表格数据
        WaitmeApproval_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Approval_list(data).then(response => {
                    let data = response.data.data;
                    /*let pagenation = {
                            currentpage:data.page.currentPage,
                            total:data.page.totalCount,
                            pageSize:data.page.perPage,
                        };
                    let searchData = {
                            keywords:data.keywords,
                            applyDate:data.applyDate
                        }
                    let sortfilterData = {
                            sort:data.sort,
                            filter:data.filter
                        }*/

                    commit('SET_waitmeapproval_table',response.data.data.res);
                    /*commit('SET_waitmeapproval_pagenation',pagenation);
                    commit('SET_waitmeapproval_search_data',searchData);
                    commit('SET_waitmeapproval_sortfilter_data',sortfilterData);*/
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
    }
}

export default waitmeaproval;


