<template>
    <el-dialog title="增加报销明细" class="constom-md-dialog" :visible.sync="istableshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleform" :rules="rules2" ref="ruleform">
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>报销金额：</el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="money">
                        <el-input v-model="ruleform.money" auto-complete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>报销类型：</el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="bxType">
                        <el-cascader placeholder="请选择" :options="constType" filterable v-model="ruleform.bxType" style="width:100%"></el-cascader>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm">费用明细：</el-col>
                <el-col :xs="24" :sm="18">
                    <el-form-item prop="des">
                        <el-input type="textarea" v-model="ruleform.des" style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleform')">取 消</el-button>
            <el-button @click="onsubmitform('ruleform')" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import { mapState } from 'vuex'
    import ElCol from "element-ui/packages/col/src/col";
    export default {
        components: {ElCol},
        name:'TableModal',
        props:['istableshow','constType'],
        data(){
            var checkmoney = (rule, value, callback) => {
                var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
                if (!value) {
                    return callback(new Error('报销金额不能为空'));
                }else{
                    if (!reg.test(value)) {
                        callback(new Error('金额格式错误'));
                    } else {
                        callback();
                    }
                };
            };
            return {
                ruleform: {
                    money: '',
                    bxType: [],
                    des: '',
                },
                rules2: {
                    money: [
                        { validator: checkmoney, trigger: 'blur' }
                    ],
                    bxType: [
                        { required: true, message: '请选择' }
                    ],
                },
                hide:false,
                loading:false
            }
        },
        computed: {

        },
        created(){
            if(this.constType == null){
                this.$store.dispatch('ConstTypeInit');
            }
        },
        methods: {
            isVisible(formName){
                this.$emit('ToggleTableDialog',{istableshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let data = this.ruleform;
                        this.loading = true;
                        this.$store.dispatch('Add_reimburse_tablesubmit',{tabledata:data,constType:this.constType})
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleTableDialog',{istableshow:false});
                        },300);
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


