<template>
    <div class="container">
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :leave_time="leave_time"
                :leave_date="leave_date"
                :job="profession"
                :stock_status="stock_status"
                :finance_status="finance_status"
                :account_status="account_status"
                :work_status="work_status"
                :stock_list="stock_list"
                :finance_list="finance_list"
                :des="des"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></SubmitModal>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">离职申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>离职日期：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10" >
                    <el-form-item prop="leave_time" :rules="[{ required: true, message: '请选择离职日期'}]">
                        <el-date-picker v-model="ruleForm.leave_time" type="date" placeholder="选择日期" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>离职原因：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10" >
                    <el-form-item prop="des" :rules="[{ required: true, message: '请输入离职原因'}]">
                        <el-input type="textarea" v-model="ruleForm.des" placeholder="请输入离职原因" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <h3 class="mb-md">离职交接</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w120 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>物资是否交还：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="stock_status" :rules="[{ required: true, message: '请确认物资是否交还'}]">
                        <el-select v-model="ruleForm.stock_status" placeholder="请选择" style="width: 100%;">
                            <el-option key="0" label="是" value="1"></el-option>
                            <el-option key="1" label="否" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="0">
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <el-table :data="stock_list" border class="mb-md">
                        <el-table-column prop="type" label="类别"></el-table-column>
                        <el-table-column prop="sn" label="库存编号" width="240"></el-table-column>
                        <el-table-column prop="brand" label="品牌" width="120"></el-table-column>
                        <el-table-column prop="name" label="名称" width="132"></el-table-column>
                        <el-table-column prop="price" label="价格" width="120"></el-table-column>
                        <el-table-column prop="status" label="状态" width="100"></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w150 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>账号密码是否交接：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="finance_status" :rules="[{ required: true, message: '请确认财务是否结算'}]">
                        <el-select v-model="ruleForm.finance_status" placeholder="请选择" style="width: 100%;">
                            <el-option key="0" label="是" value="1"></el-option>
                            <el-option key="1" label="否" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="0">
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <el-table :data="finance_list" border class="mb-md">
                        <el-table-column prop="id" label="序号" width="100"></el-table-column>
                        <el-table-column prop="time" label="借款时间" width="160"></el-table-column>
                        <el-table-column prop="des" label="借款事由"></el-table-column>
                        <el-table-column prop="name" label="借款金额" width="252"></el-table-column>
                        <el-table-column prop="status" label="状态" width="100"></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w150 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>账号密码是否交接：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="account_status" :rules="[{ required: true, message: '请确认账号密码是否交接'}]">
                        <el-select v-model="ruleForm.account_status" placeholder="请选择" style="width: 100%;">
                            <el-option key="0" label="是" value="1"></el-option>
                            <el-option key="1" label="否" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="0">
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <table cellspacing="0" cellpadding="0" border="0" class="el-table__body" width="100%">
                        <tbody>
                        <tr class="el-table__row">
                            <td class="b_gray" width="100">企业QQ</td>
                            <td class="t-l">{{qq}}</td>
                            <td class="b_gray" width="100">企业邮箱</td>
                            <td class="t-l">{{email}}</td>
                            <td class="b_gray" width="100">工作手机号</td>
                            <td class="t-l">{{tel}}</td>
                        </tr>
                        </tbody>
                    </table>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w150 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>部门工作是否交接：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item prop="work_status" :rules="[{ required: true, message: '请确认部门工作是否交接'}]">
                        <el-select v-model="ruleForm.work_status" placeholder="请选择" style="width: 100%">
                            <el-option key="0" label="是" value="1"></el-option>
                            <el-option key="1" label="否" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item>
                        <el-upload class="upload-demo" multiple name="files" :action="BaseUrl"
                                   :on-success="handlefileChange" :on-remove="handlefileRemove">
                            <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>
            <h3>审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item>
                        <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata"
                                           @ToggleConstDialog="ToggleConstDialog"/>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">抄送人：</el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item>
                        <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata"
                                           @ToggleCopyDialog="ToggleCopyDialog"/>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w150 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>部门工作是否交接：
                </el-col>
                <el-col :xs="24" :sm="12" :md="10">
                    <el-form-item label="">
                        <el-button @click="onsubmitCheck('ruleForm')" type="primary">确 定</el-button>
                        <el-button @click="isVisible('ruleForm')">取 消</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex'
    import {DateTime} from '../../components/common.js'
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import SubmitModal from '../../components/applyleave/submitModal.vue';
    export default {
        name:'applyleave',
        components:{
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    leave_time: '',
                    des: '',
                    stock_status: '',
                    finance_status: '',
                    account_status: '',
                    work_status: '',
                },
                stock_list: [],
                finance_list: [],
                qq: '',
                email: '',
                tel: '',
                leave_time:'',
                leave_date: '',
                des: '',
                stock_status: '',
                finance_status: '',
                account_status: '',
                work_status: '',
                fileList: [],
                loading:false,
                isconstshow:false,
                iscopyshow:false,
                issubmitshow:false
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.applyleave.applyleave_addApplyID;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            constdata(){
                return this.$store.state.applyleave.applyleave_constdata;
            },
            copydata(){
                return this.$store.state.applyleave.applyleave_copydata;
            },
            profession(){
                return this.$store.state.userinfo.profession;
            }
        },
        created(){
            this.$store.dispatch('Handover_list').then( response => {
                this.loading = false;
                this.stock_list = response.data.data.stock_list;
                this.finance_list = response.data.data.finance_list;
                this.qq = response.data.data.qq;
                this.email = response.data.data.email;
                this.tel = response.data.data.tel;
            }).catch(err => {
                this.loading = false;
            });
        },
        methods: {
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_applyleave_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_applyleave_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_applyleave_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_applyleave_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },

            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },


            Onsubmit(){
                this.loading=true;
                let constdata = this.$store.state.applyleave.applyleave_constdata;
                let resultconstdata = [],resulcopydata = [],attachments=[];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.applyleave.applyleave_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }

                if(this.fileList){
                     attachments = (this.fileList).map(data => data.response.data[0]);
                }

                let data = {
                    apply_id:this.ApplyID,
                    leave_time:this.leave_time,
                    stock_status:this.ruleForm.stock_status,
                    finance_status:this.ruleForm.finance_status,
                    account_status:this.ruleForm.account_status,
                    work_status:this.ruleForm.work_status,
                    des:this.ruleForm.des,
                    files:attachments,
                    approval_persons:resultconstdata,
                    copy_person:resulcopydata,
                };

                this.$store.dispatch('applyleave_Add',data).then( response => {
                    this.loading = false;
                    this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:11}});
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitCheck(formName){
                let constdata = this.$store.state.applyleave.applyleave_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (constdata.length > 0) {
                            this.issubmitshow = true;
                            this.des = this.ruleForm.des;
                            this.leave_time = this.ruleForm.leave_time.Format("yyyy-MM-dd");
                            this.leave_date = this.ruleForm.leave_time.Format("yyyy年M月d日");
                            this.stock_status = this.ruleForm.stock_status;
                            this.finance_status = this.ruleForm.finance_status;
                            this.account_status = this.ruleForm.account_status;
                            this.work_status = this.ruleForm.work_status;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    table {
        width: 100%;
        border-collapse: collapse;
        border: 0;
        background: white;
        td {
            border: 1px solid rgba(204, 204, 204, 1);
            padding: 8px;
            line-height: 23px;
            font-size: 12px;

            &
            .b_gray {
                background: #F2F2F2;
            }
        }
    }
    a{
        text-decoration: none;
        color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }
</style>


