import { Approval_list } from '../../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const alreadyapproval = {
    state: {
        alreadyapproval_table:[],//表数据
    },
    mutations: {
        SET_alreadyapproval_table: (state, data) => {
            state.alreadyapproval_table = data;
        },
    },
    actions: {
        //表格数据
        AlreadyApproval_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Approval_list(data).then(response => {
                    let data = response.data.data;
                    //console.log(data.res);
                    commit('SET_alreadyapproval_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
    }
}

export default alreadyapproval;


