import { Make_Collections_list } from '../../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const make_collections = {
    state: {
        make_collections_table:[],//表数据
    },
    mutations: {
        SET_make_collections_table: (state, data) => {
            state.make_collections_table = data;
        },
    },
    actions: {
        //表格数据
        Make_collections_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Make_Collections_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_make_collections_table',data.data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
    }
}

export default make_collections;


