import { constType } from '../../../api/reimburse';
import { constPerson,Card,AddCard,Department,AddApplyID,applyloan_Create } from '../../../api/public';
import Cookies from 'js-cookie';

const applyloan = {
    state: {
        applyloan_constdata:[],//审批人
        applyloan_copydata:[],//抄送人
        applyloan_addApplyID:null,//报销单ID
    },
    mutations: {
        SET_applyloan_constdata: (state, data) => {
            if(typeof(data) == "string") {
                (state.applyloan_constdata).push(data);
            }else{
                state.applyloan_constdata = data;
            }
        },
        SET_applyloan_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.applyloan_copydata).push(data);
            }else{
                state.applyloan_copydata = data;
            }
        },
        SET_applyloan_addApplyID: (state, data) => {
            state.applyloan_addApplyID = data;
        }
    },
    actions: {
        //添加审批人
        Add_applyloan_constsubmit:({ commit },data) =>{
            commit('SET_applyloan_constdata',data.consttext);
        },
        Delete_applyloan_ConstPerson:({ commit },data) =>{
            commit('SET_applyloan_constdata',data);
        },

        //添加抄送人
        Add_applyloan_copysubmit:({ commit },data) =>{
            commit('SET_applyloan_copydata',data.consttext);
        },
        Delete_applyloan_CopyPerson:({ commit },data) =>{
            commit('SET_applyloan_copydata',data);
        },

        //获取addApplyID
        Applyloan_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID({type:data.type}).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_applyloan_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //提交报销单
        Applyloan_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                applyloan_Create(data).then(response => {
                    commit('SET_applyloan_constdata',[]);
                    commit('SET_applyloan_copydata',[]);
                    commit('SET_applyloan_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}

export default applyloan;


