import { AssetClass_map,AssetClass_add,AssetClass_list,AssetClass_update,AssetAdd_child } from '../../../api/basic';
/*import Cookies from 'js-cookie';*/

const assetClass = {
    state: {
        assetclass_table:[],//表数据
        assetChild_table:[],//子表数据

    },
    mutations: {
        SET_assetclass_table: (state, data) => {
            state.assetclass_table = data;
        },
        SET_assetChild_table: (state, data) => {
            state.assetChild_table.splice(data[1]-1,1);
            state.assetChild_table.splice(data[1]-1,0,data[0]);
            console.log(state.assetChild_table);
        },
    },
    actions: {
            //获取数据列表
            Assetclass_table:({ commit },data) =>{
                return new Promise((resolve, reject) => {
                    AssetClass_map(data).then(response => {
                        let data = response.data.data;
                        commit('SET_assetclass_table',data.res);
                        resolve(response);
                    }).catch(error => {
                        reject(error);
                    });
                });
            },//获取子数据列表
            AssetChild_table:({ commit },data) =>{
                let index = data.index;
                return new Promise((resolve, reject) => {
                    AssetClass_map({pid:data.pid,page_size:data.page_size}).then(response => {
                        let data = response.data.data;
                        commit('SET_assetChild_table',[data.res,index]);
                        resolve(response);
                    }).catch(error => {
                        reject(error);
                    });
                });
            },
            //添加数据类型
            AssetclassAdd_table:({ commit },data) =>{
                return new Promise((resolve, reject) => {
                    AssetClass_add(data).then(response => {
                        resolve(response);
                    }).catch(error => {
                        reject(error);
                    });
                });
            },
            //编辑数据类型
            AssetclassUpdate_table:({ commit },data) =>{
                return new Promise((resolve, reject) => {
                    AssetClass_update(data).then(response => {
                        resolve(response);
                    }).catch(error => {
                        reject(error);
                    });
                });
            },
            //添加资产子类别
            AssetAddChild_table:({ commit },data) =>{
                return new Promise((resolve, reject) => {
                    AssetAdd_child (data).then(response => {
                        resolve(response);
                    }).catch(error => {
                        reject(error);
                    });
                });
            } ,
           //资产类别详情
            AssetList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                AssetClass_list (data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
};


export default assetClass;


