import { Forpayment_Create,AddApplyID } from '../../../api/public';

const forpayment = {
    state: {
        forpayment_constdata: [],//已选中审批人
        forpayment_copydata: [],//已选中抄送人
        forpayment_addApplyID: null,//报销单ID
    },
    mutations: {
        SET_forpayment_constdata: (state, data) => {
            if(typeof(data) == "string") {
                state.forpayment_constdata.push(data);
            }else{
                state.forpayment_constdata = data;
            }
        },
        SET_forpayment_copydata: (state, data) => {
            if(typeof(data) == "string") {
                (state.forpayment_copydata).push(data);
            }else{
                state.forpayment_copydata = data;
            }
        },
        SET_forpayment_addApplyID: (state, data) => {
            state.forpayment_addApplyID = data;
        }
    },
    actions: {
        //添加审批人
        Add_forpayment_constsubmit:({ commit },data) =>{
            commit('SET_forpayment_constdata',data.consttext);
        },
        Delete_forpayment_ConstPerson:({ commit },data) =>{
            commit('SET_forpayment_constdata',data);
        },

        //添加抄送人
        Add_forpayment_copysubmit:({ commit },data) =>{
            commit('SET_forpayment_copydata',data.consttext);
        },
        Delete_forpayment_CopyPerson:({ commit },data) =>{
            commit('SET_forpayment_copydata',data);
        },

        //获取addApplyID
        Forpayment_AddApplyID:({ commit },data) => {
            return new Promise((resolve, reject) => {
                AddApplyID(data).then(response => {
                    let apply_id = response.data.data.apply_id;
                    commit('SET_forpayment_addApplyID',apply_id);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

        //添加转正申请
        Forpayment_Add:({commit},data)=>{
            return new Promise((resolve, reject) => {
                Forpayment_Create(data).then(response => {
                    commit('SET_forpayment_constdata',[]);
                    commit('SET_forpayment_copydata',[]);
                    commit('SET_forpayment_addApplyID','null');
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        }
    }
}
export  default  forpayment;


