import { logout, getInfo } from '../../../api/public';
import Cookies from 'js-cookie';

const userinfo = {
    state: {
        uid: undefined,
        name: '',
        status: '',
        roles: [],
        role_info: [],
        org_full_name:'',
        profession: '',
        email: ''
    },
    mutations: {
        SET_UID: (state, uid) => {
            state.uid = uid;
        },
        SET_STATUS: (state, status) => {
            state.status = status;
        },
        SET_NAME: (state, name) => {
            state.name = name;
        },
        SET_ROLES: (state, roles) => {
            state.roles = roles;
        },
        SET_ROLE_INFO: (state, role_info) => {
            state.role_info = role_info;
        },
        SET_org_full_name: (state, org_full_name) => {
            state.org_full_name = org_full_name;
        },
        SET_profession: (state, profession) => {
            state.profession = profession;
        },
        SET_email: (state, email) => {
            state.email = email;
        }
    },
    actions: {
        // 登出
        LoginOut({commit, state}) {
            return new Promise((resolve, reject) => {
                logout().then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        // 获取用户信息
        GetInfo({ commit, state }) {
            return new Promise((resolve, reject) => {
                getInfo().then(response => {
                    const data = response.data;
                    if(data.code==200){
                        commit('SET_ROLES', [data.data.roleName]);
                        commit('SET_ROLE_INFO', data.data.roleInfo);
                        commit('SET_NAME', data.data.userinfo.person_name);
                        commit('SET_UID', data.data.userinfo.person_id);
                        commit('SET_org_full_name',data.data.userinfo.org_full_name);
                        resolve(response);
                    }else if(data.code==401){
                        location.href = data.data.login_url;
                    }

                }).catch(error => {
                    reject(error);
                });
            });
        },

    }
}

export default userinfo;


