<template>
    <div class="app-wrapper" :class="{'hideSidebar':!sidebar.opened}">
        <div class="sidebar-wrapper" v-if="isnav==1">
            <Sidebar class="sidebar-container"/>
        </div>
        <div class="main-container" :class="{'app-container':isnav == 0}">
            <Headbar/>
            <app-main/>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import Headbar from './Headbar.vue';
    import Sidebar from './Sidebar.vue';
    import AppMain from './AppMain.vue';
    export default {
        name: 'layout',
        components: {
            Headbar,
            AppMain,
            Sidebar,
        },
        data(){
            return{
                isshow:true
            }
        },
        computed: {
            ...mapGetters([
                'isnav'
            ]),
            sidebar() {
                //console.log(this.$store.state.app.sidebar.opened);
                return this.$store.state.app.sidebar;
            }
        },
        created(){
            this.$store.dispatch('Setsidebar');
            this.$store.dispatch('Setpagenation',document.body.clientWidth);
        },
        mounted(){
            /*window.onresize = ()=>{
                this.$store.dispatch('Setsidebar');
                this.$store.dispatch('Setpagenation',document.body.clientWidth);
            }*/
        },
    }
</script>

<style rel="stylesheet/less" lang="less" scoped>

    .app-wrapper {
        position: relative;
        height: 100%;
        width: 100%;
        &.hideSidebar {
            .sidebar-wrapper {
                transform: translate(-246px, 0);
                .sidebar-container {
                    transform: translate(198px, 0);
                }
                &:hover {
                    transform: translate(0, 0);
                    .sidebar-container {
                        transform: translate(0, 0);
                    }
                }
            }
            .main-container{
                margin-left: 0px;
            }
        }
        .sidebar-wrapper {
            width: 246px;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            z-index: 1001;
            overflow-x: hidden;
            transition: all .28s ease-out;
            @include scrollBar;
            .el-submenu__title{
                color: #bfcbd9;
            }
        }
        .sidebar-container {
            transition: all .28s ease-out;
        }
        .main-container {
            min-height: 100%;
            transition: all .28s ease-out;
            margin-left: 246px;
            &.app-container{
                margin-left: 0 !important;
            }
        }
    }
    @media(max-width: 768px){
        .app-wrapper {
            &.hideSidebar {
                .main-container{
                    transform: translate(-246px, 0);
                }
            }
        }
        .main-container {
            position: relative;
            margin-left: 0 !important;
            left: 246px;
        }
        .main-container.app-container{
            left: 0px;
        }
    }
</style>


