<template>
    <div class="container">
        <TableModal
                :istableshow="istableshow"
                :constType="constType"
                @ToggleTableDialog="ToggleTableDialog"
        ></TableModal>
        <ConstModal
                :isconstshow="isconstshow"
                :constdata="constdata"
                :cpperson="cpperson"
                :selectloading="selectloading"
                @Onconstsubmit="Onconstsubmit"
                @ToggleConstDialog="ToggleConstDialog"
                @DeleteConstPerson="DeleteConstPerson"
        ></ConstModal>
        <CopyModal
                :iscopyshow="iscopyshow"
                :copydata="copydata"
                :cpperson="cpperson"
                @Oncopysubmit="Oncopysubmit"
                @ToggleCopyDialog="ToggleCopyDialog"
                @DeleteCopyPerson="DeleteCopyPerson"
        ></CopyModal>
        <CardModal :iscardshow="iscardshow"
                   @ToggleCardDialog="ToggleCardDialog"
        ></CardModal>
        <SubmitModal
                :issubmitshow="issubmitshow"
                :loading="loading"
                :bank="bank"
                :files="fileList"
                @Onsubmit="Onsubmit"
                @TogglesubmitDialog="TogglesubmitDialog"
        ></SubmitModal>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <h3 class="mb-md">报销申请表</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>报销明细：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <el-table :data="tableData" border :summary-method="getSummaries" show-summary
                              style="max-width:681px;" class="mb-lg">
                        <el-table-column type="index" label="序号" width="120"></el-table-column>
                        <el-table-column prop="money" label="报销金额" width="120">
                            <template scope="scope">
                                ￥ {{ scope.row.money }}
                            </template>
                        </el-table-column>
                        <el-table-column prop="type_name" label="报销类别" width="120"></el-table-column>
                        <el-table-column prop="des" label="费用明细" width="200"></el-table-column>
                        <el-table-column prop="option" label="操作" width="120">
                            <template scope="scope">
                                <el-button @click.native.prevent="deleteRow(scope.$index,tableData)" type="text" size="small">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-button type="primary" size="small" @click="ToggleTableDialog({istableshow:true})">增加报销明细
                    </el-button>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">
                    <span class="c-red mr4">*</span>报销到：
                </el-col>
                <el-col :xs="24" :sm="10" class="card-wrap">
                    <el-form-item prop="code" :rules="[{ required: true, message: '请选择银行卡'}]">
                        <el-select v-model="ruleForm.code" placeholder="请选择" style="width: 100%;">
                            <el-option :key="item.value" :label="item.label" :value="item.value"
                                       v-for="item in carddatas"></el-option>
                        </el-select>
                        <a class="f12 addcard" href="javascript:;"
                           @click="ToggleCardDialog({iscardshow:true})">增加银行卡</a>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l">上传附件：</el-col>
                <el-col :xs="24" :sm="20" class="upload-wrap">
                    <el-upload class="upload-demo f12" multiple name="files"
                               :file-list="fileList"
                               :action="BaseUrl" method="post"
                               :on-error="errorchange"
                               :on-success="handlefileChange"
                               :on-remove="handlefileRemove">
                        <el-button size="small"><i class="el-icon-upload2"></i> 点击上传</el-button>
                    </el-upload>
                    <div class="modal-download f12 mt-sm">
                        报销差旅费和接待费请上传报销明细。
                        <a href="/template/车城体系财务模板.xlsx">模板下载</a>
                    </div>
                </el-col>
            </el-row>
            <h3 class="mt-lg mb-md">审批人和抄送人</h3>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10">
                    <span class="c-red mr4">*</span>审批人：
                </el-col>
                <el-col :xs="24" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="true" :choicetype="1" :data="constdata" @ToggleConstDialog="ToggleConstDialog"></const-copy-person>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10">
                    抄送人：
                </el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <const-copy-person :isapplyright="false" :choicetype="2" :data="copydata"
                                       @ToggleCopyDialog="ToggleCopyDialog"></const-copy-person>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l mt-10"></el-col>
                <el-col :xs="20" :sm="20" class="mb-lg">
                    <el-button @click="onsubmitReimburse('ruleForm')" type="primary">确 定</el-button>
                    <el-button @click="isVisible('ruleForm')">取 消</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import TableModal from '../../components/reimburse/TableModal.vue';
    import ConstCopyPerson from '../../components/ConstCopyPerson.vue';
    import ConstModal from '../../components/ConstModal/ConstModal.vue';
    import CopyModal from '../../components/CopyModal/CopyModal.vue';
    import CardModal from '../../components/CardModal/CardModal.vue';
    import SubmitModal from '../../components/reimburse/submitModal.vue';
    import ElCol from "element-ui/packages/col/src/col";
    export default {
        name:'reimburse',
        components:{
            ElCol,
            TableModal,
            ConstCopyPerson,
            ConstModal,
            CopyModal,
            CardModal,
            SubmitModal
        },
        data() {
            return {
                ruleForm: {
                    code: '',
                    files: ''
                },
                bank:null,
                fileList: [],
                loading:false,
                istableshow:false,
                isconstshow:false,
                iscopyshow:false,
                iscardshow:false,
                issubmitshow:false,
                selectloading:true,
                counter:0
            }
        },
        computed:{
            ...mapGetters([
                'BaseUrl'
            ]),
            ApplyID(){
                return this.$store.state.reimburse.reimburse_addApplyID;
            },
            constType(){
                return this.$store.state.global.constType;
            },
            cpperson(){
                return this.$store.state.global.cpPerson;
            },
            tableData(){
                return this.$store.state.reimburse.reimburse_tabledata;
            },
            constdata(){
                return this.$store.state.reimburse.reimburse_constdata;
            },
            copydata(){
                return this.$store.state.reimburse.reimburse_copydata;
            },
            carddatas(){
                return this.$store.state.global.bankCard;
            },
        },
        created(){
            this.$store.dispatch('InitCard');
        },
        methods: {
            isVisible(){
                this.$router.push({path:'/'});
            },
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '合计';
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                     if(index === 1){
                        if (!values.every(value => isNaN(value))) {
                            sums[index] = values.reduce((prev, curr) => {
                                const value = Number(curr);
                                if (!isNaN(value)) {
                                    let pcvalue = prev*1 + curr*1;
                                    return pcvalue.toFixed(2);
                                } else {
                                    return (prev).toFixed(2);
                                }
                            }, 0);
                            sums[index] = '￥ ' + sums[index];
                        } else {
                            sums[index] = '';
                        }
                    }
                });
                return sums;
            },
            ToggleTableDialog(data){//明细添加弹窗
                this.istableshow = data.istableshow;
            },
            deleteRow(index, rows) {
                rows.splice(index,1);
                this.$store.dispatch('Deleterows',rows);
            },
            handlefileChange(response, file, fileList){
                this.counter = this.counter + 1;
                if(file.response.code ==1012){
                    this.$message.error('文件格式错误，必须是jpg,gif,png,pdf,xls,xlsx,doc,docx中的一种!');
                    this.fileList.splice(this.counter-1,1);
                    this.counter = this.counter -1;
                }else {
                    if (this.counter > 14) {
                        this.$message.error('上传文件数量已达上限!');
                        this.fileList = fileList.slice(0,14);
                        this.counter = this.fileList.length;
                    }else{
                        this.fileList = fileList;
                    }
                }
            },
            errorchange(err,file,fileList){
                this.$message(err);
            },
            beforeUpload(file){
                console.log(file.type);
                const isjpg = file.type === 'image/jpeg';
                const isgif = file.type === 'image/gif';
                const ispng = file.type === 'image/png';
                const isxls = file.type === 'application/vnd.ms-excel';
                const isxlsx = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                const isdoc = file.type === 'application/msword';
                const isdocx = file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
                if(!isjpg && !isgif && !ispng && !isxls && !isdoc && !isxlsx && !isdocx){
                    this.$message.error('上传文件格式错误，必须是jpg,gif,png,xls,xlsx,doc,docx中的一种!');
                }
                return isjpg || isgif || ispng || isxls || isxlsx || isdoc || isdocx;
            },
            handlefileRemove(file, fileList) {//图片移除
                this.fileList = fileList;
            },

            //提交选中审批人
            Onconstsubmit(data){
                this.$store.dispatch('Add_reimburse_constsubmit',data);
            },
            DeleteConstPerson(){
                this.$store.dispatch('Delete_reimburse_ConstPerson',data);
            },
            //添加审批人弹窗状态修改
            ToggleConstDialog(data){
                this.isconstshow = data.isconstshow;
                this.selectloading = data.selectloading;
            },
            //提交选中抄送人
            Oncopysubmit(data){
                this.$store.dispatch('Add_reimburse_copysubmit',data);
            },
            DeleteCopyPerson(){
                this.$store.dispatch('Delete_reimburse_CopyPerson',data);
            },
            //添加抄送人弹窗状态修改
            ToggleCopyDialog(data){
                this.iscopyshow = data.iscopyshow;
            },
            //添加银行卡弹窗状态修改
            ToggleCardDialog(data){
                this.iscardshow = data.iscardshow;
            },
            //核对表单弹窗状态修改
            TogglesubmitDialog(data){
                this.issubmitshow = data.issubmitshow;
            },
            Onsubmit(){
                this.loading=true;
                let tabledata = this.$store.state.reimburse.reimburse_tabledata;

                let constdata = this.$store.state.reimburse.reimburse_constdata;
                let resultconstdata = [],resulcopydata = [];
                if(constdata.length > 0){
                    for(let item in constdata){
                        resultconstdata.push(parseInt(constdata[item].split("/")[0]));
                    }
                }

                let copydata = this.$store.state.reimburse.reimburse_copydata;
                if(copydata.length > 0){
                    for(let item in copydata){
                        resulcopydata.push(parseInt(copydata[item].split("/")[0]));
                    }
                }

                let resultdata = {
                        apply_id:this.ApplyID,
                        bank_card_id:this.bank.split('/')[1],
                        bank_name:this.bank.split('/')[0],
                        bank_des:this.bank.split('/')[2],
                        bao_xiao_list:tabledata,
                        approval_persons:resultconstdata,
                        copy_person:resulcopydata,
                        files:(this.fileList).map(data => data.response.data[0])
                    };

                this.$store.dispatch('Reimburse_Add',resultdata).then( response => {
                    this.loading = false;
                    this.issubmitshow = false;
                    if(response.data.code==200){
                        this.$router.push({ path: '/success' ,query:{apply_id:response.data.data,type:1}});
                    }else{
                        this.$message('系统出错，错误信息：'+response.data.message);
                    }
                }).catch(err => {
                    this.loading = false;
                });
            },
            onsubmitReimburse(formName){
                let tabledata = this.$store.state.reimburse.reimburse_tabledata;
                let constdata = this.$store.state.reimburse.reimburse_constdata;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (tabledata.length > 0 && constdata.length > 0) {
                            this.bank = this.ruleForm.code;
                            this.issubmitshow = true;
                        } else {
                            this.$message.error('请填写完整后提交!');
                        }
                    } else {
                        return false;
                    }
                });
            }
        }
    };
</script>

<style rel="stylesheet/less" lang="less" scoped>
    .el-upload--picture-card{
        width: 80px !important;
        height:80px !important;
    }
    a{
      text-decoration: none;
      color:#108ee9;
    }
    a:hover{
        text-decoration: underline;
    }

</style>


