<template>
    <el-dialog title="开店申请表" :visible="issubmitshow" custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0">开店申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{ curdate }}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{ name }}</td>
                    <td class="b_gray">门店城市</td>
                    <td class="t-l">{{ district }}</td>
                </tr>
                <tr>
                    <td class="b_gray">门店选址</td>
                    <td class="t-l">{{ address }}</td>
                    <td class="b_gray">门店租金</td>
                    <td class="t-l">{{ rental }} 元</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" height="150" width="100">门店概述</td>
                    <td colspan="5" width="150" class="t-l">{{ summary }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="100">审批人</td>
                    <td class="t-l">{{constperson}}</td>
                    <td class="b_gray" width="100">抄送人</td>
                    <td class="t-l">{{copyperson}}</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible">取 消</el-button>
            <el-button @click="onsubmitform" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'SubmitModal',
        props:['issubmitshow','loading','district','address','rental','summary'],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'name',
                'curdate'
            ]),
            applyID(){
                return this.$store.state.newStore.newStore_addApplyID;
            },
            constperson(){
                let constperson = "";
                if (this.$store.state.newStore.newStore_constdata.length > 0) {
                    constperson = (this.$store.state.newStore.newStore_constdata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return constperson;
            },
            copyperson(){
                let copyperson = "";
                if (this.$store.state.newStore.newStore_copydata.length > 0) {
                    copyperson = (this.$store.state.newStore.newStore_copydata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return copyperson;
            }
        },
        created(){
            this.$store.dispatch('NewStore_AddApplyID',{type:13});
        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid #E9E9E9;
                padding: 8px;
                line-height: 23px;
                font-size: 12px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {
            tr:last-child {
                td {
                    border-bottom: 1px solid #E9E9E9;
                }
            }
        }
    }
</style>


