<template>
    <el-dialog title="增加银行卡" :visible="iscardshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm">
            <!--<el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>银行：</el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item prop="bank_name" :rules="[{ required: true, message: '请选择'}]" >
                        <el-select v-model="ruleForm.bank_name" placeholder="请选择" style="width: 100%" >
                            <el-option
                                    v-for="(item,key) in options"
                                    :key="key"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>-->
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>卡号：</el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item prop="card_id" :rules="[{ required: true, message: '请输入卡号'}]" >
                        <el-input v-model="ruleForm.card_id" placeholder="请输入" style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span>开户行：</el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item prop="bank_name" :rules="[{ required: true, message: '请输入开户行'}]" >
                        <el-input
                                type="textarea"
                                :rows="2"
                                placeholder="请输入"
                                v-model="ruleForm.bank_name" style="width: 100%">
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"></el-col>
                <el-col :xs="24" :sm="20">例如：招商银行股份有限公司-上海金沙江路支行</el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="onsubmitform('ruleForm')" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default{
        props:['iscardshow','carddata'],
        data(){
            return{
                ruleForm:{
                    bank_name:'',
                    card_id:'',
                    bank_des:'',
                },
                hide:false,
                loading:false,
                options:[
                    {value:'中国工商银行',label:'中国工商银行'},
                    {value:'中国建设银行',label:'中国建设银行'},
                    {value:'中国银行',label:'中国银行'},
                    {value:'中国农业银行',label:'中国农业银行'},
                    {value:'交通银行',label:'交通银行'},
                    {value:'中国邮政储蓄银行',label:'中国邮政储蓄银行'},
                    {value:'招商银行',label:'招商银行'},
                    {value:'浦发银行',label:'浦发银行'},
                    {value:'中信银行',label:'中信银行'},
                    {value:'中国光大银行',label:'中国光大银行'},
                    {value:'中国民生银行',label:'中国民生银行'},
                    {value:'广发银行',label:'广发银行'},
                    {value:'兴业银行',label:'兴业银行'},
                    {value:'平安银行',label:'平安银行'},
                    {value:'恒丰银行',label:'恒丰银行'},
                    {value:'浙商银行',label:'浙商银行'},
                    {value:'渤海银行',label:'渤海银行'},
                ],
            }
        },
        computed: {

        },
        methods:{
            isVisible(formName){
                this.$emit('ToggleCardDialog',{iscardshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        let carddata = this.ruleForm;
                        this.$store.dispatch('Add_cardsubmit',{carddata:carddata}).then(response=>{
                            if(response.data.code==200){
                                this.$message({message:'银行卡添加成功！',type:'success'});
                                this.$store.dispatch('InitCard');
                            }
                        });
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleCardDialog',{iscardshow:false});
                            this.$refs[formName].resetFields();
                        },300);
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


