<template>
    <el-dialog title="离职申请表" :visible="issubmitshow"  custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md">离职申请表</h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100">日期</td>
                    <td class="t-l">{{curdate}}</td>
                    <td class="b_gray" width="100">单号</td>
                    <td class="t-l">{{applyID}}</td>
                </tr>
                <tr>
                    <td class="b_gray">姓名</td>
                    <td class="t-l">{{name}}</td>
                    <td class="b_gray">离职日期</td>
                    <td class="t-l">{{leave_date}}</td>
                </tr>
                <tr>
                    <td class="b_gray">部门</td>
                    <td class="t-l">{{org_full_name}}</td>
                    <td class="b_gray">职位</td>
                    <td class="t-l">{{job}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray" height="100">离职原因</td>
                    <td class="t-l" colspan="3">{{des}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray">物资是否归还</td>
                    <td class="t-l" colspan="3">{{stock_status=='1' ? '是' : '否'}}</td>
                </tr>
                </tbody>
            </table> 
            <el-table :data="stock_list" width="100%">
                <el-table-column prop="type" label="类别"></el-table-column>
                <el-table-column prop="sn" label="库存编号" width="150"></el-table-column>
                <el-table-column prop="brand" label="品牌" width="120"></el-table-column>
                <el-table-column prop="name" label="名称" width="120"></el-table-column>
                <el-table-column prop="price" label="价格" width="100"></el-table-column>
                <el-table-column prop="status" label="状态" width="80"></el-table-column>
            </el-table>
			<table width="100%">            
				<tbody>
                <tr>
                    <td class="t-c b_gray" width="100">财务是否结算</td>
                    <td class="t-l">{{finance_status=='1' ? '是' : '否'}}</td>
                </tr>
                </tbody>
            </table> 
            <el-table :data="finance_list" width="100%">
                <el-table-column prop="id" label="序号" width="70"></el-table-column>
                <el-table-column prop="time" label="借款时间" width="150"></el-table-column>
                <el-table-column prop="des" label="借款事由"></el-table-column>
                <el-table-column prop="name" label="借款金额" width="200"></el-table-column>
                <el-table-column prop="status" label="状态" width="80"></el-table-column>
            </el-table>
			<table width="100%">            
				<tbody>
                <tr>
                    <td class="t-c b_gray" width="120">账号密码是否交接</td>
                    <td class="t-l">{{account_status=='1' ? '是' : '否'}}</td>
                </tr>
                </tbody>
            </table> 
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="120">企业QQ</td>
                    <td class="t-l">{{qq==''?'--':qq}}</td>
                    <td class="b_gray" width="100">企业邮箱</td>
                    <td class="t-l">{{email==''?'--':email}}</td>
                    <td class="b_gray" width="100">工作手机号</td>
                    <td class="t-l">{{tel==''?'--':tel}}</td>
                </tr>
                </tbody>
            </table>
			<table width="100%">            
				<tbody>
                <tr>
                    <td class="t-c b_gray" width="120">工作是否交接</td>
                    <td class="t-l">{{work_status=='1' ? '是' : '否'}}</td>
                </tr>
                </tbody>
            </table> 
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="120">审批人</td>
                    <td class="t-l" width="19%">{{constperson}}</td>
                    <td class="b_gray" width="100">抄送人</td>
                    <td class="t-l" width="19%">{{copyperson==''?'--':copyperson}}</td>
                    <td class="b_gray" width="100">财务确认</td>
                    <td class="t-l">--</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible">取 消</el-button>
            <el-button @click="onsubmitform('ruleform')" type="primary" :loading="loading">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'SubmitModal',
        props:[
        	'issubmitshow',
        	'loading',
        	'leave_date', 
        	'stock_list',
        	'finance_list',
        	'stock_status',
        	'finance_status',
        	'account_status',
        	'work_status',
        	'des',
        	'job',
        	'qq',
        	'email',
        	'tel'
        ],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'org_full_name',
                'name',
                'curdate'
            ]),
            applyID(){
                return this.$store.state.applyleave.applyleave_addApplyID;
            },
            constperson(){
                let constperson = "";
                if (this.$store.state.applyleave.applyleave_constdata.length > 0) {
                    constperson = (this.$store.state.applyleave.applyleave_constdata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return constperson;
            },
            copyperson(){
                let copyperson = "", resultdata = "";
                if (this.$store.state.applyleave.applyleave_copydata.length > 0) {
                    copyperson = (this.$store.state.applyleave.applyleave_copydata).map(data => data.split("/")[1].split(" ")[0]).join("，");
                }
                return copyperson;
            }

        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            }
        },
        created(){
            this.$store.dispatch('Applyleave_AddApplyID',{type:11});
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        .el-table {
        	border-radius: 0;
	        border-left: 1px solid #ccc;
	        &:after {
	        	content: "";
	        	width: 1px;
	        	height: 100%;
	        	background: #ccc;
	        	right: 0;
	        }
        }
        table {
        	margin-top: -1px;
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid #ccc;
                padding: 8px;
                line-height: 23px;
                font-size: 14px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {

            tr:last-child {

                td {
                    border-bottom: 1px solid rgba(204, 204, 204, 1);
                }

            }
        }
    }
</style>


