import { Approval_list } from '../../../api/tablelist';
/*import Cookies from 'js-cookie';*/

const ccsend = {
    state: {
        ccsend_table:[],//表数据
    },
    mutations: {
        SET_ccsend_table: (state, data) => {
            state.ccsend_table = data;
        },
    },
    actions: {
        //表格数据
        Ccsend_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Approval_list(data).then(response => {
                    let data = response.data.data;
                    //console.log(data.res);
                    commit('SET_ccsend_table',data.res);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });

        },
    }
}

export default ccsend;


